/**
 * Messaging settings page - Email/SMTP and Webhooks with tabs
 */
import { useState, useEffect, useMemo } from 'react';
import { LoadingSpinner } from '../../shared/LoadingSpinner';
import { ErrorMessage } from '../../shared/ErrorMessage';
import { useSettingsAutosave } from '../../../hooks/useSettingsAutosave';
import { SettingsSection } from './settingsInputs';
import { AutosaveStatus } from './AutosaveStatus';
import {
  PROVIDER_SMTP_HOSTS,
  SIMPLIFIED_EMAIL_FIELDS,
  ALL_EMAIL_FIELDS,
} from './EmailSettings';

type MessagingTab = 'email' | 'webhooks';

interface TabConfig {
  id: MessagingTab;
  label: string;
  category: string;
}

const TABS: TabConfig[] = [
  { id: 'email', label: 'Email', category: 'email' },
  { id: 'webhooks', label: 'Webhooks', category: 'webhook' },
];

export interface MessagingSettingsProps {
  className?: string;
}

export function MessagingSettings({ className }: MessagingSettingsProps) {
  const {
    settings,
    edits,
    isLoading,
    autosaveStatus,
    autosaveError,
    error,
    fetchSettings,
    handleChange,
    getEffectiveValue,
  } = useSettingsAutosave();

  const [activeTab, setActiveTab] = useState<MessagingTab>('email');

  useEffect(() => {
    fetchSettings();
  }, [fetchSettings]);

  // Get current tab's category
  const currentTab = TABS.find((t) => t.id === activeTab);
  const currentCategory = currentTab?.category ?? '';

  // Get current email provider
  const emailProvider = getEffectiveValue('email_provider') || 'custom';
  const isCustomProvider = emailProvider === 'custom';

  // Check if SMTP is configured (has a host or a non-custom provider selected)
  const smtpHost = getEffectiveValue('email_smtp_host');
  const isSmtpConfigured = !isCustomProvider || (smtpHost != null && smtpHost !== '');

  // PERF-02: Compute categorySettings inside useMemo with stable dependencies
  // to prevent re-computation on every render
  const currentSettings = useMemo(() => {
    const categorySettings = settings[currentCategory] ?? [];
    if (activeTab !== 'email') return categorySettings;

    const fieldsToShow = isCustomProvider ? ALL_EMAIL_FIELDS : SIMPLIFIED_EMAIL_FIELDS;
    return categorySettings
      .filter((s) => fieldsToShow.includes(s.key))
      .sort((a, b) => fieldsToShow.indexOf(a.key) - fieldsToShow.indexOf(b.key));
  }, [settings, currentCategory, activeTab, isCustomProvider]);

  // Auto-set SMTP host when provider changes (for non-custom providers)
  const handleProviderAwareChange = (key: string, value: string) => {
    handleChange(key, value);

    // When provider changes, auto-fill the SMTP host
    if (key === 'email_provider' && value !== 'custom') {
      const smtpHost = PROVIDER_SMTP_HOSTS[value];
      if (smtpHost) {
        handleChange('email_smtp_host', smtpHost);
        // Also set default port and TLS for convenience
        handleChange('email_smtp_port', '587');
        handleChange('email_smtp_tls', 'true');
      }
    }
  };

  if (isLoading && Object.keys(settings).length === 0) {
    return (
      <div className={`cedros-system-settings cedros-system-settings-loading ${className ?? ''}`}>
        <LoadingSpinner />
        <span>Loading settings...</span>
      </div>
    );
  }

  if (error) {
    return (
      <div className={`cedros-system-settings ${className ?? ''}`}>
        <ErrorMessage error={error.message} />
      </div>
    );
  }

  return (
    <div className={`cedros-system-settings ${className ?? ''}`}>
      <div className="cedros-settings-page-header">
        <div className="cedros-settings-page-header-content">
          <h2 className="cedros-settings-page-title">Auth Messages</h2>
          <p className="cedros-settings-page-description">
            Configure email delivery for verification emails, password resets, and instant link
            login. Webhook notifications can be sent to Discord or Slack.
          </p>
        </div>
        <AutosaveStatus status={autosaveStatus} error={autosaveError} />
      </div>

      {activeTab === 'email' && !isSmtpConfigured && (
        <div className="cedros-settings-warning-banner">
          Email features (verification, password reset, instant link) are disabled until SMTP is
          configured. Select a provider or enter custom SMTP settings below.
        </div>
      )}

      {/* Tabs */}
      <div className="cedros-admin-tabs cedros-admin-tabs--line">
        {TABS.map((tab) => (
          <button
            key={tab.id}
            type="button"
            className={`cedros-admin-tab ${activeTab === tab.id ? 'cedros-admin-tab-active' : ''}`}
            onClick={() => setActiveTab(tab.id)}
            aria-selected={activeTab === tab.id}
            role="tab"
          >
            {tab.label}
          </button>
        ))}
      </div>

      {/* Tab content */}
      <div className="cedros-admin-tab-content" role="tabpanel">
        {currentSettings.length === 0 ? (
          <div className="cedros-system-settings-empty">
            <p>No settings found for {currentTab?.label ?? 'this section'}.</p>
          </div>
        ) : (
          <SettingsSection
            settings={currentSettings}
            edits={edits}
            onChange={activeTab === 'email' ? handleProviderAwareChange : handleChange}
          />
        )}
      </div>
    </div>
  );
}
