/**
 * Create organization form component
 *
 * Extracted from OrgSwitcher.tsx (F-02)
 */
import { useState, useCallback } from 'react';
import type { CreateOrgRequest } from '../../types';
import { LoadingSpinner } from '../shared/LoadingSpinner';
import { ErrorMessage } from '../shared/ErrorMessage';

export interface CreateOrgFormProps {
  isLoading: boolean;
  onSubmit: (data: CreateOrgRequest) => Promise<void>;
  onCancel: () => void;
}

export function CreateOrgForm({ isLoading, onSubmit, onCancel }: CreateOrgFormProps) {
  const [name, setName] = useState('');
  const [slug, setSlug] = useState('');
  const [formError, setFormError] = useState<string | null>(null);

  // Auto-generate slug from name
  const handleNameChange = useCallback((value: string) => {
    setName(value);
    // Generate slug: lowercase, replace spaces with hyphens, remove special chars
    const generatedSlug = value
      .toLowerCase()
      .replace(/\s+/g, '-')
      .replace(/[^a-z0-9-]/g, '')
      .substring(0, 50);
    setSlug(generatedSlug);
  }, []);

  const handleSubmit = useCallback(
    async (event: React.FormEvent) => {
      event.preventDefault();
      setFormError(null);

      if (!name.trim()) {
        setFormError('Organization name is required');
        return;
      }

      if (!slug.trim()) {
        setFormError('Organization slug is required');
        return;
      }

      try {
        await onSubmit({ name: name.trim(), slug: slug.trim() });
      } catch (err) {
        setFormError((err as Error).message || 'Failed to create organization');
      }
    },
    [name, slug, onSubmit]
  );

  return (
    <form className="cedros-org-create-form" onSubmit={handleSubmit}>
      {formError && <ErrorMessage error={formError} />}

      <div className="cedros-form-group">
        <label htmlFor="org-name" className="cedros-form-label">
          Organization Name
        </label>
        <input
          id="org-name"
          type="text"
          className="cedros-form-input"
          value={name}
          onChange={(e) => handleNameChange(e.target.value)}
          placeholder="My Organization"
          maxLength={255}
          disabled={isLoading}
          autoFocus
        />
      </div>

      <div className="cedros-form-group">
        <label htmlFor="org-slug" className="cedros-form-label">
          URL Slug
        </label>
        <div className="cedros-form-input-prefix">
          <span className="cedros-form-prefix">@</span>
          <input
            id="org-slug"
            type="text"
            className="cedros-form-input"
            value={slug}
            onChange={(e) => setSlug(e.target.value.toLowerCase())}
            placeholder="my-organization"
            maxLength={100}
            pattern="[a-z0-9-]+"
            disabled={isLoading}
          />
        </div>
        <span className="cedros-form-hint">Only lowercase letters, numbers, and hyphens</span>
      </div>

      <div className="cedros-form-actions">
        <button
          type="button"
          className="cedros-button cedros-button-outline"
          onClick={onCancel}
          disabled={isLoading}
        >
          Cancel
        </button>
        <button
          type="submit"
          className="cedros-button cedros-button-primary"
          disabled={isLoading || !name.trim() || !slug.trim()}
        >
          {isLoading ? <LoadingSpinner size="sm" /> : 'Create Organization'}
        </button>
      </div>
    </form>
  );
}
