/**
 * Component to display BIP-39 recovery phrase
 *
 * Shows 12 words in a grid format with copy functionality.
 * Includes security warnings about storing the phrase safely.
 */

import { useState, useCallback, useRef, useEffect } from 'react';
import { formatMnemonicForDisplay } from '../../crypto';

export interface RecoveryPhraseDisplayProps {
  /** Array of 12 mnemonic words */
  words: string[];
  /** Callback when user confirms they've saved the phrase */
  onConfirm: () => void;
  /** Optional class name */
  className?: string;
}

/**
 * Display recovery phrase with security warnings
 */
export function RecoveryPhraseDisplay({
  words,
  onConfirm,
  className = '',
}: RecoveryPhraseDisplayProps) {
  const [copied, setCopied] = useState(false);
  const [confirmed, setConfirmed] = useState(false);
  const copyTimerRef = useRef<number | null>(null);

  const wordGroups = formatMnemonicForDisplay(words);

  const handleCopy = useCallback(async () => {
    try {
      await navigator.clipboard.writeText(words.join(' '));
      setCopied(true);
      if (copyTimerRef.current !== null) {
        window.clearTimeout(copyTimerRef.current);
      }
      copyTimerRef.current = window.setTimeout(() => setCopied(false), 2000);
    } catch {
      // Clipboard API not available
    }
  }, [words]);

  useEffect(() => {
    return () => {
      if (copyTimerRef.current !== null) {
        window.clearTimeout(copyTimerRef.current);
        copyTimerRef.current = null;
      }
    };
  }, []);

  // COMP-02: The `confirmed` check is defense-in-depth - button is disabled when
  // !confirmed, but check guards against CSS/JS injection enabling the button
  const handleConfirm = useCallback(() => {
    if (confirmed) {
      onConfirm();
    }
  }, [confirmed, onConfirm]);

  return (
    <div className={`cedros-recovery-phrase-display ${className}`}>
      <div className="cedros-recovery-header">
        <h3 className="cedros-recovery-title">Recovery Phrase</h3>
        <p className="cedros-recovery-warning">
          Write down these 12 words in order and store them securely. This is the only way to
          recover your wallet if you lose access.
        </p>
      </div>

      <div className="cedros-recovery-grid">
        {wordGroups.map((group, groupIndex) => (
          <div key={groupIndex} className="cedros-word-group">
            {group.map((word, wordIndex) => {
              const number = groupIndex * 4 + wordIndex + 1;
              return (
                <div key={number} className="cedros-word-item">
                  <span className="cedros-word-number">{number}.</span>
                  <span className="cedros-word-text">{word}</span>
                </div>
              );
            })}
          </div>
        ))}
      </div>

      <button
        type="button"
        className="cedros-button cedros-button-secondary cedros-copy-btn"
        onClick={handleCopy}
      >
        {copied ? 'Copied!' : 'Copy to Clipboard'}
      </button>

      <div className="cedros-recovery-security">
        <div className="cedros-warning-box">
          <svg
            className="cedros-warning-icon"
            width="20"
            height="20"
            viewBox="0 0 20 20"
            fill="none"
            aria-hidden="true"
          >
            <path
              d="M10 6v4m0 4h.01M19 10a9 9 0 11-18 0 9 9 0 0118 0z"
              stroke="currentColor"
              strokeWidth="2"
              strokeLinecap="round"
            />
          </svg>
          <div className="cedros-warning-content">
            <strong>Security Warning</strong>
            <ul>
              <li>Never share this phrase with anyone</li>
              <li>Store it offline in a secure location</li>
              <li>Anyone with this phrase can access your wallet</li>
              <li>Cedros cannot recover this phrase for you</li>
            </ul>
          </div>
        </div>
      </div>

      <div className="cedros-recovery-confirm">
        <label className="cedros-checkbox-label">
          <input
            type="checkbox"
            checked={confirmed}
            onChange={(e) => setConfirmed(e.target.checked)}
            className="cedros-checkbox"
          />
          <span>I have written down and securely stored my recovery phrase</span>
        </label>

        <button
          type="button"
          className="cedros-button cedros-button-primary"
          onClick={handleConfirm}
          disabled={!confirmed}
        >
          Continue
        </button>
      </div>
    </div>
  );
}
