import { useState, useId, type FormEvent } from 'react';
import { usePasswordReset } from '../../hooks/usePasswordReset';
import { ErrorMessage } from '../shared/ErrorMessage';
import { LoadingSpinner } from '../shared/LoadingSpinner';

export interface ForgotPasswordFormProps {
  onSuccess?: () => void;
  onCancel?: () => void;
  className?: string;
}

/**
 * Form for requesting a password reset email.
 *
 * @example
 * ```tsx
 * <ForgotPasswordForm
 *   onSuccess={() => console.log('Email sent!')}
 *   onCancel={() => setShowForgotPassword(false)}
 * />
 * ```
 */
export function ForgotPasswordForm({
  onSuccess,
  onCancel,
  className = '',
}: ForgotPasswordFormProps) {
  const [email, setEmail] = useState('');
  const { forgotPassword, isLoading, isSuccess, error, clearError } = usePasswordReset();
  const emailId = useId();

  const handleSubmit = async (e: FormEvent) => {
    e.preventDefault();
    try {
      await forgotPassword(email);
      onSuccess?.();
    } catch {
      // Error is already set in the hook
    }
  };

  if (isSuccess) {
    return (
      <div className={`cedros-forgot-password-success ${className}`}>
        <svg
          className="cedros-success-icon"
          width="48"
          height="48"
          viewBox="0 0 48 48"
          fill="none"
          aria-hidden="true"
        >
          <circle cx="24" cy="24" r="22" stroke="currentColor" strokeWidth="2" />
          <path
            d="M14 24l7 7 13-13"
            stroke="currentColor"
            strokeWidth="2"
            strokeLinecap="round"
            strokeLinejoin="round"
          />
        </svg>
        <h3 className="cedros-success-title">Check your email</h3>
        <p className="cedros-success-message">
          If an account exists for <strong>{email}</strong>, you will receive a password reset link
          shortly.
        </p>
        {onCancel && (
          <button
            type="button"
            className="cedros-button cedros-button-md cedros-button-outline"
            onClick={onCancel}
          >
            Back to login
          </button>
        )}
      </div>
    );
  }

  return (
    <form className={`cedros-forgot-password-form ${className}`} onSubmit={handleSubmit}>
      <div className="cedros-form-header">
        <h3 className="cedros-form-title">Forgot password?</h3>
        <p className="cedros-form-subtitle">
          Enter your email address and we&apos;ll send you a link to reset your password.
        </p>
      </div>

      <ErrorMessage error={error} onDismiss={clearError} />

      <div className="cedros-form-field">
        <label htmlFor={emailId} className="cedros-label">
          Email address
        </label>
        <input
          id={emailId}
          type="email"
          className="cedros-input"
          value={email}
          onChange={(e) => setEmail(e.target.value)}
          placeholder="you@example.com"
          required
          autoComplete="email"
          disabled={isLoading}
        />
      </div>

      <div className="cedros-form-actions">
        <button
          type="submit"
          className="cedros-button cedros-button-md cedros-button-primary cedros-button-full"
          disabled={isLoading || !email}
        >
          {isLoading ? (
            <>
              <LoadingSpinner size="sm" />
              Sending...
            </>
          ) : (
            'Send reset link'
          )}
        </button>

        {onCancel && (
          <button
            type="button"
            className="cedros-button cedros-button-md cedros-button-ghost cedros-button-full"
            onClick={onCancel}
            disabled={isLoading}
          >
            Back to login
          </button>
        )}
      </div>
    </form>
  );
}
