/**
 * Unified transaction signing hook
 *
 * Handles signing for both wallet types:
 * - External Solana wallet: Delegates to wallet adapter (app-provided callback)
 * - SSS embedded wallet: Uses server-side signing
 *
 * Usage:
 * ```tsx
 * const { signTransaction, signingMethod, canSign } = useTransactionSigning({
 *   // For external wallet users, provide your wallet adapter's signTransaction
 *   onExternalSign: async (tx) => walletAdapter.signTransaction(tx),
 * });
 *
 * // Sign a transaction (automatically routes to correct method)
 * const signature = await signTransaction(txBytes, credential);
 * ```
 */

import { useCallback, useMemo } from 'react';
import { useWallet } from './useWallet';
import { useWalletSigning } from './useWalletSigning';
import type { UnlockCredential } from '../types/wallet';

/** Signing method in use */
export type SigningMethod = 'external' | 'sss' | 'none';

/** Options for external wallet signing */
export interface ExternalSignOptions {
  /**
   * Callback to sign transaction with external wallet adapter.
   * Only called when user has external Solana wallet.
   *
   * @param transaction - Transaction bytes to sign
   * @returns Signature bytes (64 bytes Ed25519)
   */
  onExternalSign?: (transaction: Uint8Array) => Promise<Uint8Array>;
}

/** Return type for useTransactionSigning hook */
export interface UseTransactionSigningReturn {
  /** Sign a transaction (routes to appropriate method) */
  signTransaction: (transaction: Uint8Array, credential?: UnlockCredential) => Promise<Uint8Array>;
  /** Which signing method is available */
  signingMethod: SigningMethod;
  /** Whether user can sign transactions */
  canSign: boolean;
  /** Whether signing is in progress */
  isSigning: boolean;
  /** Solana public key (from either wallet type) */
  publicKey: string | null;
  /** Whether user has external Solana wallet */
  hasExternalWallet: boolean;
  /** Whether SSS wallet is enrolled */
  hasSssWallet: boolean;
  /** Whether SSS wallet is unlocked (for session-based signing) */
  isSssUnlocked: boolean;
  /** Error from last signing attempt */
  error: string | null;
  /** Clear error */
  clearError: () => void;
}

/**
 * Unified transaction signing hook
 *
 * Automatically routes signing requests to the appropriate method:
 * - External wallet: Uses provided callback
 * - SSS wallet: Uses server-side signing
 */
export function useTransactionSigning(
  options: ExternalSignOptions = {}
): UseTransactionSigningReturn {
  const { onExternalSign } = options;

  const { solanaPubkey, hasExternalWallet, status, isUnlocked: isSssUnlocked } = useWallet();

  const {
    signTransaction: sssSignTransaction,
    isSigning: isSssSigning,
    error: sssError,
    clearError: clearSssError,
  } = useWalletSigning();

  // Determine which signing method is available
  const signingMethod = useMemo((): SigningMethod => {
    if (hasExternalWallet && onExternalSign) {
      return 'external';
    }
    if (status === 'enrolled_locked' || status === 'enrolled_unlocked') {
      return 'sss';
    }
    return 'none';
  }, [hasExternalWallet, onExternalSign, status]);

  // Whether user can sign
  const canSign = signingMethod !== 'none';
  const hasSssWallet = status === 'enrolled_locked' || status === 'enrolled_unlocked';

  // Unified sign function
  const signTransaction = useCallback(
    async (transaction: Uint8Array, credential?: UnlockCredential): Promise<Uint8Array> => {
      if (signingMethod === 'external') {
        if (!onExternalSign) {
          throw new Error('External wallet signing callback not provided');
        }
        return onExternalSign(transaction);
      }

      if (signingMethod === 'sss') {
        if (!credential && !isSssUnlocked) {
          throw new Error(
            'Credential required for signing. Unlock wallet first or provide credential.'
          );
        }
        if (credential) {
          return sssSignTransaction(transaction, credential);
        }
        // If unlocked, credential is optional (server uses cached key)
        return sssSignTransaction(transaction);
      }

      throw new Error('No signing method available. Enroll a wallet first.');
    },
    [signingMethod, onExternalSign, isSssUnlocked, sssSignTransaction]
  );

  return {
    signTransaction,
    signingMethod,
    canSign,
    isSigning: isSssSigning,
    publicKey: solanaPubkey,
    hasExternalWallet,
    hasSssWallet,
    isSssUnlocked,
    error: sssError,
    clearError: clearSssError,
  };
}
