import { useState, useCallback, useEffect, useRef, useMemo } from 'react';
import { useCedrosLogin } from '../context/useCedrosLogin';
import { ApiClient, handleApiError } from '../utils/apiClient';
import type { AuthResponse, AuthError } from '../types';

// Module-level singleton for Apple Sign In script loading
// Prevents race conditions when multiple components mount simultaneously
const scriptLoader = {
  loading: false,
  loaded: false,
  error: null as Error | null,
  callbacks: [] as Array<{ resolve: () => void; reject: (err: Error) => void }>,

  load(): Promise<void> {
    // UI-15: SSR guard  avoid touching browser globals in Node/SSR environments
    if (typeof window === 'undefined' || typeof document === 'undefined') {
      return Promise.reject(new Error('Apple Sign-In script loader cannot run in SSR'));
    }

    if (this.loaded) {
      return Promise.resolve();
    }

    if (this.loading) {
      return new Promise((resolve, reject) => {
        this.callbacks.push({ resolve, reject });
      });
    }

    this.loading = true;
    return new Promise((resolve, reject) => {
      this.callbacks.push({ resolve, reject });

      const existingScript = document.getElementById('apple-signin-script');
      if (existingScript) {
        if (window.AppleID) {
          this.loaded = true;
          this.loading = false;
          this.callbacks.forEach((cb) => cb.resolve());
          this.callbacks = [];
        } else {
          existingScript.addEventListener('load', () => {
            this.loaded = true;
            this.loading = false;
            this.callbacks.forEach((cb) => cb.resolve());
            this.callbacks = [];
          });
        }
        return;
      }

      const script = document.createElement('script');
      script.src =
        'https://appleid.cdn-apple.com/appleauth/static/jsapi/appleid/1/en_US/appleid.auth.js';
      script.async = true;
      script.defer = true;
      script.id = 'apple-signin-script';

      script.onload = () => {
        this.loaded = true;
        this.loading = false;
        this.callbacks.forEach((cb) => cb.resolve());
        this.callbacks = [];
      };

      script.onerror = () => {
        this.loading = false;
        // M-03: Remove failed script from DOM to allow retry on next load() call.
        // Without removal, retry finds existingScript and waits for a load event
        // that will never fire on an already-failed script.
        script.remove();
        const error = new Error('Failed to load Apple Sign In script');
        this.callbacks.forEach((cb) => cb.reject(error));
        this.callbacks = [];
      };

      document.head.appendChild(script);
    });
  },

  /**
   * Reset singleton state for test isolation
   * @internal - Only use in test setup/teardown
   */
  _reset(): void {
    this.loading = false;
    this.loaded = false;
    this.error = null;
    this.callbacks = [];
  },
};

export interface UseAppleAuthReturn {
  signIn: () => Promise<AuthResponse>;
  isLoading: boolean;
  isInitialized: boolean;
  error: AuthError | null;
  clearError: () => void;
}

/**
 * Hook for Apple Sign In authentication.
 *
 * @example
 * ```tsx
 * function AppleButton() {
 *   const { signIn, isLoading, isInitialized, error } = useAppleAuth();
 *
 *   return (
 *     <button onClick={signIn} disabled={!isInitialized || isLoading}>
 *       {isLoading ? 'Signing in...' : 'Sign in with Apple'}
 *     </button>
 *   );
 * }
 * ```
 */
export function useAppleAuth(): UseAppleAuthReturn {
  const { config, _internal } = useCedrosLogin();
  const [isLoading, setIsLoading] = useState(false);
  const [isInitialized, setIsInitialized] = useState(false);
  const [error, setError] = useState<AuthError | null>(null);

  const configRef = useRef(config);

  const apiClient = useMemo(
    () =>
      new ApiClient({
        baseUrl: config.serverUrl,
        timeoutMs: config.requestTimeout,
        retryAttempts: config.retryAttempts,
      }),
    [config.serverUrl, config.requestTimeout, config.retryAttempts]
  );

  useEffect(() => {
    configRef.current = config;
  }, [config]);

  // Initialize Apple Sign In SDK
  useEffect(() => {
    if (!config.appleClientId) {
      return;
    }

    let isMounted = true;

    const initializeAppleSignIn = () => {
      if (!isMounted) return;

      try {
        window.AppleID?.auth?.init({
          clientId: config.appleClientId!,
          scope: 'name email',
          redirectURI: window.location.origin,
          usePopup: true,
        });

        if (isMounted) {
          setIsInitialized(true);
        }
      } catch {
        if (isMounted) {
          setError({
            code: 'SERVER_ERROR',
            message: 'Failed to initialize Apple Sign In',
          });
        }
      }
    };

    scriptLoader
      .load()
      .then(() => {
        if (isMounted) {
          initializeAppleSignIn();
        }
      })
      .catch(() => {
        if (isMounted) {
          setError({
            code: 'SERVER_ERROR',
            message: 'Failed to load Apple Sign In',
          });
        }
      });

    return () => {
      isMounted = false;
    };
  }, [config.appleClientId]);

  const signIn = useCallback(async (): Promise<AuthResponse> => {
    if (!config.appleClientId) {
      const err: AuthError = {
        code: 'VALIDATION_ERROR',
        message: 'Apple Client ID not configured',
      };
      setError(err);
      throw err;
    }

    if (!isInitialized) {
      const err: AuthError = {
        code: 'VALIDATION_ERROR',
        message: 'Apple Sign In not initialized',
      };
      setError(err);
      throw err;
    }

    setIsLoading(true);
    setError(null);

    try {
      // Trigger Apple Sign In popup
      const response = await window.AppleID!.auth!.signIn();

      // Apple returns authorization with id_token
      const idToken = response.authorization?.id_token;
      if (!idToken) {
        throw new Error('No ID token received from Apple');
      }

      // Apple only provides user info on first sign-in
      // The name comes from response.user (if available)
      const userName = response.user?.name
        ? `${response.user.name.firstName || ''} ${response.user.name.lastName || ''}`.trim()
        : undefined;

      // Send to our backend
      const data = await apiClient.post<AuthResponse>('/apple', {
        idToken,
        name: userName || undefined,
      });

      configRef.current.callbacks?.onLoginSuccess?.(data.user, 'apple');
      _internal?.handleLoginSuccess(data.user, data.tokens);
      setIsLoading(false);
      return data;
    } catch (err) {
      // Check if user cancelled
      const appleError = err as { error?: string };
      if (appleError.error === 'popup_closed_by_user') {
        const authError: AuthError = {
          code: 'SERVER_ERROR',
          message: 'Apple Sign In was cancelled',
        };
        setError(authError);
        setIsLoading(false);
        throw authError;
      }

      const authError = handleApiError(err, 'Apple sign-in failed');
      setError(authError);
      setIsLoading(false);
      throw authError;
    }
  }, [config.appleClientId, isInitialized, apiClient, _internal]);

  const clearError = useCallback(() => setError(null), []);

  return {
    signIn,
    isLoading,
    isInitialized,
    error,
    clearError,
  };
}

// Type declaration for Apple Sign In JS SDK
declare global {
  interface Window {
    AppleID?: {
      auth?: {
        init: (config: {
          clientId: string;
          scope: string;
          redirectURI: string;
          usePopup?: boolean;
        }) => void;
        signIn: () => Promise<{
          authorization?: {
            id_token?: string;
            code?: string;
          };
          user?: {
            email?: string;
            name?: {
              firstName?: string;
              lastName?: string;
            };
          };
        }>;
      };
    };
  }
}
