/**
 * WithdrawalFlow  multi-step flow for withdrawing SOL/SPL tokens
 * from the user's embedded wallet to an external Solana address.
 *
 * Fetches actual token balances from the wallet on mount.
 * Steps: Token Select  Form  Confirm  Processing  Success
 */

import { useState, useCallback, useMemo, useEffect } from 'react';
import { useCedrosLoginOptional } from '../../context/useCedrosLogin';
import { useWithdrawal } from '../../hooks/useWithdrawal';
import { ErrorMessage } from '../shared/ErrorMessage';
import { LoadingSpinner } from '../shared/LoadingSpinner';
import type { WithdrawalResponse } from '../../types/withdrawal';

/** A token the user holds, derived from balance fetch */
interface HeldToken {
  symbol: string;
  mint: string;
  decimals: number;
  /** Raw amount in smallest units */
  rawBalance: string;
  /** Human-readable balance */
  displayBalance: string;
}

/** Native SOL mint (used as sentinel) */
const SOL_MINT = 'So11111111111111111111111111111111111111112';

/** Well-known mint  symbol mapping for display */
const KNOWN_MINTS: Record<string, string> = {
  EPjFWdd5AufqSSqeM2qN1xzybapC8G4wEGGkZwyTDt1v: 'USDC',
  Es9vMFrzaCERmJfrF4H2FYD4KCoNkY11McCe8BenwNYB: 'USDT',
  HzwqbKZw8HxMN6bF2yFZNrht3c2iXXzpKcFu7uBEDKtr: 'EURC',
  USD1ttGY1N17NEEHLmELoaybftRBUSErhqYiQzvEmuB: 'USD1',
  '2b1kV6DkPAnxd5ixfnxCpjxmKwqjjaYmCZfHsFu24GXo': 'PYUSD',
  USDH1SM1ojwWUga67PGrgFWUHibbjqMvuMaDkRJTgkX: 'USDH',
  CASHVDm2wsJXfhj6VWxb7GiMdoLc17Du7paH4bNr5woT: 'CASH',
  DezXAZ8z7PnrnRJjz3wXBoRgixCa6xjnB7YaB1pPB263: 'BONK',
  oreoU2P8bN6jkk3jbaiVxYnG1dCXcYxwhwyK9jSybcp: 'ORE',
};

type FlowStep = 'loading' | 'select' | 'form' | 'confirm' | 'processing' | 'success';

export interface WithdrawalFlowProps {
  onSuccess?: (result: WithdrawalResponse) => void;
  onError?: (error: Error) => void;
  onCancel?: () => void;
  className?: string;
}

function isValidSolanaAddress(address: string): boolean {
  if (address.length < 32 || address.length > 50) return false;
  return /^[1-9A-HJ-NP-Za-km-z]+$/.test(address);
}

function truncateAddress(address: string): string {
  if (address.length <= 16) return address;
  return `${address.slice(0, 6)}...${address.slice(-6)}`;
}

function formatTokenAmount(raw: string, decimals: number): string {
  const num = Number(raw) / Math.pow(10, decimals);
  return num.toLocaleString(undefined, {
    minimumFractionDigits: 2,
    maximumFractionDigits: Math.min(decimals, 6),
  });
}

export function WithdrawalFlow({
  onSuccess,
  onError,
  onCancel,
  className = '',
}: WithdrawalFlowProps) {
  const context = useCedrosLoginOptional();
  const { withdrawSol, withdrawSpl, getBalances, isSubmitting, error, clearError } =
    useWithdrawal();

  const [step, setStep] = useState<FlowStep>('loading');
  const [heldTokens, setHeldTokens] = useState<HeldToken[]>([]);
  const [selectedToken, setSelectedToken] = useState<HeldToken | null>(null);
  const [destination, setDestination] = useState('');
  const [amount, setAmount] = useState('');
  const [result, setResult] = useState<WithdrawalResponse | null>(null);
  const [validationError, setValidationError] = useState<string | null>(null);
  const [loadError, setLoadError] = useState<string | null>(null);

  const network = context?.config.solana?.network ?? 'mainnet-beta';

  const explorerTxUrl = useMemo(() => {
    if (!result?.txSignature) return '';
    const base = `https://explorer.solana.com/tx/${result.txSignature}`;
    if (network === 'mainnet-beta') return base;
    return `${base}?cluster=${encodeURIComponent(network)}`;
  }, [result, network]);

  // Convert human-readable amount to raw token units
  const rawAmount = useMemo(() => {
    if (!selectedToken || !amount) return '0';
    const num = parseFloat(amount);
    if (isNaN(num) || num <= 0) return '0';
    return Math.floor(num * Math.pow(10, selectedToken.decimals)).toString();
  }, [amount, selectedToken]);

  // Fetch balances on mount
  useEffect(() => {
    if (!context) return;

    let cancelled = false;
    (async () => {
      try {
        const balances = await getBalances();
        if (cancelled) return;

        const tokens: HeldToken[] = [];

        // SOL (always show if > 0)
        if (balances.solLamports > 0) {
          tokens.push({
            symbol: 'SOL',
            mint: SOL_MINT,
            decimals: 9,
            rawBalance: String(balances.solLamports),
            displayBalance: formatTokenAmount(String(balances.solLamports), 9),
          });
        }

        // SPL tokens
        for (const t of balances.tokens) {
          const symbol = KNOWN_MINTS[t.mint] ?? truncateAddress(t.mint);
          tokens.push({
            symbol,
            mint: t.mint,
            decimals: t.decimals,
            rawBalance: t.amount,
            displayBalance: formatTokenAmount(t.amount, t.decimals),
          });
        }

        setHeldTokens(tokens);
        setStep(tokens.length > 0 ? 'select' : 'select');
      } catch {
        if (!cancelled) {
          setLoadError('Failed to load wallet balances');
          setStep('select');
        }
      }
    })();

    return () => {
      cancelled = true;
    };
  }, [context, getBalances]);

  const handleSelectToken = useCallback(
    (token: HeldToken) => {
      setSelectedToken(token);
      setStep('form');
      clearError();
      setValidationError(null);
      setAmount('');
    },
    [clearError]
  );

  const handleMax = useCallback(() => {
    if (!selectedToken) return;
    const maxHuman = Number(selectedToken.rawBalance) / Math.pow(10, selectedToken.decimals);
    // For SOL, leave some for fees
    if (selectedToken.mint === SOL_MINT) {
      const reserved = 0.01; // ~10k lamports buffer for fees + rent
      setAmount(String(Math.max(0, maxHuman - reserved)));
    } else {
      setAmount(String(maxHuman));
    }
  }, [selectedToken]);

  const handleFormSubmit = useCallback(() => {
    setValidationError(null);

    if (!destination.trim()) {
      setValidationError('Destination address is required');
      return;
    }
    if (!isValidSolanaAddress(destination.trim())) {
      setValidationError('Invalid Solana address');
      return;
    }
    if (!amount || parseFloat(amount) <= 0 || isNaN(parseFloat(amount))) {
      setValidationError('Please enter a valid amount');
      return;
    }
    if (rawAmount === '0') {
      setValidationError('Amount is too small');
      return;
    }

    setStep('confirm');
  }, [destination, amount, rawAmount]);

  const handleConfirm = useCallback(async () => {
    if (!selectedToken) return;
    setStep('processing');
    clearError();

    try {
      let txResult: WithdrawalResponse;

      if (selectedToken.mint === SOL_MINT) {
        txResult = await withdrawSol(destination.trim(), Number(rawAmount));
      } else {
        txResult = await withdrawSpl(destination.trim(), selectedToken.mint, rawAmount);
      }

      setResult(txResult);
      setStep('success');
      onSuccess?.(txResult);
    } catch (err) {
      setStep('confirm');
      onError?.(err instanceof Error ? err : new Error(String(err)));
    }
  }, [
    selectedToken,
    destination,
    rawAmount,
    withdrawSol,
    withdrawSpl,
    clearError,
    onSuccess,
    onError,
  ]);

  const handleBack = useCallback(() => {
    clearError();
    setValidationError(null);
    if (step === 'form') {
      setStep('select');
      setSelectedToken(null);
      setAmount('');
      setDestination('');
    } else if (step === 'confirm') {
      setStep('form');
    }
  }, [step, clearError]);

  const handleReset = useCallback(() => {
    setStep('select');
    setSelectedToken(null);
    setDestination('');
    setAmount('');
    setResult(null);
    clearError();
    setValidationError(null);
  }, [clearError]);

  if (!context) return null;

  return (
    <div className={`cedros-withdrawal ${className}`}>
      {/* Loading */}
      {step === 'loading' && (
        <div className="cedros-withdrawal-processing">
          <LoadingSpinner />
          <p className="cedros-withdrawal-processing-text">Loading wallet balances...</p>
        </div>
      )}

      {/* Token Select */}
      {step === 'select' && (
        <div className="cedros-withdrawal-select">
          <div className="cedros-withdrawal-header">
            <h3 className="cedros-withdrawal-title">Withdraw</h3>
            <p className="cedros-withdrawal-subtitle">Select a token to withdraw</p>
          </div>

          {loadError && <ErrorMessage error={loadError} />}

          {heldTokens.length === 0 && !loadError && (
            <p className="cedros-withdrawal-empty">No tokens found in your wallet.</p>
          )}

          <div className="cedros-withdrawal-tokens">
            {heldTokens.map((token) => (
              <button
                key={token.mint}
                type="button"
                className="cedros-withdrawal-token-pill"
                onClick={() => handleSelectToken(token)}
              >
                <span className="cedros-withdrawal-token-symbol">{token.symbol}</span>
                <span className="cedros-withdrawal-token-balance">{token.displayBalance}</span>
              </button>
            ))}
          </div>

          {onCancel && (
            <button
              type="button"
              className="cedros-button cedros-button-ghost cedros-withdrawal-cancel"
              onClick={onCancel}
            >
              Cancel
            </button>
          )}
        </div>
      )}

      {/* Form */}
      {step === 'form' && selectedToken && (
        <div className="cedros-withdrawal-form">
          <div className="cedros-withdrawal-header">
            <button
              type="button"
              className="cedros-button cedros-button-ghost cedros-button-sm"
              onClick={handleBack}
            >
              Back
            </button>
            <h3 className="cedros-withdrawal-title">Withdraw {selectedToken.symbol}</h3>
          </div>

          <div className="cedros-withdrawal-balance-hint">
            Balance: {selectedToken.displayBalance} {selectedToken.symbol}
          </div>

          <div className="cedros-withdrawal-field">
            <label className="cedros-label" htmlFor="cedros-withdrawal-destination">
              Destination Address
            </label>
            <input
              id="cedros-withdrawal-destination"
              type="text"
              className="cedros-input"
              placeholder="Solana address (base58)"
              value={destination}
              onChange={(e) => setDestination(e.target.value)}
              autoComplete="off"
              spellCheck={false}
            />
          </div>

          <div className="cedros-withdrawal-field">
            <label className="cedros-label" htmlFor="cedros-withdrawal-amount">
              Amount ({selectedToken.symbol})
            </label>
            <div className="cedros-withdrawal-amount-row">
              <input
                id="cedros-withdrawal-amount"
                type="number"
                className="cedros-input"
                placeholder="0.00"
                value={amount}
                onChange={(e) => setAmount(e.target.value)}
                min="0"
                step="any"
              />
              <button
                type="button"
                className="cedros-button cedros-button-ghost cedros-button-sm"
                onClick={handleMax}
              >
                Max
              </button>
            </div>
          </div>

          {(validationError || error) && <ErrorMessage error={validationError || error || ''} />}

          <button
            type="button"
            className="cedros-button cedros-button-primary cedros-withdrawal-submit"
            onClick={handleFormSubmit}
          >
            Review Withdrawal
          </button>
        </div>
      )}

      {/* Confirm */}
      {step === 'confirm' && selectedToken && (
        <div className="cedros-withdrawal-confirm">
          <div className="cedros-withdrawal-header">
            <button
              type="button"
              className="cedros-button cedros-button-ghost cedros-button-sm"
              onClick={handleBack}
              disabled={isSubmitting}
            >
              Back
            </button>
            <h3 className="cedros-withdrawal-title">Confirm Withdrawal</h3>
          </div>

          <div className="cedros-withdrawal-summary">
            <div className="cedros-withdrawal-summary-row">
              <span className="cedros-withdrawal-summary-label">Token</span>
              <span className="cedros-withdrawal-summary-value">{selectedToken.symbol}</span>
            </div>
            <div className="cedros-withdrawal-summary-row">
              <span className="cedros-withdrawal-summary-label">Amount</span>
              <span className="cedros-withdrawal-summary-value">
                {formatTokenAmount(rawAmount, selectedToken.decimals)} {selectedToken.symbol}
              </span>
            </div>
            <div className="cedros-withdrawal-summary-row">
              <span className="cedros-withdrawal-summary-label">Destination</span>
              <span className="cedros-withdrawal-summary-value" title={destination}>
                {truncateAddress(destination)}
              </span>
            </div>
            <div className="cedros-withdrawal-summary-row">
              <span className="cedros-withdrawal-summary-label">Est. Fee</span>
              <span className="cedros-withdrawal-summary-value">~0.000005 SOL</span>
            </div>
          </div>

          {error && <ErrorMessage error={error} />}

          <div className="cedros-withdrawal-actions">
            <button
              type="button"
              className="cedros-button cedros-button-outline"
              onClick={handleBack}
              disabled={isSubmitting}
            >
              Cancel
            </button>
            <button
              type="button"
              className="cedros-button cedros-button-primary"
              onClick={handleConfirm}
              disabled={isSubmitting}
            >
              {isSubmitting ? 'Sending...' : 'Confirm & Send'}
            </button>
          </div>
        </div>
      )}

      {/* Processing */}
      {step === 'processing' && (
        <div className="cedros-withdrawal-processing">
          <LoadingSpinner />
          <p className="cedros-withdrawal-processing-text">Sending {selectedToken?.symbol}...</p>
        </div>
      )}

      {/* Success */}
      {step === 'success' && result && (
        <div className="cedros-withdrawal-success">
          <div className="cedros-withdrawal-success-icon">&#10003;</div>
          <h3 className="cedros-withdrawal-title">Withdrawal Complete</h3>
          <p className="cedros-withdrawal-subtitle">
            {formatTokenAmount(rawAmount, selectedToken?.decimals ?? 9)} {selectedToken?.symbol}{' '}
            sent
          </p>

          <div className="cedros-withdrawal-tx">
            <span className="cedros-withdrawal-tx-label">Transaction</span>
            <a
              className="cedros-withdrawal-tx-link"
              href={explorerTxUrl}
              target="_blank"
              rel="noreferrer"
            >
              {truncateAddress(result.txSignature)}
            </a>
          </div>

          <button
            type="button"
            className="cedros-button cedros-button-outline cedros-withdrawal-done"
            onClick={handleReset}
          >
            Done
          </button>
        </div>
      )}
    </div>
  );
}
