import { useState, useCallback, useEffect, useMemo } from 'react';
import type { SystemSetting, UpdateSettingRequest } from '../../types';
import { LoadingSpinner } from '../shared/LoadingSpinner';
import { ErrorMessage } from '../shared/ErrorMessage';
import { useSystemSettings } from '../../hooks/useSystemSettings';
import { SETTING_METADATA, CATEGORY_METADATA } from './settings/settingsMetadata';
import { SettingsSection as SettingsGrid } from './settings/settingsInputs';

// =============================================================================
// COMPONENT PROPS
// =============================================================================

export interface SystemSettingsProps {
  /** Whether to show section descriptions */
  showDescriptions?: boolean;
  /** Additional CSS class */
  className?: string;
  /** Callback when settings are saved */
  onSave?: () => void;
}

// =============================================================================
// MAIN COMPONENT
// =============================================================================

/**
 * System settings management component for administrators.
 * Uses manual save/reset flow (not autosave).
 */
export function SystemSettings({
  showDescriptions = true,
  className = '',
  onSave,
}: SystemSettingsProps) {
  const { settings, isLoading, isUpdating, error, fetchSettings, updateSettings } =
    useSystemSettings();

  const [edits, setEdits] = useState<Record<string, string>>({});
  const [saveError, setSaveError] = useState<string | null>(null);
  const [saveSuccess, setSaveSuccess] = useState(false);

  useEffect(() => {
    fetchSettings();
  }, [fetchSettings]);

  // Clear success message after 3 seconds
  useEffect(() => {
    if (saveSuccess) {
      const timer = setTimeout(() => setSaveSuccess(false), 3000);
      return () => clearTimeout(timer);
    }
  }, [saveSuccess]);

  const handleChange = useCallback((key: string, value: string) => {
    setEdits((prev) => ({ ...prev, [key]: value }));
    setSaveError(null);
    setSaveSuccess(false);
  }, []);

  const handleSave = useCallback(async () => {
    const updates: UpdateSettingRequest[] = Object.entries(edits).map(([key, value]) => ({
      key,
      value,
    }));

    if (updates.length === 0) return;

    try {
      await updateSettings(updates);
      setEdits({});
      setSaveError(null);
      setSaveSuccess(true);
      onSave?.();
    } catch (err) {
      setSaveError(err instanceof Error ? err.message : 'Failed to save settings');
    }
  }, [edits, updateSettings, onSave]);

  const handleReset = useCallback(() => {
    setEdits({});
    setSaveError(null);
    setSaveSuccess(false);
  }, []);

  const hasChanges = Object.keys(edits).length > 0;
  const changeCount = Object.keys(edits).length;

  if (isLoading && Object.keys(settings).length === 0) {
    return (
      <div className={`cedros-system-settings cedros-system-settings-loading ${className}`}>
        <LoadingSpinner />
        <span>Loading settings...</span>
      </div>
    );
  }

  if (error) {
    return (
      <div className={`cedros-system-settings ${className}`}>
        <ErrorMessage error={error.message} />
      </div>
    );
  }

  const categories = Object.keys(settings).sort();

  if (categories.length === 0) {
    return (
      <div className={`cedros-system-settings cedros-system-settings-empty ${className}`}>
        <p>No system settings found.</p>
      </div>
    );
  }

  return (
    <div className={`cedros-system-settings ${className}`}>
      {saveError && <ErrorMessage error={saveError} />}
      {saveSuccess && <div className="cedros-settings-success">Settings saved successfully.</div>}

      {categories.map((category) => (
        <CategorySection
          key={category}
          category={category}
          settings={settings[category]}
          edits={edits}
          showDescription={showDescriptions}
          onChange={handleChange}
        />
      ))}

      <div className="cedros-system-settings-actions">
        {hasChanges && (
          <span className="cedros-settings-change-count">
            {changeCount} unsaved change{changeCount !== 1 ? 's' : ''}
          </span>
        )}
        <button
          type="button"
          className="cedros-button cedros-button-secondary"
          onClick={handleReset}
          disabled={!hasChanges || isUpdating}
        >
          Reset
        </button>
        <button
          type="button"
          className="cedros-button cedros-button-primary"
          onClick={handleSave}
          disabled={!hasChanges || isUpdating}
        >
          {isUpdating ? <LoadingSpinner size="sm" /> : 'Save Changes'}
        </button>
      </div>
    </div>
  );
}

// =============================================================================
// CATEGORY SECTION  header + ordered settings grid
// =============================================================================

/** Stable key order derived from SETTING_METADATA insertion order */
const METADATA_KEY_ORDER = Object.keys(SETTING_METADATA);

interface CategorySectionProps {
  category: string;
  settings: SystemSetting[];
  edits: Record<string, string>;
  showDescription: boolean;
  onChange: (key: string, value: string) => void;
}

function CategorySection({
  category,
  settings,
  edits,
  showDescription,
  onChange,
}: CategorySectionProps) {
  const meta = CATEGORY_METADATA[category] || {
    label: category,
    description: '',
    icon: '',
  };

  // Sort settings by SETTING_METADATA order; unknown keys sort to end
  const orderedSettings = useMemo(() => {
    return [...settings].sort((a, b) => {
      const ai = METADATA_KEY_ORDER.indexOf(a.key);
      const bi = METADATA_KEY_ORDER.indexOf(b.key);
      return (ai === -1 ? Infinity : ai) - (bi === -1 ? Infinity : bi);
    });
  }, [settings]);

  return (
    <section className="cedros-settings-section">
      <div className="cedros-settings-section-header">
        <span className="cedros-settings-section-icon">{meta.icon}</span>
        <div>
          <h3 className="cedros-settings-section-title">{meta.label}</h3>
          {showDescription && meta.description && (
            <p className="cedros-settings-section-description">{meta.description}</p>
          )}
        </div>
      </div>

      <SettingsGrid settings={orderedSettings} edits={edits} onChange={onChange} />
    </section>
  );
}
