/**
 * Tiered amount input with deposit tier indicator
 *
 * Shows which deposit tier (private, public, sol_micro) applies
 * based on the entered USD amount. Displays tier-specific guidance
 * for privacy and processing expectations.
 */

import { useMemo } from 'react';
import type { DepositTier, DepositConfigResponse } from '../../types/deposit';
import { getTierForAmount } from './tierUtils';
import solLogo from '../../assets/tokens/sol.webp';

export interface TieredAmountSliderProps {
  /** Deposit configuration with tier thresholds */
  config: DepositConfigResponse;
  /** Current amount in USD */
  valueUsd: number;
  /** Amount change handler */
  onChange: (valueUsd: number) => void;
  /** Cap in USD (default: 10000) */
  maxUsd?: number;
  /** Disabled state */
  disabled?: boolean;
  /** Additional CSS classes */
  className?: string;
}

const DEFAULT_MAX_USD = 10_000;
const SLIDER_STEPS = 1000;
const CURVE_POWER = 3;

function formatUsd(value: number): string {
  if (!Number.isFinite(value)) return '$0';
  return `$${Math.round(value)}`;
}

function getTierInfo(tier: DepositTier, config: DepositConfigResponse) {
  switch (tier) {
    case 'private':
      return {
        label: 'Private',
        detail: 'Private transaction, instant credit',
        note: null,
      };
    case 'public':
      return {
        label: 'Public',
        detail: 'Visible on-chain, instant credit',
        note: null,
      };
    case 'sol_micro':
      return {
        label: 'SOL Only',
        detail: `SOL only under ${formatUsd(config.solMicroMaxUsd)}`,
        note: 'Small deposits must be sent in SOL and are visible on-chain. Increase the amount to use tokens.',
      };
  }
}

function clamp(value: number, min: number, max: number): number {
  return Math.min(Math.max(value, min), max);
}

function usdToSlider(usd: number, maxUsd: number): number {
  if (maxUsd <= 0) return 0;
  const normalized = clamp(usd / maxUsd, 0, 1);
  return Math.round(Math.pow(normalized, 1 / CURVE_POWER) * SLIDER_STEPS);
}

function sliderToUsd(position: number, maxUsd: number): number {
  const normalized = clamp(position / SLIDER_STEPS, 0, 1);
  return maxUsd * Math.pow(normalized, CURVE_POWER);
}

function getUsdStep(value: number): number {
  if (value < 10) return 0.01;
  if (value < 100) return 1;
  if (value < 500) return 5;
  if (value < 1000) return 10;
  if (value < 5000) return 25;
  return 50;
}

function getStepDecimals(step: number): number {
  if (step < 1) return 2;
  return 0;
}

function roundUsd(value: number): number {
  const step = getUsdStep(value);
  const rounded = Math.round(value / step) * step;
  const decimals = getStepDecimals(step);
  return Number(rounded.toFixed(decimals));
}

/**
 * Tiered amount input with deposit tier indicator
 */
export function TieredAmountSlider({
  config,
  valueUsd,
  onChange,
  maxUsd = DEFAULT_MAX_USD,
  disabled = false,
  className = '',
}: TieredAmountSliderProps) {
  const normalizedValue = clamp(Number.isFinite(valueUsd) ? valueUsd : 0, 0, maxUsd);
  const tier = useMemo(() => getTierForAmount(normalizedValue, config), [normalizedValue, config]);
  const tierInfo = getTierInfo(tier, config);
  const sliderValue = usdToSlider(normalizedValue, maxUsd);
  const sliderPercent = (sliderValue / SLIDER_STEPS) * 100;

  return (
    <div className={`cedros-tiered-slider ${className}`}>
      <div className="cedros-tiered-slider-input-row">
        <div className="cedros-tiered-slider-input">
          <span className="cedros-tiered-slider-currency">$</span>
          <input
            type="number"
            value={normalizedValue || ''}
            onChange={(e) => onChange(roundUsd(parseFloat(e.target.value) || 0))}
            placeholder="Enter amount"
            disabled={disabled}
            min={0}
            step={getUsdStep(normalizedValue)}
            className="cedros-deposit-flow-input cedros-tiered-slider-input-field"
            aria-label="Deposit amount"
          />
        </div>
        <div className="cedros-tiered-slider-tier">
          <span className={`cedros-tiered-slider-badge cedros-tiered-slider-badge-${tier}`}>
            {tier === 'sol_micro' && (
              <img src={solLogo} alt="SOL" className="cedros-tiered-slider-badge-icon" />
            )}
            {tierInfo.label}
          </span>
          <span className="cedros-tiered-slider-tier-detail">{tierInfo.detail}</span>
        </div>
      </div>

      <input
        type="range"
        min={0}
        max={SLIDER_STEPS}
        step={1}
        value={sliderValue}
        onChange={(e) => onChange(roundUsd(sliderToUsd(parseFloat(e.target.value), maxUsd)))}
        className="cedros-tiered-slider-range"
        style={{
          background: `linear-gradient(to right, var(--cedros-primary) 0%, var(--cedros-primary) ${sliderPercent}%, var(--cedros-border) ${sliderPercent}%, var(--cedros-border) 100%)`,
        }}
        disabled={disabled}
        aria-label="Deposit amount slider"
      />

      {tierInfo.note && <div className="cedros-tiered-slider-note">{tierInfo.note}</div>}
    </div>
  );
}
