import type {
  ChangePasswordRequest,
  ChangePasswordResponse,
  UpdateProfileRequest,
  UpdateProfileResponse,
} from '../types';
import { ApiClient, handleApiError } from './apiClient';

/**
 * API client for user profile operations
 *
 * Supports:
 * - POST /auth/change-password - Change user password
 * - PATCH /auth/me - Update profile (name, picture) [requires backend support]
 */
export class ProfileApiClient {
  private client: ApiClient;

  constructor(
    baseUrl: string,
    timeoutMs?: number,
    retryAttempts?: number,
    getAccessToken?: () => string | null
  ) {
    this.client = new ApiClient({ baseUrl, timeoutMs, retryAttempts, getAccessToken });
  }

  /**
   * Change the user's password
   *
   * Requires the current password for verification. Also revokes all other sessions
   * and re-encrypts wallet Share A if using password-based wallet protection.
   */
  async changePassword(data: ChangePasswordRequest): Promise<ChangePasswordResponse> {
    try {
      return await this.client.post<ChangePasswordResponse>('/auth/change-password', data);
    } catch (error) {
      throw handleApiError(error, 'Failed to change password');
    }
  }

  /**
   * Update user profile (name, picture)
   *
   * NOTE: Requires PATCH /auth/me endpoint on the backend.
   * If not implemented, returns a rejection.
   */
  async updateProfile(data: UpdateProfileRequest): Promise<UpdateProfileResponse> {
    try {
      return await this.client.patch<UpdateProfileResponse>('/auth/me', data);
    } catch (error) {
      throw handleApiError(error, 'Failed to update profile');
    }
  }
}
