import { useState, useCallback, useEffect } from 'react';
import { useCedrosLogin } from '../context/useCedrosLogin';
import { EmailLoginForm } from './email/EmailLoginForm';
import { EmailRegisterForm } from './email/EmailRegisterForm';
import { ForgotPasswordForm } from './email/ForgotPasswordForm';
import { GoogleLoginButton } from './google/GoogleLoginButton';
import { AppleLoginButton } from './apple/AppleLoginButton';
import { SolanaLoginButton } from './solana/SolanaLoginButton';
import { PasskeyLoginButton } from './webauthn/PasskeyLoginButton';
import { detectSolanaWallets } from '../utils/walletDetection';
import { isAppleDevice } from '../utils/deviceDetection';

type TabValue = 'login' | 'register';
type ViewState = 'form' | 'forgotPassword';
const TABS: TabValue[] = ['login', 'register'];

export interface LoginFormProps {
  onSuccess?: () => void;
  className?: string;
  defaultTab?: 'login' | 'register';
}

/**
 * Combined login form with all enabled auth methods
 */
export function LoginForm({ onSuccess, className = '', defaultTab = 'login' }: LoginFormProps) {
  const { config } = useCedrosLogin();
  const [activeTab, setActiveTab] = useState<TabValue>(defaultTab);
  const [viewState, setViewState] = useState<ViewState>('form');
  // Detect wallets on initial render and re-check on window load/focus
  const [hasWallets, setHasWallets] = useState(() => detectSolanaWallets());
  const [isApple] = useState(() => isAppleDevice());

  useEffect(() => {
    const updateWallets = () => setHasWallets(detectSolanaWallets());
    updateWallets();
    window.addEventListener('load', updateWallets);
    window.addEventListener('focus', updateWallets);
    return () => {
      window.removeEventListener('load', updateWallets);
      window.removeEventListener('focus', updateWallets);
    };
  }, []);

  // Get forgot password mode from config (only show link in 'reset' mode - instantLink is handled inline)
  const forgotPasswordMode = config.forms?.forgotPassword?.mode ?? 'reset';

  // UI-04: Handle Arrow key navigation for tabs (ARIA tabs pattern)
  const handleTabKeyDown = useCallback(
    (e: React.KeyboardEvent<HTMLButtonElement>) => {
      const currentIndex = TABS.indexOf(activeTab);
      let newIndex = currentIndex;

      switch (e.key) {
        case 'ArrowLeft':
        case 'ArrowUp':
          // Move to previous tab (wrapping to end)
          newIndex = currentIndex === 0 ? TABS.length - 1 : currentIndex - 1;
          break;
        case 'ArrowRight':
        case 'ArrowDown':
          // Move to next tab (wrapping to start)
          newIndex = currentIndex === TABS.length - 1 ? 0 : currentIndex + 1;
          break;
        case 'Home':
          newIndex = 0;
          break;
        case 'End':
          newIndex = TABS.length - 1;
          break;
        default:
          return; // Don't prevent default for other keys
      }

      e.preventDefault();
      const newTab = TABS[newIndex];
      setActiveTab(newTab);
      // Focus the new tab button
      const newButton = document.getElementById(`cedros-tab-${newTab}`);
      newButton?.focus();
    },
    [activeTab]
  );

  const features = config.features ?? {
    email: true,
    google: true,
    apple: true,
    solana: true,
    webauthn: true,
  };
  const showEmail = features.email !== false;
  const showGoogle = features.google !== false && config.googleClientId;
  // Only show Apple button if feature enabled, clientId configured, AND on Apple device
  const showApple = features.apple !== false && config.appleClientId && isApple;
  // Only show Solana button if feature enabled AND wallets are installed
  const showSolana = features.solana !== false && hasWallets;
  const showPasskey = features.webauthn !== false;

  const showDivider = showEmail && (showGoogle || showApple || showSolana || showPasskey);

  // Show forgot password form
  if (viewState === 'forgotPassword') {
    return (
      <div className={`cedros-login-form ${className}`}>
        <ForgotPasswordForm onCancel={() => setViewState('form')} />
      </div>
    );
  }

  return (
    <div className={`cedros-login-form ${className}`}>
      {/* Social login buttons */}
      {(showPasskey || showGoogle || showApple || showSolana) && (
        <div className="cedros-social-buttons">
          {showPasskey && <PasskeyLoginButton onSuccess={onSuccess} />}
          {showGoogle && <GoogleLoginButton onSuccess={onSuccess} />}
          {showApple && <AppleLoginButton onSuccess={onSuccess} />}
          {showSolana && <SolanaLoginButton onSuccess={onSuccess} />}
        </div>
      )}

      {/* Divider */}
      {showDivider && (
        <div className="cedros-divider">
          <span>Or continue with</span>
        </div>
      )}

      {/* Email forms */}
      {showEmail && (
        <>
          {/* Tabs */}
          <div className="cedros-tabs" role="tablist" aria-label="Authentication method">
            <button
              type="button"
              role="tab"
              id="cedros-tab-login"
              className={`cedros-tab ${activeTab === 'login' ? 'cedros-tab-active' : ''}`}
              onClick={() => setActiveTab('login')}
              onKeyDown={handleTabKeyDown}
              aria-selected={activeTab === 'login'}
              aria-controls="cedros-tabpanel-login"
              tabIndex={activeTab === 'login' ? 0 : -1}
            >
              Sign in
            </button>
            <button
              type="button"
              role="tab"
              id="cedros-tab-register"
              className={`cedros-tab ${activeTab === 'register' ? 'cedros-tab-active' : ''}`}
              onClick={() => setActiveTab('register')}
              onKeyDown={handleTabKeyDown}
              aria-selected={activeTab === 'register'}
              aria-controls="cedros-tabpanel-register"
              tabIndex={activeTab === 'register' ? 0 : -1}
            >
              Sign up
            </button>
          </div>

          {/* Tab panels */}
          <div
            role="tabpanel"
            id={`cedros-tabpanel-${activeTab}`}
            aria-labelledby={`cedros-tab-${activeTab}`}
          >
            {activeTab === 'login' ? (
              <EmailLoginForm
                onSuccess={onSuccess}
                onSwitchToRegister={() => setActiveTab('register')}
                onForgotPassword={
                  forgotPasswordMode === 'reset' ? () => setViewState('forgotPassword') : undefined
                }
              />
            ) : (
              <EmailRegisterForm
                onSuccess={onSuccess}
                onSwitchToLogin={() => setActiveTab('login')}
              />
            )}
          </div>
        </>
      )}
    </div>
  );
}
