/**
 * Wallet enrollment wizard component (server-side signing)
 *
 * Guides user through the complete wallet enrollment process:
 * 1. Choose authentication method (password/passkey)
 * 2. Enter credentials for Share A encryption
 * 3. Display and confirm recovery phrase
 *
 * Share B is stored as plaintext on the server (SSS math protects it).
 */

import { useState, useCallback, useEffect } from 'react';
import { useCedrosLogin } from '../../context/useCedrosLogin';
import { useWalletEnrollment } from '../../hooks/useWalletEnrollment';
import { PasswordInput } from '../email/PasswordInput';
import { RecoveryPhraseDisplay } from './RecoveryPhraseDisplay';
import { validatePassword } from '../../utils/validation';
import type { ShareAAuthMethod } from '../../types/wallet';

export interface WalletEnrollmentProps {
  /** Callback when enrollment completes */
  onComplete?: (solanaPubkey: string) => void;
  /** Callback to cancel enrollment */
  onCancel?: () => void;
  /** Optional class name */
  className?: string;
  /** Force a specific auth method (otherwise auto-detected) */
  forceAuthMethod?: ShareAAuthMethod;
}

/**
 * Wallet enrollment wizard
 */
export function WalletEnrollment({
  onComplete,
  onCancel,
  className = '',
  forceAuthMethod,
}: WalletEnrollmentProps) {
  const { user } = useCedrosLogin();
  const {
    state,
    startEnrollmentWithPassword,
    startEnrollmentWithPasskey,
    confirmRecoveryPhrase,
    cancel,
    isEnrolling,
  } = useWalletEnrollment();

  // Determine default auth method based on user type
  const getDefaultAuthMethod = (): ShareAAuthMethod => {
    if (forceAuthMethod) return forceAuthMethod;
    return 'password';
  };

  const [authMethod, setAuthMethod] = useState<ShareAAuthMethod>(getDefaultAuthMethod);
  const [password, setPassword] = useState('');
  const [confirmPassword, setConfirmPassword] = useState('');
  const [passwordError, setPasswordError] = useState<string | null>(null);

  // MAINT-02: Update authMethod when user changes (e.g., different login)
  useEffect(() => {
    setAuthMethod(getDefaultAuthMethod());
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [user?.id, forceAuthMethod]);

  const handlePasswordSubmit = useCallback(
    async (e: React.FormEvent) => {
      e.preventDefault();

      // Validate passwords match
      if (password !== confirmPassword) {
        setPasswordError('Passwords do not match');
        return;
      }

      // Validate password strength using full validation rules
      const passwordValidation = validatePassword(password);
      if (!passwordValidation.isValid) {
        const firstError = Object.values(passwordValidation.errors)[0];
        setPasswordError(firstError ?? 'Password does not meet requirements');
        return;
      }

      setPasswordError(null);
      await startEnrollmentWithPassword(password);
    },
    [password, confirmPassword, startEnrollmentWithPassword]
  );

  const handlePasskeySubmit = useCallback(async () => {
    await startEnrollmentWithPasskey();
  }, [startEnrollmentWithPasskey]);

  const handleRecoveryConfirm = useCallback(() => {
    confirmRecoveryPhrase();
    if (state.solanaPubkey) {
      onComplete?.(state.solanaPubkey);
    }
  }, [confirmRecoveryPhrase, state.solanaPubkey, onComplete]);

  const handleCancel = useCallback(() => {
    cancel();
    onCancel?.();
  }, [cancel, onCancel]);

  // Show loading/progress indicator during enrollment steps
  // A11Y-03: aria-busy indicates async operation in progress
  if (state.step === 'generating_seed' || state.step === 'splitting_shares') {
    return (
      <div className={`cedros-wallet-enrollment ${className}`} aria-busy="true">
        <div className="cedros-enrollment-progress" role="status" aria-live="polite">
          <div className="cedros-spinner" aria-hidden="true" />
          <p>Generating secure wallet...</p>
        </div>
      </div>
    );
  }

  if (state.step === 'encrypting_shares') {
    return (
      <div className={`cedros-wallet-enrollment ${className}`} aria-busy="true">
        <div className="cedros-enrollment-progress" role="status" aria-live="polite">
          <div className="cedros-spinner" aria-hidden="true" />
          <p>Encrypting wallet shares...</p>
        </div>
      </div>
    );
  }

  if (state.step === 'registering_passkey') {
    return (
      <div className={`cedros-wallet-enrollment ${className}`} aria-busy="true">
        <div className="cedros-enrollment-progress" role="status" aria-live="polite">
          <div className="cedros-passkey-prompt">
            <svg
              className="cedros-passkey-icon"
              width="48"
              height="48"
              viewBox="0 0 48 48"
              fill="none"
              aria-hidden="true"
            >
              <rect
                x="8"
                y="16"
                width="32"
                height="24"
                rx="4"
                stroke="currentColor"
                strokeWidth="2"
              />
              <circle cx="24" cy="28" r="4" stroke="currentColor" strokeWidth="2" />
              <path d="M24 32v4" stroke="currentColor" strokeWidth="2" strokeLinecap="round" />
            </svg>
            <h3>Authenticate with Passkey</h3>
            <p>Follow your browser's prompt to use your passkey.</p>
          </div>
        </div>
      </div>
    );
  }

  if (state.step === 'uploading') {
    return (
      <div className={`cedros-wallet-enrollment ${className}`} aria-busy="true">
        <div className="cedros-enrollment-progress" role="status" aria-live="polite">
          <div className="cedros-spinner" aria-hidden="true" />
          <p>Saving wallet...</p>
        </div>
      </div>
    );
  }

  if (state.step === 'showing_recovery' && state.recoveryPhrase) {
    return (
      <div className={`cedros-wallet-enrollment ${className}`}>
        <RecoveryPhraseDisplay words={state.recoveryPhrase} onConfirm={handleRecoveryConfirm} />
      </div>
    );
  }

  if (state.step === 'complete') {
    return (
      <div className={`cedros-wallet-enrollment ${className}`}>
        <div className="cedros-enrollment-complete">
          <svg
            className="cedros-success-icon"
            width="48"
            height="48"
            viewBox="0 0 48 48"
            fill="none"
            aria-hidden="true"
          >
            <circle cx="24" cy="24" r="20" stroke="currentColor" strokeWidth="2" />
            <path
              d="M16 24l6 6 12-12"
              stroke="currentColor"
              strokeWidth="2"
              strokeLinecap="round"
              strokeLinejoin="round"
            />
          </svg>
          <h3>Wallet Created!</h3>
          <p className="cedros-pubkey">
            <strong>Address:</strong> {state.solanaPubkey}
          </p>
          <p>Your non-custodial Solana wallet is ready to use.</p>
        </div>
      </div>
    );
  }

  if (state.step === 'error') {
    return (
      <div className={`cedros-wallet-enrollment ${className}`}>
        <div className="cedros-enrollment-error">
          <svg
            className="cedros-error-icon"
            width="48"
            height="48"
            viewBox="0 0 48 48"
            fill="none"
            aria-hidden="true"
          >
            <circle cx="24" cy="24" r="20" stroke="currentColor" strokeWidth="2" />
            <path
              d="M24 16v12m0 6h.01"
              stroke="currentColor"
              strokeWidth="2"
              strokeLinecap="round"
            />
          </svg>
          <h3>Enrollment Failed</h3>
          <p className="cedros-error-message">{state.error}</p>
          <div className="cedros-error-actions">
            <button
              type="button"
              className="cedros-button cedros-button-secondary"
              onClick={handleCancel}
            >
              Cancel
            </button>
            <button
              type="button"
              className="cedros-button cedros-button-primary"
              onClick={() => cancel()}
            >
              Try Again
            </button>
          </div>
        </div>
      </div>
    );
  }

  // Default: credential entry form
  return (
    <div className={`cedros-wallet-enrollment ${className}`}>
      <div className="cedros-enrollment-header">
        <h2>Create Wallet</h2>
        <p>Secure your wallet with a {authMethod === 'passkey' ? 'passkey' : 'password'}.</p>
      </div>

      {/* Auth method selector (only show if not forced) */}
      {!forceAuthMethod && (
        <div className="cedros-auth-method-selector">
          <label className="cedros-radio-label">
            <input
              type="radio"
              name="authMethod"
              value="password"
              checked={authMethod === 'password'}
              onChange={() => setAuthMethod('password')}
              disabled={isEnrolling}
            />
            <span>Password</span>
          </label>
          <label className="cedros-radio-label">
            <input
              type="radio"
              name="authMethod"
              value="passkey"
              checked={authMethod === 'passkey'}
              onChange={() => setAuthMethod('passkey')}
              disabled={isEnrolling}
            />
            <span>Passkey</span>
          </label>
        </div>
      )}

      {/* Password form */}
      {authMethod === 'password' && (
        <form onSubmit={handlePasswordSubmit} className="cedros-enrollment-form">
          <PasswordInput
            label="Password"
            value={password}
            onChange={(e) => setPassword(e.target.value)}
            showStrengthMeter
            disabled={isEnrolling}
            required
            minLength={8}
            placeholder="Enter a strong password"
          />

          <PasswordInput
            label="Confirm Password"
            value={confirmPassword}
            onChange={(e) => setConfirmPassword(e.target.value)}
            error={passwordError ?? undefined}
            disabled={isEnrolling}
            required
            minLength={8}
            placeholder="Confirm your password"
          />

          <div className="cedros-password-info">
            <p>This password will be used to sign transactions.</p>
          </div>

          <div className="cedros-enrollment-actions">
            {onCancel && (
              <button
                type="button"
                className="cedros-button cedros-button-secondary"
                onClick={handleCancel}
                disabled={isEnrolling}
              >
                Cancel
              </button>
            )}
            <button
              type="submit"
              className="cedros-button cedros-button-primary"
              disabled={isEnrolling || !password || !confirmPassword}
            >
              {isEnrolling ? 'Creating...' : 'Continue'}
            </button>
          </div>
        </form>
      )}

      {/* Passkey form */}
      {authMethod === 'passkey' && (
        <div className="cedros-enrollment-form">
          <div className="cedros-passkey-info">
            <svg
              className="cedros-passkey-icon"
              width="48"
              height="48"
              viewBox="0 0 48 48"
              fill="none"
              aria-hidden="true"
            >
              <rect
                x="8"
                y="16"
                width="32"
                height="24"
                rx="4"
                stroke="currentColor"
                strokeWidth="2"
              />
              <circle cx="24" cy="28" r="4" stroke="currentColor" strokeWidth="2" />
              <path d="M24 32v4" stroke="currentColor" strokeWidth="2" strokeLinecap="round" />
            </svg>
            <p>Use your passkey (Touch ID, Face ID, or Windows Hello) to secure your wallet.</p>
            <p>You'll use the same passkey to sign transactions.</p>
          </div>

          <div className="cedros-enrollment-actions">
            {onCancel && (
              <button
                type="button"
                className="cedros-button cedros-button-secondary"
                onClick={handleCancel}
                disabled={isEnrolling}
              >
                Cancel
              </button>
            )}
            <button
              type="button"
              className="cedros-button cedros-button-primary"
              onClick={handlePasskeySubmit}
              disabled={isEnrolling}
            >
              {isEnrolling ? 'Creating...' : 'Use Passkey'}
            </button>
          </div>
        </div>
      )}
    </div>
  );
}
