/**
 * Hook for wallet transaction signing (server-side signing)
 *
 * Signing happens server-side:
 * - User provides unlock credential (password/PIN/passkey PRF)
 * - Server decrypts Share A, combines with Share B, signs, wipes
 * - Client just sends credential and transaction, receives signature
 */

import { useState, useCallback } from 'react';
import { useCedrosLogin } from '../context/useCedrosLogin';
import { useWalletMaterial } from './useWalletMaterial';
import { authenticateWithDiscoverablePrf, base64ToUint8Array, uint8ArrayToBase64 } from '../crypto';
import type { UseWalletSigningReturn, UnlockCredential } from '../types/wallet';
import { toCredentialRequest } from '../utils/unlockCredential';

/**
 * Hook for wallet transaction signing
 *
 * Usage:
 * ```tsx
 * const { signTransaction, isSigning, error } = useWalletSigning();
 *
 * // Sign with password
 * const signature = await signTransaction(txBytes, { type: 'password', password: 'xxx' });
 *
 * // Sign with PIN
 * const signature = await signTransaction(txBytes, { type: 'password', password: 'secret' });
 *
 * // Sign with passkey (PRF)
 * const signature = await signTransaction(txBytes, { type: 'prfOutput', prfOutput: 'base64...' });
 * ```
 */
export function useWalletSigning(): UseWalletSigningReturn {
  const { user } = useCedrosLogin();
  const { signTransaction: apiSignTransaction } = useWalletMaterial();

  const [isSigning, setIsSigning] = useState(false);
  const [error, setError] = useState<string | null>(null);

  /**
   * Sign a transaction with the wallet
   *
   * @param transaction - Transaction bytes to sign
   * @param credential - Unlock credential (password, PIN, or PRF output)
   * @returns Ed25519 signature (64 bytes)
   */
  const signTransaction = useCallback(
    async (transaction: Uint8Array, credential?: UnlockCredential): Promise<Uint8Array> => {
      if (!user) {
        const msg = 'User not authenticated';
        setError(msg);
        throw new Error(msg);
      }

      setIsSigning(true);
      setError(null);

      try {
        const request = {
          transaction: uint8ArrayToBase64(transaction),
          ...(credential ? { credential: toCredentialRequest(credential) } : {}),
        };

        const response = await apiSignTransaction(request);

        // Decode signature from base64
        return base64ToUint8Array(response.signature);
      } catch (err) {
        const msg = err instanceof Error ? err.message : 'Signing failed';
        setError(msg);
        throw err;
      } finally {
        setIsSigning(false);
      }
    },
    [user, apiSignTransaction]
  );

  const clearError = useCallback(() => setError(null), []);

  return {
    signTransaction,
    isSigning,
    error,
    clearError,
  };
}

/**
 * Helper hook to get PRF output for passkey signing
 *
 * This handles the WebAuthn PRF flow and returns a credential
 * that can be used with useWalletSigning.
 */
export function usePasskeySigning() {
  const { getMaterial } = useWalletMaterial();
  const [isAuthenticating, setIsAuthenticating] = useState(false);
  const [error, setError] = useState<string | null>(null);

  /**
   * Get PRF output from passkey for wallet signing
   *
   * @returns UnlockCredential with PRF output, or null if cancelled
   */
  const getPasskeyCredential = useCallback(async (): Promise<UnlockCredential | null> => {
    setIsAuthenticating(true);
    setError(null);

    try {
      // Get wallet material to get PRF salt
      const material = await getMaterial();
      if (!material) {
        throw new Error('No wallet enrolled');
      }

      if (material.shareAAuthMethod !== 'passkey') {
        throw new Error('Wallet is not configured for passkey authentication');
      }

      if (!material.prfSalt) {
        throw new Error('No PRF salt configured for wallet');
      }

      // Authenticate with passkey and get PRF output
      const result = await authenticateWithDiscoverablePrf(material.prfSalt);

      try {
        return {
          type: 'prfOutput',
          prfOutput: uint8ArrayToBase64(result.prfOutput),
        };
      } finally {
        // UI-21: Wipe PRF output from memory after encoding to base64
        result.prfOutput.fill(0);
      }
    } catch (err) {
      // User cancelled or error occurred
      const msg = err instanceof Error ? err.message : 'Passkey authentication failed';
      setError(msg);
      return null;
    } finally {
      setIsAuthenticating(false);
    }
  }, [getMaterial]);

  const clearError = useCallback(() => setError(null), []);

  return {
    getPasskeyCredential,
    isAuthenticating,
    error,
    clearError,
  };
}
