import { useState, useCallback, useMemo, useEffect } from 'react';
import type { Session, AuthError, RevokeAllSessionsResponse } from '../types';
import { useCedrosLogin } from '../context/useCedrosLogin';
import { SessionApiClient } from '../utils/sessionApi';

export interface UseSessionsReturn {
  /** List of active sessions */
  sessions: Session[];
  /** Loading state */
  isLoading: boolean;
  /** Error state */
  error: AuthError | null;
  /** Fetch/refresh sessions list */
  fetchSessions: () => Promise<void>;
  /** Revoke all sessions (logout everywhere) */
  revokeAllSessions: () => Promise<RevokeAllSessionsResponse>;
  /** Number of other active sessions (excluding current) */
  otherSessionCount: number;
}

/**
 * Hook for managing user sessions across devices.
 *
 * @example
 * ```tsx
 * function SessionManager() {
 *   const { sessions, isLoading, revokeAllSessions, otherSessionCount } = useSessions();
 *
 *   return (
 *     <div>
 *       <h3>Active Sessions ({sessions.length})</h3>
 *       <ul>
 *         {sessions.map(session => (
 *           <li key={session.id}>
 *             {session.userAgent}
 *             {session.isCurrent && ' (current)'}
 *           </li>
 *         ))}
 *       </ul>
 *       {otherSessionCount > 0 && (
 *         <button onClick={revokeAllSessions}>
 *           Sign out of {otherSessionCount} other device(s)
 *         </button>
 *       )}
 *     </div>
 *   );
 * }
 * ```
 */
export function useSessions(): UseSessionsReturn {
  const { config, authState, _internal } = useCedrosLogin();

  const [sessions, setSessions] = useState<Session[]>([]);
  const [isLoading, setIsLoading] = useState(false);
  const [error, setError] = useState<AuthError | null>(null);

  const apiClient = useMemo(
    () =>
      new SessionApiClient(
        config.serverUrl,
        config.requestTimeout,
        config.retryAttempts,
        _internal?.getAccessToken
      ),
    [config.serverUrl, config.requestTimeout, config.retryAttempts, _internal]
  );

  const fetchSessions = useCallback(async () => {
    if (authState !== 'authenticated') {
      setSessions([]);
      return;
    }

    setIsLoading(true);
    setError(null);

    try {
      const fetchedSessions = await apiClient.listSessions();
      setSessions(fetchedSessions);
    } catch (err) {
      setError(err as AuthError);
    } finally {
      setIsLoading(false);
    }
  }, [authState, apiClient]);

  // Auto-fetch when authenticated
  useEffect(() => {
    if (authState === 'authenticated') {
      fetchSessions();
    } else {
      setSessions([]);
    }
  }, [authState, fetchSessions]);

  const revokeAllSessions = useCallback(async (): Promise<RevokeAllSessionsResponse> => {
    setIsLoading(true);
    setError(null);

    try {
      const result = await apiClient.revokeAllSessions();
      // Refresh sessions list after revoking
      await fetchSessions();
      return result;
    } catch (err) {
      setError(err as AuthError);
      throw err;
    } finally {
      setIsLoading(false);
    }
  }, [apiClient, fetchSessions]);

  const otherSessionCount = useMemo(() => {
    return sessions.filter((s) => !s.isCurrent).length;
  }, [sessions]);

  return {
    sessions,
    isLoading,
    error,
    fetchSessions,
    revokeAllSessions,
    otherSessionCount,
  };
}
