import { useEffect, useRef } from 'react';
import { useCedrosLogin } from '../context/useCedrosLogin';
import { LoginForm } from './LoginForm';
import { ErrorBoundary } from './shared/ErrorBoundary';

export interface LoginModalProps {
  className?: string;
  title?: string;
}

/**
 * Modal containing the login form.
 * Controlled by the CedrosLoginProvider context.
 */
export function LoginModal({ className = '', title = 'Sign in to your account' }: LoginModalProps) {
  const { isModalOpen, closeModal } = useCedrosLogin();
  const modalRef = useRef<HTMLDivElement>(null);
  const previousActiveElement = useRef<Element | null>(null);

  // C-03: Use ref to avoid closeModal in effect dependencies
  // Prevents event listener leak when closeModal identity changes
  const closeModalRef = useRef(closeModal);
  useEffect(() => {
    closeModalRef.current = closeModal;
  }, [closeModal]);

  // Handle escape key and focus trapping
  useEffect(() => {
    if (!isModalOpen) return;

    // Store currently focused element
    previousActiveElement.current = document.activeElement;

    // Focus the modal
    modalRef.current?.focus();

    const handleKeyDown = (e: KeyboardEvent) => {
      if (e.key === 'Escape') {
        closeModalRef.current();
      }

      // Focus trap
      if (e.key === 'Tab' && modalRef.current) {
        const focusableElements = modalRef.current.querySelectorAll<HTMLElement>(
          'button, [href], input, select, textarea, [tabindex]:not([tabindex="-1"])'
        );
        const firstElement = focusableElements[0];
        const lastElement = focusableElements[focusableElements.length - 1];

        if (e.shiftKey && document.activeElement === firstElement) {
          e.preventDefault();
          lastElement?.focus();
        } else if (!e.shiftKey && document.activeElement === lastElement) {
          e.preventDefault();
          firstElement?.focus();
        }
      }
    };

    document.addEventListener('keydown', handleKeyDown);

    // Prevent body scroll
    const previousOverflow = document.body.style.overflow;
    document.body.style.overflow = 'hidden';

    return () => {
      document.removeEventListener('keydown', handleKeyDown);
      document.body.style.overflow = previousOverflow;

      // Restore focus
      if (previousActiveElement.current instanceof HTMLElement) {
        previousActiveElement.current.focus();
      }
    };
  }, [isModalOpen]);

  if (!isModalOpen) return null;

  const handleBackdropClick = (e: React.MouseEvent) => {
    if (e.target === e.currentTarget) {
      closeModal();
    }
  };

  return (
    <div
      className={`cedros-modal-backdrop ${className}`}
      onClick={handleBackdropClick}
      role="presentation"
    >
      <div
        ref={modalRef}
        className="cedros-modal"
        role="dialog"
        aria-modal="true"
        aria-labelledby="cedros-modal-title"
        tabIndex={-1}
      >
        <div className="cedros-modal-header">
          <h2 id="cedros-modal-title" className="cedros-modal-title">
            {title}
          </h2>
          <button
            type="button"
            className="cedros-modal-close"
            onClick={closeModal}
            aria-label="Close"
          >
            <svg width="24" height="24" viewBox="0 0 24 24" fill="none" aria-hidden="true">
              <path
                d="M18 6L6 18M6 6l12 12"
                stroke="currentColor"
                strokeWidth="2"
                strokeLinecap="round"
              />
            </svg>
          </button>
        </div>
        {/* M-06: ErrorBoundary catches rendering errors in login form */}
        <div className="cedros-modal-content">
          <ErrorBoundary>
            <LoginForm onSuccess={closeModal} />
          </ErrorBoundary>
        </div>
      </div>
    </div>
  );
}
