/**
 * Component for entering BIP-39 recovery phrase
 *
 * Provides input fields for 12 words with validation and autocomplete.
 */

import { useState, useCallback, useId, useRef, useEffect } from 'react';
import {
  isValidWord,
  getWordSuggestions,
  parseMnemonicInput,
  isValidMnemonic,
  MNEMONIC_WORD_COUNT,
} from '../../crypto';

export interface RecoveryPhraseInputProps {
  /** Callback when valid phrase is entered */
  onSubmit: (words: string[]) => void;
  /** Callback to cancel */
  onCancel?: () => void;
  /** Whether submission is in progress */
  isSubmitting?: boolean;
  /** Error message to display */
  error?: string;
  /** Optional class name */
  className?: string;
}

/**
 * Input form for recovery phrase
 */
export function RecoveryPhraseInput({
  onSubmit,
  onCancel,
  isSubmitting = false,
  error,
  className = '',
}: RecoveryPhraseInputProps) {
  const [words, setWords] = useState<string[]>(Array(MNEMONIC_WORD_COUNT).fill(''));
  const [activeIndex, setActiveIndex] = useState<number | null>(null);
  const [suggestions, setSuggestions] = useState<string[]>([]);
  const [validationError, setValidationError] = useState<string | null>(null);
  const inputId = useId();
  const suggestionsTimerRef = useRef<number | null>(null);

  const handleWordChange = useCallback(
    (index: number, value: string) => {
      const newWords = [...words];
      newWords[index] = value.toLowerCase().trim();
      setWords(newWords);

      // Update suggestions
      if (value.length > 0) {
        const newSuggestions = getWordSuggestions(value, 5);
        setSuggestions(newSuggestions);
      } else {
        setSuggestions([]);
      }

      // Clear validation error while typing
      setValidationError(null);
    },
    [words]
  );

  const handleWordFocus = useCallback((index: number) => {
    setActiveIndex(index);
    setSuggestions([]);
  }, []);

  const handleWordBlur = useCallback(
    (index: number) => {
      // Validate word on blur
      const word = words[index];
      if (word && !isValidWord(word)) {
        setValidationError(`Word ${index + 1} is not in the wordlist`);
      }

      // Clear suggestions after a delay (allows clicking suggestions)
      if (suggestionsTimerRef.current !== null) {
        window.clearTimeout(suggestionsTimerRef.current);
      }
      suggestionsTimerRef.current = window.setTimeout(() => {
        if (activeIndex === index) {
          setSuggestions([]);
        }
      }, 200);
    },
    [words, activeIndex]
  );

  useEffect(() => {
    return () => {
      if (suggestionsTimerRef.current !== null) {
        window.clearTimeout(suggestionsTimerRef.current);
        suggestionsTimerRef.current = null;
      }
    };
  }, []);

  const handleSuggestionClick = useCallback(
    (suggestion: string) => {
      if (activeIndex !== null) {
        const newWords = [...words];
        newWords[activeIndex] = suggestion;
        setWords(newWords);
        setSuggestions([]);

        // Move to next input
        const nextInput = document.querySelector<HTMLInputElement>(
          `[data-word-index="${activeIndex + 1}"]`
        );
        nextInput?.focus();
      }
    },
    [activeIndex, words]
  );

  const handlePaste = useCallback((e: React.ClipboardEvent) => {
    const pastedText = e.clipboardData.getData('text');
    const parsedWords = parseMnemonicInput(pastedText);

    if (parsedWords.length === MNEMONIC_WORD_COUNT) {
      e.preventDefault();
      setWords(parsedWords);
      setValidationError(null);
    }
  }, []);

  const handleSubmit = useCallback(
    (e: React.FormEvent) => {
      e.preventDefault();

      // Validate all words are filled
      const emptyCount = words.filter((w) => !w).length;
      if (emptyCount > 0) {
        setValidationError(`Please enter all ${MNEMONIC_WORD_COUNT} words`);
        return;
      }

      // Validate each word is in wordlist
      const invalidWords = words
        .map((w, i) => ({ word: w, index: i + 1 }))
        .filter(({ word }) => !isValidWord(word));

      if (invalidWords.length > 0) {
        setValidationError(`Invalid words: ${invalidWords.map((w) => `#${w.index}`).join(', ')}`);
        return;
      }

      // Validate checksum
      if (!isValidMnemonic(words)) {
        setValidationError('Invalid recovery phrase - please check your words');
        return;
      }

      onSubmit(words);
    },
    [words, onSubmit]
  );

  const displayError = error || validationError;

  return (
    <form
      className={`cedros-recovery-phrase-input ${className}`}
      onSubmit={handleSubmit}
      onPaste={handlePaste}
    >
      <div className="cedros-recovery-input-header">
        <h3 className="cedros-recovery-input-title">Enter Recovery Phrase</h3>
        <p className="cedros-recovery-input-description">
          Enter your 12-word recovery phrase. You can paste the entire phrase at once.
        </p>
      </div>

      <div className="cedros-word-inputs">
        {Array.from({ length: MNEMONIC_WORD_COUNT }, (_, index) => (
          <div key={index} className="cedros-word-input-wrapper">
            <label className="cedros-word-label">{index + 1}.</label>
            <input
              type="text"
              className={`cedros-word-input ${
                words[index] && !isValidWord(words[index])
                  ? 'cedros-word-invalid'
                  : words[index] && isValidWord(words[index])
                    ? 'cedros-word-valid'
                    : ''
              }`}
              value={words[index]}
              onChange={(e) => handleWordChange(index, e.target.value)}
              onFocus={() => handleWordFocus(index)}
              onBlur={() => handleWordBlur(index)}
              data-word-index={index}
              autoComplete="off"
              autoCapitalize="none"
              spellCheck={false}
              disabled={isSubmitting}
              aria-label={`Word ${index + 1}`}
            />
          </div>
        ))}
      </div>

      {activeIndex !== null && suggestions.length > 0 && (
        <div className="cedros-suggestions" role="listbox" id={`${inputId}-suggestions`}>
          {suggestions.map((suggestion) => (
            <button
              key={suggestion}
              type="button"
              className="cedros-suggestion"
              onClick={() => handleSuggestionClick(suggestion)}
              role="option"
            >
              {suggestion}
            </button>
          ))}
        </div>
      )}

      {displayError && (
        <p className="cedros-input-error" role="alert">
          {displayError}
        </p>
      )}

      <div className="cedros-recovery-input-actions">
        {onCancel && (
          <button
            type="button"
            className="cedros-button cedros-button-secondary"
            onClick={onCancel}
            disabled={isSubmitting}
          >
            Cancel
          </button>
        )}
        <button
          type="submit"
          className="cedros-button cedros-button-primary"
          disabled={isSubmitting}
        >
          {isSubmitting ? 'Recovering...' : 'Recover Wallet'}
        </button>
      </div>
    </form>
  );
}
