/**
 * Wallet status indicator component
 *
 * Displays the current state of the user's wallet:
 * - Loading: Checking wallet status
 * - Not enrolled: No wallet created yet
 * - Enrolled (locked): Wallet exists but needs unlock
 * - Enrolled (unlocked): Wallet ready for transactions
 * - Error: Something went wrong
 *
 * Supports two modes:
 * - Uncontrolled (default): Gets status from useWallet() hook
 * - Controlled: Accepts status/publicKey props (useful for stories/demos)
 */

import { useWallet } from '../../hooks/useWallet';
import type { WalletStatus as WalletStatusType } from '../../types/wallet';
import { WalletAddressRow } from './WalletAddressRow';

export interface WalletStatusProps {
  /** Controlled mode: Override wallet status (for demos/stories) */
  status?: WalletStatusType;
  /** Controlled mode: Override public key display */
  publicKey?: string;
  /** Controlled mode: Override lock state */
  onLock?: () => void;
  /** Callback when user wants to create a wallet */
  onEnroll?: () => void;
  /** Callback when user wants to unlock wallet */
  onUnlock?: () => void;
  /** Callback when user wants to recover wallet */
  onRecover?: () => void;
  /** Whether to show action buttons */
  showActions?: boolean;
  /** Compact display mode */
  compact?: boolean;
  /** Additional CSS classes */
  className?: string;
}

/**
 * Wallet status indicator
 *
 * Shows current wallet state with optional action buttons.
 * Pass `status` prop to use in controlled mode (bypasses hook).
 */
export function WalletStatus({
  status: controlledStatus,
  publicKey: controlledPubkey,
  onLock: _onLock,
  onEnroll,
  onUnlock,
  onRecover,
  showActions = true,
  compact = false,
  className = '',
}: WalletStatusProps) {
  // onLock reserved for future use
  void _onLock;
  // Use controlled mode if status prop is provided, otherwise use hook
  const isControlled = controlledStatus !== undefined;
  const wallet = useWallet();

  const status = isControlled ? controlledStatus : wallet.status;
  const solanaPubkey = isControlled ? (controlledPubkey ?? null) : wallet.solanaPubkey;
  const error = isControlled ? null : wallet.error;
  const refresh = isControlled ? () => {} : wallet.refresh;
  const clearError = isControlled ? () => {} : wallet.clearError;

  const statusConfig = getStatusConfig(status, error);

  if (compact) {
    return (
      <div className={`cedros-wallet-status cedros-wallet-status-compact ${className}`}>
        <span
          className={`cedros-wallet-status-dot cedros-wallet-status-${statusConfig.color}`}
          title={statusConfig.label}
        />
        <span className="cedros-wallet-status-label">{statusConfig.label}</span>
        {solanaPubkey && (
          <span className="cedros-wallet-status-pubkey" title={solanaPubkey}>
            {truncatePubkey(solanaPubkey)}
          </span>
        )}
      </div>
    );
  }

  return (
    <div className={`cedros-wallet-status ${className}`}>
      <div className="cedros-wallet-status-header">
        <div
          className={`cedros-wallet-status-icon cedros-wallet-status-icon-${statusConfig.color}`}
        >
          <StatusIcon status={status} />
        </div>
        <div className="cedros-wallet-status-info">
          <h4 className="cedros-wallet-status-title">{statusConfig.title}</h4>
          <p className="cedros-wallet-status-description">{statusConfig.description}</p>
        </div>
      </div>

      {solanaPubkey && (
        <div className="cedros-wallet-status-pubkey-full">
          <WalletAddressRow address={solanaPubkey} />
        </div>
      )}

      {error && (
        <div className="cedros-wallet-status-error-box" role="alert">
          <p>{error}</p>
          <button
            type="button"
            className="cedros-button cedros-button-sm cedros-button-secondary"
            onClick={clearError}
          >
            Dismiss
          </button>
        </div>
      )}

      {showActions && (
        <div className="cedros-wallet-status-actions">
          {status === 'not_enrolled' && onEnroll && (
            <button
              type="button"
              className="cedros-button cedros-button-primary cedros-button-sm"
              onClick={onEnroll}
            >
              Create Wallet
            </button>
          )}

          {status === 'enrolled_locked' && onUnlock && (
            <button
              type="button"
              className="cedros-button cedros-button-primary cedros-button-sm"
              onClick={onUnlock}
            >
              Unlock Wallet
            </button>
          )}

          {(status === 'not_enrolled' || status === 'error') && onRecover && (
            <button
              type="button"
              className="cedros-button cedros-button-outline cedros-button-sm"
              onClick={onRecover}
            >
              Recover Wallet
            </button>
          )}

          {status === 'error' && (
            <button
              type="button"
              className="cedros-button cedros-button-outline cedros-button-sm"
              onClick={refresh}
            >
              Retry
            </button>
          )}
        </div>
      )}
    </div>
  );
}

interface StatusConfig {
  label: string;
  title: string;
  description: string;
  color: 'loading' | 'success' | 'warning' | 'error' | 'muted';
}

function getStatusConfig(status: WalletStatusType, error: string | null): StatusConfig {
  switch (status) {
    case 'loading':
      return {
        label: 'Loading',
        title: 'Checking Wallet',
        description: 'Verifying wallet status...',
        color: 'loading',
      };

    case 'not_enrolled':
      return {
        label: 'Not Set Up',
        title: 'No Wallet',
        description: 'Create a wallet to start using Solana.',
        color: 'muted',
      };

    case 'enrolled_locked':
      return {
        label: 'Locked',
        title: 'Wallet Locked',
        description: 'Unlock your wallet to sign transactions.',
        color: 'warning',
      };

    case 'enrolled_unlocked':
    case 'unlocked':
      return {
        label: 'Ready',
        title: 'Wallet Ready',
        description: 'Your wallet is unlocked and ready to use.',
        color: 'success',
      };

    case 'error':
    default:
      return {
        label: 'Error',
        title: 'Wallet Error',
        description: error ?? 'An error occurred with your wallet.',
        color: 'error',
      };
  }
}

function truncatePubkey(pubkey: string): string {
  if (pubkey.length <= 12) return pubkey;
  return `${pubkey.slice(0, 4)}...${pubkey.slice(-4)}`;
}

function StatusIcon({ status }: { status: WalletStatusType }) {
  switch (status) {
    case 'loading':
      return (
        <svg viewBox="0 0 24 24" fill="none" aria-hidden="true" className="cedros-spinner">
          <circle cx="12" cy="12" r="9" stroke="currentColor" strokeWidth="2" opacity="0.25" />
          <path
            d="M12 3a9 9 0 0 1 9 9"
            stroke="currentColor"
            strokeWidth="2"
            strokeLinecap="round"
          />
        </svg>
      );

    case 'not_enrolled':
      // Plus icon (add wallet)
      return (
        <svg
          viewBox="0 0 24 24"
          fill="none"
          stroke="currentColor"
          strokeWidth="2"
          strokeLinecap="round"
          strokeLinejoin="round"
          aria-hidden="true"
        >
          <circle cx="12" cy="12" r="10" />
          <path d="M8 12h8M12 8v8" />
        </svg>
      );

    case 'enrolled_locked':
      // Lock icon
      return (
        <svg
          viewBox="0 0 24 24"
          fill="none"
          stroke="currentColor"
          strokeWidth="2"
          strokeLinecap="round"
          strokeLinejoin="round"
          aria-hidden="true"
        >
          <rect x="3" y="11" width="18" height="11" rx="2" ry="2" />
          <path d="M7 11V7a5 5 0 0 1 10 0v4" />
        </svg>
      );

    case 'enrolled_unlocked':
    case 'unlocked':
      // Check icon
      return (
        <svg
          viewBox="0 0 24 24"
          fill="none"
          stroke="currentColor"
          strokeWidth="2.5"
          strokeLinecap="round"
          strokeLinejoin="round"
          aria-hidden="true"
        >
          <path d="M20 6L9 17l-5-5" />
        </svg>
      );

    case 'error':
      // Alert triangle icon
      return (
        <svg
          viewBox="0 0 24 24"
          fill="none"
          stroke="currentColor"
          strokeWidth="2"
          strokeLinecap="round"
          strokeLinejoin="round"
          aria-hidden="true"
        >
          <path d="m21.73 18-8-14a2 2 0 0 0-3.48 0l-8 14A2 2 0 0 0 4 21h16a2 2 0 0 0 1.73-3" />
          <path d="M12 9v4M12 17h.01" />
        </svg>
      );

    default:
      return null;
  }
}
