import { useCallback, useMemo, useState } from 'react';
import { useWallet } from '../../hooks/useWallet';
import { WalletEnrollment } from './WalletEnrollment';
import { WalletRecovery } from './WalletRecovery';
import { WalletStatus } from './WalletStatus';
import { WalletUnlock } from './WalletUnlock';

type WalletManagerView = 'status' | 'enroll' | 'unlock' | 'recover_intro' | 'recover';

export interface WalletManagerProps {
  className?: string;
  showActions?: boolean;
}

/**
 * Orchestrates embedded wallet flows in one cohesive surface:
 * status -> enroll/unlock/recover.
 */
export function WalletManager({ className = '', showActions = true }: WalletManagerProps) {
  const wallet = useWallet();

  const [view, setView] = useState<WalletManagerView>('status');

  const header = useMemo(() => {
    switch (view) {
      case 'enroll':
        return { title: 'Create Wallet', description: 'Set up your embedded wallet.' };
      case 'unlock':
        return { title: 'Unlock Wallet', description: 'Unlock to sign transactions.' };
      case 'recover_intro':
      case 'recover':
        return {
          title: 'Recover Wallet',
          description: 'Restore access using your recovery phrase.',
        };
      case 'status':
      default:
        return null;
    }
  }, [view]);

  const handleBackToStatus = useCallback(() => setView('status'), []);

  const handleEnrollComplete = useCallback(async () => {
    setView('status');
    await wallet.refresh();
  }, [wallet]);

  const handleUnlockComplete = useCallback(async () => {
    setView('status');
    await wallet.refresh();
  }, [wallet]);

  const handleRecoverComplete = useCallback(async () => {
    setView('status');
    await wallet.refresh();
  }, [wallet]);

  return (
    <div className={`cedros-wallet-manager ${className}`}>
      {view !== 'status' && header && (
        <div className="cedros-wallet-manager-header">
          <div className="cedros-wallet-manager-header-text">
            <h3 className="cedros-wallet-manager-title">{header.title}</h3>
            <p className="cedros-wallet-manager-subtitle">{header.description}</p>
          </div>
          <button
            type="button"
            className="cedros-button cedros-button-sm cedros-button-ghost"
            onClick={handleBackToStatus}
          >
            Back
          </button>
        </div>
      )}

      {view === 'status' && (
        <WalletStatus
          onEnroll={() => setView('enroll')}
          onUnlock={() => setView('unlock')}
          onRecover={() => setView('recover_intro')}
          showActions={showActions}
        />
      )}

      {view === 'enroll' && (
        <WalletEnrollment
          onComplete={() => {
            void handleEnrollComplete();
          }}
          onCancel={handleBackToStatus}
        />
      )}

      {view === 'unlock' && (
        <WalletUnlock
          onUnlock={() => {
            void handleUnlockComplete();
          }}
          onCancel={handleBackToStatus}
        />
      )}

      {view === 'recover_intro' && (
        <div className="cedros-wallet-manager-intro">
          <div className="cedros-wallet-manager-intro-card">
            <h4 className="cedros-wallet-manager-intro-title">Before you start</h4>
            <ul className="cedros-wallet-manager-intro-list">
              <li>Youll need your 12-word recovery phrase.</li>
              <li>Youll set a new password or passkey for this wallet.</li>
              <li>If youre on a shared device, avoid copying the phrase into other apps.</li>
            </ul>
            <div className="cedros-wallet-manager-intro-actions">
              <button
                type="button"
                className="cedros-button cedros-button-primary"
                onClick={() => setView('recover')}
              >
                Start recovery
              </button>
              <button
                type="button"
                className="cedros-button cedros-button-secondary"
                onClick={handleBackToStatus}
              >
                Cancel
              </button>
            </div>
          </div>
        </div>
      )}

      {view === 'recover' && (
        <WalletRecovery
          onComplete={() => {
            void handleRecoverComplete();
          }}
          onCancel={handleBackToStatus}
        />
      )}
    </div>
  );
}
