/**
 * Token selector component for deposit currency selection
 *
 * Allows users to select which token to deposit.
 * Supports search and displays token icons.
 */

import { useState, useCallback, useMemo, useRef, useEffect } from 'react';
import type { Token } from './tokens';

export interface TokenSelectorProps {
  /** List of available tokens */
  tokens: Token[];
  /** Currently selected token */
  selectedToken?: Token;
  /** Callback when token is selected */
  onSelect: (token: Token) => void;
  /** Signal to force-open the dropdown */
  openSignal?: number;
  /** Placeholder text when no token selected */
  placeholder?: string;
  /** Disable the selector */
  disabled?: boolean;
  /** Additional CSS classes */
  className?: string;
  /** Show search input */
  searchable?: boolean;
}

/**
 * Token selector dropdown component
 */
export function TokenSelector({
  tokens,
  selectedToken,
  onSelect,
  openSignal,
  placeholder = 'Select token',
  disabled = false,
  className = '',
  searchable = true,
}: TokenSelectorProps) {
  const [isOpen, setIsOpen] = useState(false);
  const [search, setSearch] = useState('');
  const containerRef = useRef<HTMLDivElement>(null);
  const searchInputRef = useRef<HTMLInputElement>(null);

  // Filter tokens by search
  const filteredTokens = useMemo(() => {
    if (!search.trim()) return tokens;
    const searchLower = search.toLowerCase();
    return tokens.filter(
      (token) =>
        token.symbol.toLowerCase().includes(searchLower) ||
        token.name.toLowerCase().includes(searchLower) ||
        token.mint.toLowerCase().includes(searchLower)
    );
  }, [tokens, search]);

  // Close on outside click
  useEffect(() => {
    const handleClickOutside = (event: MouseEvent) => {
      if (containerRef.current && !containerRef.current.contains(event.target as Node)) {
        setIsOpen(false);
        setSearch('');
      }
    };

    if (isOpen) {
      document.addEventListener('mousedown', handleClickOutside);
      return () => document.removeEventListener('mousedown', handleClickOutside);
    }
  }, [isOpen]);

  // Focus search input when opening
  useEffect(() => {
    if (isOpen && searchable && searchInputRef.current) {
      searchInputRef.current.focus();
    }
  }, [isOpen, searchable]);

  useEffect(() => {
    if (openSignal === undefined || disabled) return;
    setIsOpen(true);
    setSearch('');
  }, [openSignal, disabled]);

  const handleToggle = useCallback(() => {
    if (!disabled) {
      setIsOpen((prev) => !prev);
      if (isOpen) {
        setSearch('');
      }
    }
  }, [disabled, isOpen]);

  const handleSelect = useCallback(
    (token: Token) => {
      onSelect(token);
      setIsOpen(false);
      setSearch('');
    },
    [onSelect]
  );

  const handleKeyDown = useCallback(
    (event: React.KeyboardEvent) => {
      if (event.key === 'Escape') {
        setIsOpen(false);
        setSearch('');
      } else if (event.key === 'Enter' && filteredTokens.length === 1) {
        handleSelect(filteredTokens[0]);
      }
    },
    [filteredTokens, handleSelect]
  );

  return (
    <div
      ref={containerRef}
      className={`cedros-token-selector ${isOpen ? 'cedros-token-selector-open' : ''} ${disabled ? 'cedros-token-selector-disabled' : ''} ${className}`}
      onKeyDown={handleKeyDown}
    >
      {/* Trigger button */}
      <button
        type="button"
        className="cedros-token-selector-trigger"
        onClick={handleToggle}
        disabled={disabled}
        aria-haspopup="listbox"
        aria-expanded={isOpen}
      >
        {selectedToken ? (
          <span className="cedros-token-selector-selected">
            {selectedToken.logoUrl && (
              <img
                src={selectedToken.logoUrl}
                alt={selectedToken.symbol}
                className="cedros-token-icon"
                onError={(e) => {
                  (e.target as HTMLImageElement).style.display = 'none';
                }}
              />
            )}
            <span className="cedros-token-symbol">{selectedToken.symbol}</span>
          </span>
        ) : (
          <span className="cedros-token-selector-placeholder">{placeholder}</span>
        )}
        <span className="cedros-token-selector-arrow">{isOpen ? '' : ''}</span>
      </button>

      {/* Dropdown */}
      {isOpen && (
        <div className="cedros-token-selector-dropdown" role="listbox">
          {/* Search input */}
          {searchable && (
            <div className="cedros-token-search">
              <input
                ref={searchInputRef}
                type="text"
                value={search}
                onChange={(e) => setSearch(e.target.value)}
                placeholder="Search tokens..."
                className="cedros-token-search-input"
              />
            </div>
          )}

          {/* Token list */}
          <div className="cedros-token-list">
            {filteredTokens.length === 0 ? (
              <div className="cedros-token-empty">No tokens found</div>
            ) : (
              <>
                {filteredTokens.map((token) => (
                  <button
                    key={token.mint}
                    type="button"
                    className={`cedros-token-option ${selectedToken?.mint === token.mint ? 'cedros-token-option-selected' : ''}`}
                    onClick={() => handleSelect(token)}
                    role="option"
                    aria-selected={selectedToken?.mint === token.mint}
                  >
                    {token.logoUrl && (
                      <img
                        src={token.logoUrl}
                        alt={token.symbol}
                        className="cedros-token-icon"
                        onError={(e) => {
                          (e.target as HTMLImageElement).style.display = 'none';
                        }}
                      />
                    )}
                    <span className="cedros-token-info">
                      <span className="cedros-token-symbol">{token.symbol}</span>
                      <span className="cedros-token-name">{token.name}</span>
                    </span>
                    {selectedToken?.mint === token.mint && (
                      <span className="cedros-token-check"></span>
                    )}
                  </button>
                ))}
              </>
            )}
          </div>
        </div>
      )}
    </div>
  );
}
