import { useCallback, useMemo, useState } from 'react';
import { useCedrosLogin } from '../context/useCedrosLogin';
import { ApiClient, handleApiError } from '../utils/apiClient';
import type { AuthError, AuthResponse } from '../types';
import {
  credentialToServerJson,
  parseCreationOptionsFromServer,
  parseRequestOptionsFromServer,
} from '../utils/webauthnJson';

type WebAuthnOptionsResponse = {
  challengeId: string;
  options: {
    publicKey: Record<string, unknown>;
  };
};

export interface UseWebAuthnReturn {
  isSupported: boolean;
  isLoading: boolean;
  error: AuthError | null;
  clearError: () => void;

  /** Start a server-managed WebAuthn authentication ceremony (login). */
  authenticatePasskey: (params?: { email?: string }) => Promise<AuthResponse>;
  /** Register a new passkey for the currently authenticated user. */
  registerPasskey: (params?: {
    label?: string;
  }) => Promise<{ credentialId: string; label?: string }>;
}

function isWebAuthnAvailable(): boolean {
  if (typeof window !== 'undefined') {
    const hostname = window.location?.hostname;
    const isLocalhost =
      hostname === 'localhost' || hostname === '127.0.0.1' || hostname?.endsWith('.localhost');
    const isSecure = (window as unknown as { isSecureContext?: boolean }).isSecureContext === true;
    if (!isSecure && !isLocalhost) {
      return false;
    }
  }
  return (
    typeof window !== 'undefined' &&
    typeof window.PublicKeyCredential !== 'undefined' &&
    typeof navigator !== 'undefined' &&
    typeof navigator.credentials !== 'undefined'
  );
}

function mapWebAuthnDomError(err: unknown): AuthError | null {
  if (!(err instanceof Error)) return null;
  // DOMExceptions inherit from Error in modern browsers.
  const name = err.name;

  // User cancelled, dismissed, timed out, or no available credential.
  if (name === 'NotAllowedError') {
    return { code: 'SERVER_ERROR', message: 'Passkey operation was cancelled or timed out' };
  }

  // Common for trying to create a credential that already exists (or unusable state).
  if (name === 'InvalidStateError') {
    return { code: 'VALIDATION_ERROR', message: 'Passkey is not available for this operation' };
  }

  // Thrown for non-secure contexts, or RP ID / origin mismatches.
  if (name === 'SecurityError') {
    return {
      code: 'VALIDATION_ERROR',
      message:
        'Passkeys require HTTPS and a correctly configured relying party (WEBAUTHN_RP_ID / WEBAUTHN_RP_ORIGIN)',
    };
  }

  return null;
}

/**
 * Server-managed WebAuthn (passkey) authentication + registration.
 *
 * - Login: /webauthn/auth/options -> navigator.credentials.get -> /webauthn/auth/verify
 * - Registration: /webauthn/register/options -> navigator.credentials.create -> /webauthn/register/verify
 */
export function useWebAuthn(): UseWebAuthnReturn {
  const { config, _internal } = useCedrosLogin();
  const [isLoading, setIsLoading] = useState(false);
  const [error, setError] = useState<AuthError | null>(null);

  const apiClient = useMemo(
    () =>
      new ApiClient({
        baseUrl: config.serverUrl,
        timeoutMs: config.requestTimeout,
        retryAttempts: config.retryAttempts,
        // Needed for authenticated registration endpoints when cookie auth is disabled
        getAccessToken: _internal?.getAccessToken,
      }),
    [_internal?.getAccessToken, config.serverUrl, config.requestTimeout, config.retryAttempts]
  );

  const clearError = useCallback(() => setError(null), []);

  const isSupported = isWebAuthnAvailable();

  const authenticatePasskey = useCallback(
    async (params?: { email?: string }): Promise<AuthResponse> => {
      if (!isSupported) {
        const err: AuthError = {
          code: 'VALIDATION_ERROR',
          message: 'Passkeys are not supported in this browser',
        };
        setError(err);
        throw err;
      }

      setIsLoading(true);
      setError(null);

      try {
        const optionsResponse = await apiClient.post<WebAuthnOptionsResponse>(
          '/webauthn/auth/options',
          { email: params?.email }
        );

        const publicKey = parseRequestOptionsFromServer(optionsResponse.options);

        const assertion = (await navigator.credentials.get({
          publicKey,
        })) as PublicKeyCredential | null;

        if (!assertion) {
          throw new Error('Passkey authentication returned no credential');
        }

        const data = await apiClient.post<AuthResponse>('/webauthn/auth/verify', {
          challengeId: optionsResponse.challengeId,
          credential: credentialToServerJson(assertion),
        });

        config.callbacks?.onLoginSuccess?.(data.user, 'webauthn');
        _internal?.handleLoginSuccess(data.user, data.tokens);

        return data;
      } catch (err) {
        const domMapped = mapWebAuthnDomError(err);
        const authError = domMapped ?? handleApiError(err, 'Passkey sign-in failed');
        setError(authError);
        throw authError;
      } finally {
        setIsLoading(false);
      }
    },
    [apiClient, config.callbacks, _internal, isSupported]
  );

  const registerPasskey = useCallback(
    async (params?: { label?: string }): Promise<{ credentialId: string; label?: string }> => {
      if (!isSupported) {
        const err: AuthError = {
          code: 'VALIDATION_ERROR',
          message: 'Passkeys are not supported in this browser',
        };
        setError(err);
        throw err;
      }

      setIsLoading(true);
      setError(null);

      try {
        const optionsResponse = await apiClient.post<WebAuthnOptionsResponse>(
          '/webauthn/register/options',
          {}
        );

        const publicKey = parseCreationOptionsFromServer(optionsResponse.options);

        const created = (await navigator.credentials.create({
          publicKey,
        })) as PublicKeyCredential | null;

        if (!created) {
          throw new Error('Passkey registration returned no credential');
        }

        const result = await apiClient.post<{
          success: boolean;
          credentialId: string;
          label?: string;
        }>('/webauthn/register/verify', {
          challengeId: optionsResponse.challengeId,
          credential: credentialToServerJson(created),
          label: params?.label,
        });

        if (!result.success) {
          throw new Error('Passkey registration failed');
        }

        return { credentialId: result.credentialId, label: result.label };
      } catch (err) {
        const domMapped = mapWebAuthnDomError(err);
        const authError = domMapped ?? handleApiError(err, 'Passkey registration failed');
        setError(authError);
        throw authError;
      } finally {
        setIsLoading(false);
      }
    },
    [apiClient, isSupported]
  );

  return {
    isSupported,
    isLoading,
    error,
    clearError,
    authenticatePasskey,
    registerPasskey,
  };
}
