/**
 * Hook to fetch wallet discovery config from server
 *
 * Fetches wallet configuration from the discovery endpoint to determine
 * recovery mode and other wallet settings.
 */

import { useState, useEffect, useCallback, useMemo } from 'react';
import { useCedrosLoginOptional } from '../context/useCedrosLogin';
import { ApiClient } from '../utils/apiClient';
import type { WalletDiscoveryConfig, WalletRecoveryMode } from '../types/wallet';

/** Discovery endpoint response */
interface DiscoveryResponse {
  wallet?: {
    enabled: boolean;
    recoveryMode: string;
    unlockTtlSeconds: number;
  };
}

const VALID_RECOVERY_MODES: WalletRecoveryMode[] = ['share_c_only', 'full_seed', 'none'];

function normalizeRecoveryMode(mode: string | undefined): WalletRecoveryMode {
  if (!mode) return 'share_c_only';
  return VALID_RECOVERY_MODES.includes(mode as WalletRecoveryMode)
    ? (mode as WalletRecoveryMode)
    : 'share_c_only';
}

/** Return type for useWalletDiscovery hook */
export interface UseWalletDiscoveryReturn {
  /** Whether wallet feature is enabled on server */
  walletEnabled: boolean;
  /** Recovery mode: share_c_only or full_seed */
  recoveryMode: WalletRecoveryMode;
  /** Session unlock TTL in seconds */
  unlockTtlSeconds: number;
  /** Whether config is still loading */
  isLoading: boolean;
  /** Error message if fetch failed */
  error: string | null;
  /** Refetch the config */
  refetch: () => Promise<void>;
}

/** Default return value when outside provider context */
const DEFAULT_RETURN: UseWalletDiscoveryReturn = {
  walletEnabled: false,
  recoveryMode: 'share_c_only',
  unlockTtlSeconds: 900,
  isLoading: false,
  error: null,
  refetch: async () => {},
};

/**
 * Hook to fetch wallet discovery configuration from server
 *
 * Automatically fetches on mount and caches the result.
 * Default values are used if fetch fails or wallet is not enabled.
 * Returns defaults when used outside CedrosLoginProvider.
 */
export function useWalletDiscovery(): UseWalletDiscoveryReturn {
  const context = useCedrosLoginOptional();

  const [walletConfig, setWalletConfig] = useState<WalletDiscoveryConfig | null>(null);
  const [isLoading, setIsLoading] = useState(!!context);
  const [error, setError] = useState<string | null>(null);

  const apiClient = useMemo(() => {
    if (!context) return null;
    return new ApiClient({
      baseUrl: context.config.serverUrl,
      timeoutMs: context.config.requestTimeout,
      retryAttempts: context.config.retryAttempts,
    });
  }, [context]);

  const fetchConfig = useCallback(async () => {
    if (!apiClient) return;

    setIsLoading(true);
    setError(null);

    try {
      const response = await apiClient.get<DiscoveryResponse>('/discovery');

      if (response.wallet) {
        setWalletConfig({
          enabled: response.wallet.enabled,
          recoveryMode: normalizeRecoveryMode(response.wallet.recoveryMode),
          unlockTtlSeconds: response.wallet.unlockTtlSeconds,
        });
      } else {
        // Wallet not configured on server
        setWalletConfig({
          enabled: false,
          recoveryMode: 'share_c_only',
          unlockTtlSeconds: 900,
        });
      }
    } catch (err) {
      const message = err instanceof Error ? err.message : 'Failed to fetch wallet config';
      setError(message);
      // Use defaults on error
      setWalletConfig({
        enabled: false,
        recoveryMode: 'share_c_only',
        unlockTtlSeconds: 900,
      });
    } finally {
      setIsLoading(false);
    }
  }, [apiClient]);

  useEffect(() => {
    if (apiClient) {
      fetchConfig();
    }
  }, [apiClient, fetchConfig]);

  // Return defaults when outside provider
  if (!context) {
    return DEFAULT_RETURN;
  }

  return {
    walletEnabled: walletConfig?.enabled ?? false,
    recoveryMode: walletConfig?.recoveryMode ?? 'share_c_only',
    unlockTtlSeconds: walletConfig?.unlockTtlSeconds ?? 900,
    isLoading,
    error,
    refetch: fetchConfig,
  };
}
