interface SectionConfig {
  id: DashboardSection;
  label: string;
  icon: React.ReactNode;
  /** Optional group name for sidebar organization */
  group?: string;
  /** Feature flag required for this section (from server settings) */
  requiredFeature?: 'organizations' | 'credits';
}

const ALL_SECTIONS: SectionConfig[] = [
  // Top-level menu items
  { id: 'users', label: 'Users', icon: Icons.users },
  { id: 'team', label: 'Team', icon: Icons.members },
  { id: 'deposits', label: 'Deposits', icon: Icons.deposits, requiredFeature: 'credits' },
  { id: 'withdrawals', label: 'Withdrawals', icon: Icons.withdrawals, requiredFeature: 'credits' },
  // Configuration group
  { id: 'settings-auth', label: 'Authentication', icon: Icons.key, group: 'Configuration' },
  { id: 'settings-messaging', label: 'Auth Messages', icon: Icons.mail, group: 'Configuration' },
  { id: 'settings-wallet', label: 'User Wallets', icon: Icons.wallet, group: 'Configuration' },
  {
    id: 'settings-credits',
    label: 'Credit System',
    icon: Icons.coins,
    group: 'Configuration',
    requiredFeature: 'credits',
  },
  { id: 'settings-server', label: 'Auth Server', icon: Icons.server, group: 'Configuration' },
];

/**
 * Unified Admin Dashboard
 *
 * Provides a complete admin interface with sidebar navigation.
 * Follows shadcn/ui dashboard patterns.
 */
export function CedrosAdminDashboard({
  title = 'Dashboard',
  sections = [
    'users',
    'team',
    'deposits',
    'withdrawals',
    'settings-wallet',
    'settings-auth',
    'settings-messaging',
    'settings-credits',
    'settings-server',
  ],
  defaultSection = 'users',
  refreshInterval = 0,
  pageSize = 20,
  onSectionChange,
  onSettingsClick,
  onLogoutClick,
  className = '',
}: CedrosAdminDashboardProps) {
  const [activeSection, setActiveSection] = useState<DashboardSection>(defaultSection);
  const [configExpanded, setConfigExpanded] = useState(true);

  const { user, logout } = useCedrosLogin();
  const { activeOrg, role, isLoading: orgsLoading, fetchOrgs, hasPermission } = useOrgs();
  const { status, isLoading: setupLoading, checkStatus } = useSetup();
  const { features, isLoading: featuresLoading } = useServerFeatures();
  const { canAccess } = useDashboardPermissions();

  const handleSectionChange = useCallback(
    (section: DashboardSection) => {
      setActiveSection(section);
      onSectionChange?.(section);
    },
    [onSectionChange]
  );

  // Filter sections by: 1) explicitly enabled in props, 2) feature flag enabled, 3) role permissions
  const enabledSections = ALL_SECTIONS.filter((s) => {
    // Must be in the sections prop
    if (!sections.includes(s.id)) return false;
    // If section requires a feature, check if feature is enabled
    if (s.requiredFeature && !features[s.requiredFeature]) return false;
    // Check role-based dashboard permissions (owner always has access via canAccess)
    if (!canAccess(s.id)) return false;
    return true;
  });

  const currentSection = enabledSections.find((s) => s.id === activeSection);
  const activeSectionDisabled = !currentSection && !featuresLoading;

  useEffect(() => {
    fetchOrgs();
    checkStatus();
  }, [fetchOrgs, checkStatus]);

  // If active section is disabled (feature flag off), redirect to users
  useEffect(() => {
    if (activeSectionDisabled && enabledSections.length > 0) {
      setActiveSection('users');
    }
  }, [activeSectionDisabled, enabledSections.length]);

  // Show setup wizard if no admin exists
  if (!setupLoading && status?.needsSetup) {
    return (
      <div className={`cedros-admin cedros-dashboard cedros-dashboard--setup ${className}`}>
        <SetupWizard onComplete={() => checkStatus()} />
      </div>
    );
  }

  if ((orgsLoading || setupLoading || featuresLoading) && !activeOrg) {
    return (
      <div className={`cedros-admin cedros-dashboard cedros-dashboard--loading ${className}`}>
        <LoadingSpinner />
        <span className="cedros-dashboard__loading-text">Loading...</span>
      </div>
    );
  }

  const needsOrg = activeSection === 'team';
  if (needsOrg && !activeOrg) {
    return (
      <div className={`cedros-admin cedros-dashboard ${className}`}>
        <ErrorMessage error="No organization selected. Please select an organization first." />
      </div>
    );
  }

  return (
    <div className={`cedros-admin cedros-dashboard ${className}`}>
      {/* Sidebar */}
      <aside className="cedros-dashboard__sidebar">
        <div className="cedros-dashboard__sidebar-header">
          <div className="cedros-dashboard__logo">
            {Icons.wallet}
            <span className="cedros-dashboard__logo-text">{title}</span>
          </div>
        </div>

        <nav className="cedros-dashboard__nav">
          {/* Top-level menu items (no group) */}
          <div className="cedros-dashboard__nav-group">
            <span className="cedros-dashboard__nav-label">Menu</span>
            {enabledSections
              .filter((s) => !s.group)
              .map((section) => (
                <button
                  key={section.id}
                  type="button"
                  className={`cedros-dashboard__nav-item ${activeSection === section.id ? 'cedros-dashboard__nav-item--active' : ''}`}
                  onClick={() => handleSectionChange(section.id)}
                  aria-current={activeSection === section.id ? 'page' : undefined}
                >
                  <span className="cedros-dashboard__nav-icon">{section.icon}</span>
                  <span className="cedros-dashboard__nav-text">{section.label}</span>
                </button>
              ))}
          </div>

          {/* Configuration group (collapsible) */}
          {enabledSections.some((s) => s.group === 'Configuration') && (
            <div className="cedros-dashboard__nav-group">
              <button
                type="button"
                className="cedros-dashboard__nav-label cedros-dashboard__nav-label--collapsible"
                onClick={() => setConfigExpanded(!configExpanded)}
                aria-expanded={configExpanded}
              >
                <span>Configuration</span>
                <span
                  className={`cedros-dashboard__nav-chevron ${configExpanded ? 'cedros-dashboard__nav-chevron--expanded' : ''}`}
                >
                  {Icons.chevronRight}
                </span>
              </button>
              {configExpanded &&
                enabledSections
                  .filter((s) => s.group === 'Configuration')
                  .map((section) => (
                    <button
                      key={section.id}
                      type="button"
                      className={`cedros-dashboard__nav-item ${activeSection === section.id ? 'cedros-dashboard__nav-item--active' : ''}`}
                      onClick={() => handleSectionChange(section.id)}
                      aria-current={activeSection === section.id ? 'page' : undefined}
                    >
                      <span className="cedros-dashboard__nav-icon">{section.icon}</span>
                      <span className="cedros-dashboard__nav-text">{section.label}</span>
                    </button>
                  ))}
            </div>
          )}
        </nav>

        {/* User profile dropdown at bottom */}
        {user && (
          <div className="cedros-dashboard__sidebar-footer">
            <ProfileDropdown
              name={user.name}
              email={user.email}
              picture={user.picture}
              onSettings={onSettingsClick}
              onLogout={onLogoutClick ?? logout}
            />
          </div>
        )}
      </aside>

      {/* Main Content */}
      <main className="cedros-dashboard__main">
        <header className="cedros-dashboard__header">
          <div className="cedros-dashboard__breadcrumb">
            <span className="cedros-dashboard__breadcrumb-root">{title}</span>
            <span className="cedros-dashboard__breadcrumb-sep">{Icons.chevronRight}</span>
            <span className="cedros-dashboard__breadcrumb-current">{currentSection?.label}</span>
          </div>
        </header>

        <div className="cedros-dashboard__content">
          {activeSection === 'users' && (
            <UsersSection pageSize={pageSize} currentUserId={user?.id} />
          )}
          {activeSection === 'team' && activeOrg && (
            <TeamSection
              orgId={activeOrg.id}
              currentUserId={user?.id}
              hasPermission={hasPermission}
              role={role}
            />
          )}
          {activeSection === 'deposits' && (
            <DepositsSection pageSize={pageSize} refreshInterval={refreshInterval} />
          )}
          {activeSection === 'withdrawals' && (
            <WithdrawalsSection pageSize={pageSize} refreshInterval={refreshInterval} />
          )}
          {activeSection === 'settings-auth' && (
            <div className="cedros-dashboard__section">
              <AuthenticationSettings />
            </div>
          )}
          {activeSection === 'settings-wallet' && (
            <div className="cedros-dashboard__section">
              <EmbeddedWalletSettings />
            </div>
          )}
          {activeSection === 'settings-messaging' && (
            <div className="cedros-dashboard__section">
              <MessagingSettings />
            </div>
          )}
          {activeSection === 'settings-credits' && (
            <div className="cedros-dashboard__section">
              <CreditSystemSettings />
            </div>
          )}
          {activeSection === 'settings-server' && (
            <div className="cedros-dashboard__section">
              <ServerSettings />
            </div>
          )}
        </div>
      </main>
    </div>
  );
}

// =============================================================================
// Section Components
// =============================================================================

interface UsersSectionProps {
  pageSize: number;
  currentUserId?: string;
}

function UsersSection({ pageSize, currentUserId }: UsersSectionProps) {
  const [selectedUser, setSelectedUser] = useState<AdminUser | null>(null);
  const { statsItems, isLoading: statsLoading, error: statsError, refresh } = useUsersStatsSummary();

  if (selectedUser) {
    return (
      <div className="cedros-dashboard__section">
        <AdminUserDetail
          userId={selectedUser.id}
          currentUserId={currentUserId}
          onBack={() => setSelectedUser(null)}
        />
      </div>
    );
  }

  return (
    <div className="cedros-dashboard__section">
      <StatsBar stats={statsItems} isLoading={statsLoading} onRefresh={refresh} />
      {statsError && <p className="cedros-admin-error-inline">{statsError}</p>}

      <AdminUserList
        pageSize={pageSize}
        currentUserId={currentUserId}
        onUserClick={(user) => setSelectedUser(user)}
      />
    </div>
  );
}

type TeamTab = 'members' | 'invites' | 'permissions';

interface TeamSectionProps {
  orgId: string;
  currentUserId?: string;
  hasPermission: (permission: Permission) => boolean;
  role: string | null;
}

function TeamSection({ orgId, currentUserId, hasPermission, role }: TeamSectionProps) {
  const [activeTab, setActiveTab] = useState<TeamTab>('members');

  const {
    members,
    isLoading: membersLoading,
    error: membersError,
    fetchMembers,
    updateMemberRole,
    removeMember,
  } = useMembers(orgId);
  const {
    invites,
    isLoading: invitesLoading,
    error: invitesError,
    fetchInvites,
    createInvite,
    cancelInvite,
    resendInvite,
  } = useInvites(orgId);

  useEffect(() => {
    fetchMembers();
    fetchInvites();
  }, [fetchMembers, fetchInvites]);

  const canInvite = hasPermission('invite:create');
  const canCancel = hasPermission('invite:cancel');
  const pendingCount = invites.length;
  const roleCounts = members.reduce(
    (acc, member) => {
      acc[member.role] = (acc[member.role] ?? 0) + 1;
      return acc;
    },
    {} as Record<string, number>
  );
  const ownerCount = roleCounts.owner ?? 0;
  const adminCount = roleCounts.admin ?? 0;
  const memberCount = roleCounts.member ?? 0;

  return (
    <div className="cedros-dashboard__team">
      <StatsBar
        stats={[
          { label: 'Owners', value: ownerCount },
          { label: 'Admins', value: adminCount },
          { label: 'Members', value: memberCount },
          { label: 'Pending Invites', value: pendingCount },
        ]}
      />
      {/* Tabs */}
      <div className="cedros-admin-tabs cedros-admin-tabs--line">
        <button
          type="button"
          className={`cedros-admin-tab ${activeTab === 'members' ? 'cedros-admin-tab-active' : ''}`}
          onClick={() => setActiveTab('members')}
          aria-selected={activeTab === 'members'}
          role="tab"
        >
          Members
        </button>
        <button
          type="button"
          className={`cedros-admin-tab ${activeTab === 'invites' ? 'cedros-admin-tab-active' : ''}`}
          onClick={() => setActiveTab('invites')}
          aria-selected={activeTab === 'invites'}
          role="tab"
        >
          Pending Invites{pendingCount > 0 && ` (${pendingCount})`}
        </button>
        {role === 'owner' && (
          <button
            type="button"
            className={`cedros-admin-tab ${activeTab === 'permissions' ? 'cedros-admin-tab-active' : ''}`}
            onClick={() => setActiveTab('permissions')}
            aria-selected={activeTab === 'permissions'}
            role="tab"
          >
            Permissions
          </button>
        )}
      </div>

      {/* Tab content */}
      <div className="cedros-admin-tab-content" role="tabpanel">
        {activeTab === 'members' && (
          <MemberList
            members={members}
            currentUserId={currentUserId}
            isLoading={membersLoading}
            error={membersError?.message}
            canManage={hasPermission('member:remove')}
            canChangeRoles={hasPermission('member:role_change')}
            onUpdateRole={updateMemberRole}
            onRemove={removeMember}
          />
        )}

        {activeTab === 'invites' && (
          <div className="cedros-dashboard__invites">
            {canInvite && (
              <div className="cedros-dashboard__section">
                <div className="cedros-dashboard__section-header">
                  <h3 className="cedros-dashboard__section-title">Send Invitation</h3>
                </div>
                <InviteForm
                  onSubmit={createInvite}
                  isLoading={invitesLoading}
                  error={invitesError?.message}
                />
              </div>
            )}

            <div className="cedros-dashboard__section">
              <InviteList
                invites={invites}
                isLoading={invitesLoading}
                error={invitesError?.message}
                canManage={canCancel || canInvite}
                onCancel={canCancel ? cancelInvite : undefined}
                onResend={canInvite ? resendInvite : undefined}
              />
            </div>
          </div>
        )}

        {activeTab === 'permissions' && role === 'owner' && <PermissionsSection userRole={role} />}
      </div>
    </div>
  );
}

interface DepositsSectionProps {
  pageSize: number;
  refreshInterval: number;
}

function DepositsSection({ pageSize, refreshInterval }: DepositsSectionProps) {
  const [statusFilter, setStatusFilter] = useState<string>('');

  return (
    <div className="cedros-dashboard__deposits">
      {/* Stats summary */}
      <AdminDepositStats refreshInterval={refreshInterval} />

      {/* Filter and list */}
      <div className="cedros-dashboard__deposits-list">
        <div className="cedros-dashboard__toolbar">
          <div className="cedros-dashboard__filter">
            <label className="cedros-dashboard__filter-label" htmlFor="status-filter">
              Status
            </label>
            <select
              id="status-filter"
              className="cedros-dashboard__select"
              value={statusFilter}
              onChange={(e) => setStatusFilter(e.target.value)}
            >
              <option value="">All statuses</option>
              <option value="pending">Pending</option>
              <option value="detected">Detected</option>
              <option value="processing">Processing</option>
              <option value="completed">Completed</option>
              <option value="withdrawn">Withdrawn</option>
              <option value="expired">Expired</option>
              <option value="failed">Failed</option>
            </select>
          </div>
        </div>

        <AdminDepositList
          statusFilter={statusFilter || undefined}
          pageSize={pageSize}
          refreshInterval={refreshInterval}
        />
      </div>
    </div>
  );
}

interface WithdrawalsSectionProps {
  pageSize: number;
  refreshInterval: number;
}

function WithdrawalsSection({ pageSize, refreshInterval }: WithdrawalsSectionProps) {
  return (
    <div className="cedros-dashboard__withdrawals">
      <AdminWithdrawalStats refreshInterval={refreshInterval} />
      <p className="cedros-dashboard__text-muted">
        Track deposits through the withdrawal pipeline: privacy period, ready for processing, and
        completed.
      </p>

      {/* Pipeline view: Privacy Period  Ready  Completed */}
      <div className="cedros-dashboard__pipeline">
        <AdminPrivacyPeriodDeposits pageSize={pageSize} refreshInterval={refreshInterval} />
        <AdminWithdrawalQueue pageSize={pageSize} refreshInterval={refreshInterval} />
        <AdminWithdrawalHistory pageSize={pageSize} refreshInterval={refreshInterval} />
      </div>
    </div>
  );
}
