import { useState, type FormEvent } from 'react';
import { usePasswordReset } from '../../hooks/usePasswordReset';
import { PasswordInput } from './PasswordInput';
import { ErrorMessage } from '../shared/ErrorMessage';
import { LoadingSpinner } from '../shared/LoadingSpinner';
import { validatePassword } from '../../utils/validation';
import type { PasswordValidation } from '../../types';

export interface ResetPasswordFormProps {
  token: string;
  onSuccess?: () => void;
  onLoginClick?: () => void;
  className?: string;
}

/**
 * Form for resetting password using a reset token.
 *
 * @example
 * ```tsx
 * // Get token from URL params
 * const token = new URLSearchParams(location.search).get('token');
 *
 * <ResetPasswordForm
 *   token={token}
 *   onSuccess={() => navigate('/login')}
 *   onLoginClick={() => navigate('/login')}
 * />
 * ```
 */
export function ResetPasswordForm({
  token,
  onSuccess,
  onLoginClick,
  className = '',
}: ResetPasswordFormProps) {
  const [password, setPassword] = useState('');
  const [confirmPassword, setConfirmPassword] = useState('');
  const [validation, setValidation] = useState<PasswordValidation | null>(null);
  const { resetPassword, isLoading, isSuccess, error, clearError } = usePasswordReset();

  const passwordsMatch = password === confirmPassword;
  const isValid = validation?.isValid && passwordsMatch && password.length > 0;

  const handleSubmit = async (e: FormEvent) => {
    e.preventDefault();
    if (!isValid) return;

    try {
      await resetPassword(token, password);
      onSuccess?.();
    } catch {
      // Error is already set in the hook
    }
  };

  if (isSuccess) {
    return (
      <div className={`cedros-reset-password-success ${className}`}>
        <svg
          className="cedros-success-icon"
          width="48"
          height="48"
          viewBox="0 0 48 48"
          fill="none"
          aria-hidden="true"
        >
          <circle cx="24" cy="24" r="22" stroke="currentColor" strokeWidth="2" />
          <path
            d="M14 24l7 7 13-13"
            stroke="currentColor"
            strokeWidth="2"
            strokeLinecap="round"
            strokeLinejoin="round"
          />
        </svg>
        <h3 className="cedros-success-title">Password reset successful</h3>
        <p className="cedros-success-message">
          Your password has been reset. You can now log in with your new password.
        </p>
        {onLoginClick && (
          <button
            type="button"
            className="cedros-button cedros-button-md cedros-button-primary"
            onClick={onLoginClick}
          >
            Go to login
          </button>
        )}
      </div>
    );
  }

  return (
    <form className={`cedros-reset-password-form ${className}`} onSubmit={handleSubmit}>
      <div className="cedros-form-header">
        <h3 className="cedros-form-title">Reset your password</h3>
        <p className="cedros-form-subtitle">Enter your new password below.</p>
      </div>

      <ErrorMessage error={error} onDismiss={clearError} />

      <div className="cedros-form-field">
        <PasswordInput
          label="New password"
          value={password}
          onChange={(e) => {
            setPassword(e.target.value);
            setValidation(validatePassword(e.target.value));
          }}
          showStrengthMeter
          onValidationChange={setValidation}
          disabled={isLoading}
          autoComplete="new-password"
          error={
            validation && !validation.isValid
              ? Object.values(validation.errors).find(Boolean)
              : undefined
          }
        />
      </div>

      <div className="cedros-form-field">
        <PasswordInput
          label="Confirm password"
          value={confirmPassword}
          onChange={(e) => setConfirmPassword(e.target.value)}
          disabled={isLoading}
          autoComplete="new-password"
          error={confirmPassword && !passwordsMatch ? 'Passwords do not match' : undefined}
        />
      </div>

      <div className="cedros-form-actions">
        <button
          type="submit"
          className="cedros-button cedros-button-md cedros-button-primary cedros-button-full"
          disabled={isLoading || !isValid}
        >
          {isLoading ? (
            <>
              <LoadingSpinner size="sm" />
              Resetting...
            </>
          ) : (
            'Reset password'
          )}
        </button>

        {onLoginClick && (
          <button
            type="button"
            className="cedros-button cedros-button-md cedros-button-ghost cedros-button-full"
            onClick={onLoginClick}
            disabled={isLoading}
          >
            Back to login
          </button>
        )}
      </div>
    </form>
  );
}
