/**
 * Hook for credit balance and transaction history
 *
 * Provides access to the user's credit balance and transaction history.
 * Credits are earned through Privacy Cash deposits.
 *
 * Safe to call outside CedrosLoginProvider (returns no-op functions).
 */

import { useState, useCallback, useMemo } from 'react';
import { useCedrosLoginOptional } from '../context/useCedrosLogin';
import { ApiClient, handleApiError } from '../utils/apiClient';
import type {
  UseCreditsReturn,
  CreditBalanceResponse,
  CreditHistoryResponse,
  BalancesResponse,
} from '../types/deposit';

export type { UseCreditsReturn } from '../types/deposit';

/**
 * Hook for credit balance and transaction history
 *
 * Credits represent the user's balance from Privacy Cash deposits.
 * The balance can be used for services within the application.
 *
 * Safe to call outside CedrosLoginProvider - returns no-op functions that throw.
 */
export function useCredits(): UseCreditsReturn {
  const context = useCedrosLoginOptional();
  const [isLoading, setIsLoading] = useState(false);
  const [error, setError] = useState<string | null>(null);

  const apiClient = useMemo(() => {
    if (!context) return null;
    return new ApiClient({
      baseUrl: context.config.serverUrl,
      timeoutMs: context.config.requestTimeout,
      retryAttempts: context.config.retryAttempts,
      getAccessToken: context._internal?.getAccessToken,
    });
  }, [context]);

  const clearError = useCallback(() => {
    setError(null);
  }, []);

  const getBalance = useCallback(async (): Promise<CreditBalanceResponse> => {
    if (!apiClient) {
      throw new Error('useCredits must be used within a CedrosLoginProvider');
    }
    setIsLoading(true);
    setError(null);

    try {
      const data = await apiClient.get<CreditBalanceResponse>('/credits/balance/sol');
      return data;
    } catch (err) {
      const apiError = handleApiError(err, 'Failed to fetch credit balance');
      setError(apiError.message);
      throw apiError;
    } finally {
      setIsLoading(false);
    }
  }, [apiClient]);

  const getAllBalances = useCallback(async (): Promise<CreditBalanceResponse[]> => {
    if (!apiClient) {
      throw new Error('useCredits must be used within a CedrosLoginProvider');
    }
    setIsLoading(true);
    setError(null);

    try {
      const data = await apiClient.get<BalancesResponse>('/credits/balance');
      return data.balances;
    } catch (err) {
      const apiError = handleApiError(err, 'Failed to fetch credit balances');
      setError(apiError.message);
      throw apiError;
    } finally {
      setIsLoading(false);
    }
  }, [apiClient]);

  const getHistory = useCallback(
    async (options?: {
      currency?: string;
      limit?: number;
      offset?: number;
    }): Promise<CreditHistoryResponse> => {
      if (!apiClient) {
        throw new Error('useCredits must be used within a CedrosLoginProvider');
      }
      setIsLoading(true);
      setError(null);

      try {
        const params = new URLSearchParams();
        if (options?.currency) params.set('currency', options.currency);
        if (options?.limit) params.set('limit', options.limit.toString());
        if (options?.offset) params.set('offset', options.offset.toString());

        const queryString = params.toString();
        const url = queryString ? `/credits/history?${queryString}` : '/credits/history';

        const data = await apiClient.get<CreditHistoryResponse>(url);
        return data;
      } catch (err) {
        const apiError = handleApiError(err, 'Failed to fetch transaction history');
        setError(apiError.message);
        throw apiError;
      } finally {
        setIsLoading(false);
      }
    },
    [apiClient]
  );

  return {
    getBalance,
    getAllBalances,
    getHistory,
    isLoading,
    error,
    clearError,
  };
}
