import { useCallback, useState, useRef, useEffect } from 'react';
import type { OrgWithMembership } from '../../types';
import { LoadingSpinner } from '../shared/LoadingSpinner';
import { OrgAvatar } from './OrgAvatar';

export interface OrgSelectorProps {
  /** List of organizations to display */
  orgs: OrgWithMembership[];
  /** Currently active organization */
  activeOrg: OrgWithMembership | null;
  /** Loading state */
  isLoading?: boolean;
  /** Callback when organization is selected */
  onSelect: (orgId: string) => void;
  /** Callback when "Create organization" is clicked */
  onCreateClick?: () => void;
  /** Additional CSS class */
  className?: string;
  /** Placeholder when no orgs */
  placeholder?: string;
}

/**
 * Dropdown selector for switching between organizations.
 *
 * ## A11Y-02: Keyboard Navigation Limitation
 *
 * Currently supports:
 * - Tab/Shift+Tab: Move focus between trigger and items
 * - Escape: Close dropdown
 * - Enter/Space: Select focused item
 *
 * FUTURE: Consider implementing arrow key navigation for enhanced accessibility:
 * - ArrowUp/ArrowDown: Navigate between items in the list
 * - Home/End: Jump to first/last item
 * - Type-ahead: Focus item starting with typed character
 *
 * See WAI-ARIA Listbox Pattern for reference implementation.
 * Currently the component meets basic accessibility requirements.
 *
 * @example
 * ```tsx
 * function Header() {
 *   const { orgs, activeOrg, switchOrg } = useOrgs();
 *
 *   return (
 *     <OrgSelector
 *       orgs={orgs}
 *       activeOrg={activeOrg}
 *       onSelect={switchOrg}
 *       onCreateClick={() => setShowCreateModal(true)}
 *     />
 *   );
 * }
 * ```
 */
export function OrgSelector({
  orgs,
  activeOrg,
  isLoading = false,
  onSelect,
  onCreateClick,
  className = '',
  placeholder = 'Select organization',
}: OrgSelectorProps) {
  const [isOpen, setIsOpen] = useState(false);
  const dropdownRef = useRef<HTMLDivElement>(null);

  // Close dropdown when clicking outside
  useEffect(() => {
    const handleClickOutside = (event: MouseEvent) => {
      if (dropdownRef.current && !dropdownRef.current.contains(event.target as Node)) {
        setIsOpen(false);
      }
    };

    document.addEventListener('mousedown', handleClickOutside);
    return () => document.removeEventListener('mousedown', handleClickOutside);
  }, []);

  // Close on escape key
  useEffect(() => {
    const handleEscape = (event: KeyboardEvent) => {
      if (event.key === 'Escape') {
        setIsOpen(false);
      }
    };

    if (isOpen) {
      document.addEventListener('keydown', handleEscape);
      return () => document.removeEventListener('keydown', handleEscape);
    }
  }, [isOpen]);

  const handleSelect = useCallback(
    (orgId: string) => {
      onSelect(orgId);
      setIsOpen(false);
    },
    [onSelect]
  );

  const handleCreateClick = useCallback(() => {
    setIsOpen(false);
    onCreateClick?.();
  }, [onCreateClick]);

  const toggleDropdown = useCallback(() => {
    setIsOpen((prev) => !prev);
  }, []);

  if (isLoading) {
    return (
      <div
        className={`cedros-org-selector cedros-org-selector-loading ${className}`}
        aria-busy="true"
        aria-live="polite"
      >
        <LoadingSpinner size="sm" />
        <span>Loading...</span>
      </div>
    );
  }

  return (
    <div ref={dropdownRef} className={`cedros-org-selector ${className}`}>
      <button
        type="button"
        className="cedros-org-selector-trigger"
        onClick={toggleDropdown}
        aria-haspopup="listbox"
        aria-expanded={isOpen}
      >
        {activeOrg ? (
          <>
            <OrgAvatar org={activeOrg} size="sm" />
            <span className="cedros-org-selector-name">{activeOrg.name}</span>
            <RoleBadge role={activeOrg.membership.role} />
          </>
        ) : (
          <span className="cedros-org-selector-placeholder">{placeholder}</span>
        )}
        <ChevronIcon isOpen={isOpen} />
      </button>

      {isOpen && (
        <div className="cedros-org-selector-dropdown" role="listbox">
          {orgs.length === 0 ? (
            <div className="cedros-org-selector-empty">No organizations</div>
          ) : (
            <ul className="cedros-org-selector-list">
              {orgs.map((org) => (
                <li key={org.id}>
                  <button
                    type="button"
                    className={`cedros-org-selector-item ${
                      org.id === activeOrg?.id ? 'cedros-org-selector-item-active' : ''
                    }`}
                    onClick={() => handleSelect(org.id)}
                    role="option"
                    aria-selected={org.id === activeOrg?.id}
                  >
                    <OrgAvatar org={org} size="sm" />
                    <span className="cedros-org-selector-item-name">{org.name}</span>
                    <RoleBadge role={org.membership.role} />
                    {org.id === activeOrg?.id && <CheckIcon />}
                  </button>
                </li>
              ))}
            </ul>
          )}

          {onCreateClick && (
            <>
              <div className="cedros-org-selector-divider" />
              <button
                type="button"
                className="cedros-org-selector-create"
                onClick={handleCreateClick}
              >
                <PlusIcon />
                <span>Create organization</span>
              </button>
            </>
          )}
        </div>
      )}
    </div>
  );
}

// Helper components
function RoleBadge({ role }: { role: string }) {
  return <span className={`cedros-org-role cedros-org-role-${role}`}>{role}</span>;
}

function ChevronIcon({ isOpen }: { isOpen: boolean }) {
  return (
    <svg
      className={`cedros-org-chevron ${isOpen ? 'cedros-org-chevron-open' : ''}`}
      width="16"
      height="16"
      viewBox="0 0 16 16"
      fill="none"
      xmlns="http://www.w3.org/2000/svg"
    >
      <path
        d="M4 6L8 10L12 6"
        stroke="currentColor"
        strokeWidth="1.5"
        strokeLinecap="round"
        strokeLinejoin="round"
      />
    </svg>
  );
}

function CheckIcon() {
  return (
    <svg
      className="cedros-org-check"
      width="16"
      height="16"
      viewBox="0 0 16 16"
      fill="none"
      xmlns="http://www.w3.org/2000/svg"
    >
      <path
        d="M3 8L6 11L13 4"
        stroke="currentColor"
        strokeWidth="1.5"
        strokeLinecap="round"
        strokeLinejoin="round"
      />
    </svg>
  );
}

function PlusIcon() {
  return (
    <svg
      className="cedros-org-plus"
      width="16"
      height="16"
      viewBox="0 0 16 16"
      fill="none"
      xmlns="http://www.w3.org/2000/svg"
    >
      <path
        d="M8 3V13M3 8H13"
        stroke="currentColor"
        strokeWidth="1.5"
        strokeLinecap="round"
        strokeLinejoin="round"
      />
    </svg>
  );
}
