import { useState, useCallback, useEffect, useRef, useMemo } from 'react';
import type { Session, DisplayError } from '../../types';
import { LoadingSpinner } from '../shared/LoadingSpinner';
import { ErrorMessage } from '../shared/ErrorMessage';

export interface SessionListProps {
  /** List of active sessions */
  sessions: Session[];
  /** Loading state */
  isLoading?: boolean;
  /** Error message */
  error?: DisplayError;
  /** Callback to revoke all other sessions */
  onRevokeAll?: () => Promise<void>;
  /** Additional CSS class */
  className?: string;
}

/**
 * Display active sessions and allow revoking all other sessions.
 *
 * @example
 * ```tsx
 * function SecuritySettings() {
 *   const { sessions, isLoading, error, revokeAllSessions } = useSessions();
 *
 *   return (
 *     <SessionList
 *       sessions={sessions}
 *       isLoading={isLoading}
 *       error={error?.message}
 *       onRevokeAll={revokeAllSessions}
 *     />
 *   );
 * }
 * ```
 */
export function SessionList({
  sessions,
  isLoading = false,
  error,
  onRevokeAll,
  className = '',
}: SessionListProps) {
  const [isRevoking, setIsRevoking] = useState(false);
  const [revokeSuccess, setRevokeSuccess] = useState(false);
  const revokeTimerRef = useRef<number | null>(null);

  // PERF-05: Memoize filtered count to avoid O(n) filter on every render.
  // This count is used in the button text and handleRevokeAll callback.
  const otherSessionCount = useMemo(() => sessions.filter((s) => !s.isCurrent).length, [sessions]);

  const handleRevokeAll = useCallback(async () => {
    if (!onRevokeAll) return;

    // M-09: Recalculate count inside handler to avoid stale closure.
    // If sessions change between callback creation and confirm dialog,
    // we want the confirm message to show the current count.
    const currentOtherCount = sessions.filter((s) => !s.isCurrent).length;
    if (currentOtherCount === 0) return;

    const confirmed = window.confirm(
      `Are you sure you want to sign out of ${currentOtherCount} other device(s)? This will log you out everywhere except this browser.`
    );
    if (!confirmed) return;

    setIsRevoking(true);
    setRevokeSuccess(false);

    try {
      await onRevokeAll();
      setRevokeSuccess(true);
      if (revokeTimerRef.current !== null) {
        window.clearTimeout(revokeTimerRef.current);
      }
      revokeTimerRef.current = window.setTimeout(() => {
        setRevokeSuccess(false);
        revokeTimerRef.current = null;
      }, 3000);
    } finally {
      setIsRevoking(false);
    }
  }, [onRevokeAll, sessions]);

  useEffect(() => {
    return () => {
      if (revokeTimerRef.current !== null) {
        window.clearTimeout(revokeTimerRef.current);
        revokeTimerRef.current = null;
      }
    };
  }, []);

  if (isLoading && sessions.length === 0) {
    return (
      <div className={`cedros-session-list cedros-session-list-loading ${className}`}>
        <LoadingSpinner />
        <span>Loading sessions...</span>
      </div>
    );
  }

  if (error) {
    return (
      <div className={`cedros-session-list ${className}`}>
        <ErrorMessage error={error} />
      </div>
    );
  }

  if (sessions.length === 0) {
    return (
      <div className={`cedros-session-list cedros-session-list-empty ${className}`}>
        <p>No active sessions found.</p>
      </div>
    );
  }

  return (
    <div className={`cedros-session-list ${className}`}>
      {revokeSuccess && (
        <div className="cedros-session-success" role="status">
          <CheckIcon />
          <span>All other sessions have been revoked.</span>
        </div>
      )}

      <ul className="cedros-session-items">
        {sessions.map((session) => (
          <SessionItem key={session.id} session={session} />
        ))}
      </ul>

      {onRevokeAll && otherSessionCount > 0 && (
        <div className="cedros-session-actions">
          <button
            type="button"
            className="cedros-button cedros-button-danger"
            onClick={handleRevokeAll}
            disabled={isRevoking}
          >
            {isRevoking ? (
              <>
                <LoadingSpinner size="sm" />
                <span>Signing out...</span>
              </>
            ) : (
              `Sign out of ${otherSessionCount} other device${otherSessionCount > 1 ? 's' : ''}`
            )}
          </button>
        </div>
      )}
    </div>
  );
}

// Session item component
function SessionItem({ session }: { session: Session }) {
  const browserInfo = parseUserAgent(session.userAgent);
  const isExpiringSoon = isSessionExpiringSoon(session.expiresAt);

  return (
    <li className={`cedros-session-item ${session.isCurrent ? 'cedros-session-item-current' : ''}`}>
      <div className="cedros-session-item-icon">
        <DeviceIcon userAgent={session.userAgent} />
      </div>
      <div className="cedros-session-item-info">
        <div className="cedros-session-item-main">
          <span className="cedros-session-item-device">
            {browserInfo.browser} on {browserInfo.os}
          </span>
          {session.isCurrent && (
            <span className="cedros-session-current-badge">Current session</span>
          )}
        </div>
        <div className="cedros-session-item-meta">
          {session.ipAddress && (
            <span className="cedros-session-item-ip">IP: {session.ipAddress}</span>
          )}
          <span className="cedros-session-item-created">
            Started {formatRelativeTime(session.createdAt)}
          </span>
          {isExpiringSoon && <span className="cedros-session-expiring">Expires soon</span>}
        </div>
      </div>
    </li>
  );
}

// Helper functions
interface BrowserInfo {
  browser: string;
  os: string;
}

function parseUserAgent(userAgent?: string): BrowserInfo {
  if (!userAgent) {
    return { browser: 'Unknown browser', os: 'Unknown device' };
  }

  // Simple browser detection
  let browser = 'Unknown browser';
  if (userAgent.includes('Chrome') && !userAgent.includes('Edg')) {
    browser = 'Chrome';
  } else if (userAgent.includes('Safari') && !userAgent.includes('Chrome')) {
    browser = 'Safari';
  } else if (userAgent.includes('Firefox')) {
    browser = 'Firefox';
  } else if (userAgent.includes('Edg')) {
    browser = 'Edge';
  }

  // Simple OS detection
  let os = 'Unknown device';
  if (userAgent.includes('Windows')) {
    os = 'Windows';
  } else if (userAgent.includes('Mac')) {
    os = 'macOS';
  } else if (userAgent.includes('Linux')) {
    os = 'Linux';
  } else if (userAgent.includes('iPhone') || userAgent.includes('iPad')) {
    os = 'iOS';
  } else if (userAgent.includes('Android')) {
    os = 'Android';
  }

  return { browser, os };
}

function formatRelativeTime(dateString: string): string {
  const date = new Date(dateString);
  const now = new Date();
  const diffMs = now.getTime() - date.getTime();
  const diffMins = Math.floor(diffMs / (1000 * 60));
  const diffHours = Math.floor(diffMs / (1000 * 60 * 60));
  const diffDays = Math.floor(diffMs / (1000 * 60 * 60 * 24));

  if (diffMins < 1) {
    return 'just now';
  } else if (diffMins < 60) {
    return `${diffMins} minute${diffMins > 1 ? 's' : ''} ago`;
  } else if (diffHours < 24) {
    return `${diffHours} hour${diffHours > 1 ? 's' : ''} ago`;
  } else if (diffDays < 7) {
    return `${diffDays} day${diffDays > 1 ? 's' : ''} ago`;
  } else {
    return date.toLocaleDateString(undefined, {
      year: 'numeric',
      month: 'short',
      day: 'numeric',
    });
  }
}

function isSessionExpiringSoon(expiresAt: string): boolean {
  const expires = new Date(expiresAt);
  const now = new Date();
  const hourInMs = 60 * 60 * 1000;
  return expires.getTime() - now.getTime() < hourInMs;
}

// Icon components
function DeviceIcon({ userAgent }: { userAgent?: string }) {
  const isMobile =
    userAgent?.includes('iPhone') || userAgent?.includes('iPad') || userAgent?.includes('Android');

  if (isMobile) {
    return (
      <svg
        className="cedros-session-device-icon"
        width="24"
        height="24"
        viewBox="0 0 24 24"
        fill="none"
        xmlns="http://www.w3.org/2000/svg"
      >
        <rect x="5" y="2" width="14" height="20" rx="2" stroke="currentColor" strokeWidth="1.5" />
        <circle cx="12" cy="18" r="1" fill="currentColor" />
      </svg>
    );
  }

  return (
    <svg
      className="cedros-session-device-icon"
      width="24"
      height="24"
      viewBox="0 0 24 24"
      fill="none"
      xmlns="http://www.w3.org/2000/svg"
    >
      <rect x="2" y="4" width="20" height="14" rx="2" stroke="currentColor" strokeWidth="1.5" />
      <path d="M8 21H16" stroke="currentColor" strokeWidth="1.5" strokeLinecap="round" />
      <path d="M12 18V21" stroke="currentColor" strokeWidth="1.5" />
    </svg>
  );
}

function CheckIcon() {
  return (
    <svg
      className="cedros-session-check"
      width="16"
      height="16"
      viewBox="0 0 16 16"
      fill="none"
      xmlns="http://www.w3.org/2000/svg"
    >
      <path
        d="M3 8L6 11L13 5"
        stroke="currentColor"
        strokeWidth="2"
        strokeLinecap="round"
        strokeLinejoin="round"
      />
    </svg>
  );
}
