import { useState } from 'react';
import { useAppleAuth } from '../../hooks/useAppleAuth';
import { LoadingSpinner } from '../shared/LoadingSpinner';
import { isAppleDevice } from '../../utils/deviceDetection';

export interface AppleLoginButtonProps {
  onSuccess?: () => void;
  onError?: (error: Error) => void;
  className?: string;
  variant?: 'default' | 'outline';
  size?: 'sm' | 'md' | 'lg';
  disabled?: boolean;
  /**
   * Hide the button on non-Apple devices (macOS, iOS, iPadOS).
   * When true (default), button only renders on Apple devices.
   * When false, button always renders regardless of device.
   * @default true
   */
  hideOnNonApple?: boolean;
}

/**
 * Apple Sign In button
 *
 * Requires `appleClientId` to be configured in CedrosLoginProvider.
 *
 * @example
 * ```tsx
 * <AppleLoginButton
 *   onSuccess={() => console.log('Logged in!')}
 *   onError={(err) => console.error(err)}
 * />
 * ```
 */
export function AppleLoginButton({
  onSuccess,
  onError,
  className = '',
  variant = 'default',
  size = 'md',
  disabled = false,
  hideOnNonApple = true,
}: AppleLoginButtonProps) {
  const { signIn, isLoading, isInitialized } = useAppleAuth();

  // Detect Apple device on initial render (lazy initializer avoids re-detection)
  const [isApple] = useState(() => isAppleDevice());

  // Hide button on non-Apple devices if hideOnNonApple is true
  if (hideOnNonApple && !isApple) {
    return null;
  }

  const handleClick = async () => {
    try {
      await signIn();
      onSuccess?.();
    } catch (err) {
      const error = err instanceof Error ? err : new Error(String(err));
      onError?.(error);
    }
  };

  const sizeClasses = {
    sm: 'cedros-button-sm',
    md: 'cedros-button-md',
    lg: 'cedros-button-lg',
  };

  const variantClasses = {
    default: 'cedros-button-social',
    outline: 'cedros-button-social-outline',
  };

  return (
    <button
      type="button"
      className={`cedros-button ${variantClasses[variant]} ${sizeClasses[size]} ${className}`}
      onClick={handleClick}
      disabled={disabled || !isInitialized || isLoading}
      aria-label="Sign in with Apple"
    >
      {isLoading ? (
        <LoadingSpinner size="sm" />
      ) : (
        <svg
          className="cedros-button-icon"
          width="18"
          height="18"
          viewBox="0 0 24 24"
          fill="currentColor"
          aria-hidden="true"
        >
          <path d="M18.71 19.5c-.83 1.24-1.71 2.45-3.05 2.47-1.34.03-1.77-.79-3.29-.79-1.53 0-2 .77-3.27.82-1.31.05-2.3-1.32-3.14-2.53C4.25 17 2.94 12.45 4.7 9.39c.87-1.52 2.43-2.48 4.12-2.51 1.28-.02 2.5.87 3.29.87.78 0 2.26-1.07 3.81-.91.65.03 2.47.26 3.64 1.98-.09.06-2.17 1.28-2.15 3.81.03 3.02 2.65 4.03 2.68 4.04-.03.07-.42 1.44-1.38 2.83M13 3.5c.73-.83 1.94-1.46 2.94-1.5.13 1.17-.34 2.35-1.04 3.19-.69.85-1.83 1.51-2.95 1.42-.15-1.15.41-2.35 1.05-3.11z" />
        </svg>
      )}
      <span>Continue with Apple</span>
    </button>
  );
}
