/**
 * Setup Wizard Component
 *
 * Displayed during first-run when no admin user exists.
 * Allows creating the first admin account.
 *
 * @example
 * ```tsx
 * function AdminApp() {
 *   const { status, isLoading, checkStatus, createAdmin } = useSetup();
 *
 *   useEffect(() => {
 *     checkStatus();
 *   }, [checkStatus]);
 *
 *   if (isLoading) return <LoadingSpinner />;
 *   if (status?.needsSetup) {
 *     return <SetupWizard onComplete={() => window.location.reload()} />;
 *   }
 *   return <CedrosAdminDashboard />;
 * }
 * ```
 */

import { useState, useCallback, useEffect } from 'react';
import { useSetup } from '../../hooks/useSetup';
import { LoadingSpinner } from '../shared/LoadingSpinner';
import { ErrorMessage } from '../shared/ErrorMessage';

export interface SetupWizardProps {
  /** Callback when setup is complete */
  onComplete?: () => void;
  /** Additional CSS class */
  className?: string;
}

interface FormState {
  email: string;
  password: string;
  confirmPassword: string;
  name: string;
  orgName: string;
}

interface ValidationErrors {
  email?: string;
  password?: string;
  confirmPassword?: string;
}

const EMAIL_REGEX = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
const MIN_PASSWORD_LENGTH = 8;

function validateForm(form: FormState): ValidationErrors {
  const errors: ValidationErrors = {};

  if (!form.email) {
    errors.email = 'Email is required';
  } else if (!EMAIL_REGEX.test(form.email)) {
    errors.email = 'Invalid email format';
  }

  if (!form.password) {
    errors.password = 'Password is required';
  } else if (form.password.length < MIN_PASSWORD_LENGTH) {
    errors.password = `Password must be at least ${MIN_PASSWORD_LENGTH} characters`;
  }

  if (!form.confirmPassword) {
    errors.confirmPassword = 'Please confirm your password';
  } else if (form.password !== form.confirmPassword) {
    errors.confirmPassword = 'Passwords do not match';
  }

  return errors;
}

export function SetupWizard({ onComplete, className = '' }: SetupWizardProps) {
  const { status, isLoading, isCreating, error, checkStatus, createAdmin } = useSetup();
  const [form, setForm] = useState<FormState>({
    email: '',
    password: '',
    confirmPassword: '',
    name: '',
    orgName: '',
  });
  const [validationErrors, setValidationErrors] = useState<ValidationErrors>({});
  const [success, setSuccess] = useState(false);

  useEffect(() => {
    checkStatus();
  }, [checkStatus]);

  const handleChange = useCallback(
    (field: keyof FormState) => (e: React.ChangeEvent<HTMLInputElement>) => {
      setForm((prev) => ({ ...prev, [field]: e.target.value }));
      // Clear validation error when user types
      setValidationErrors((prev) => ({ ...prev, [field]: undefined }));
    },
    []
  );

  const handleSubmit = useCallback(
    async (e: React.FormEvent) => {
      e.preventDefault();

      const errors = validateForm(form);
      if (Object.keys(errors).length > 0) {
        setValidationErrors(errors);
        return;
      }

      try {
        await createAdmin({
          email: form.email,
          password: form.password,
          name: form.name || undefined,
          orgName: form.orgName || undefined,
        });
        setSuccess(true);
        onComplete?.();
      } catch {
        // Error is handled by the hook
      }
    },
    [form, createAdmin, onComplete]
  );

  if (isLoading) {
    return (
      <div className={`cedros-setup ${className}`}>
        <div className="cedros-setup__loading">
          <LoadingSpinner />
          <span className="cedros-setup__loading-text">Checking setup status...</span>
        </div>
      </div>
    );
  }

  if (status && !status.needsSetup) {
    return (
      <div className={`cedros-setup ${className}`}>
        <div className="cedros-setup__complete">
          <div className="cedros-setup__icon cedros-setup__icon--success">
            <svg
              width="48"
              height="48"
              viewBox="0 0 24 24"
              fill="none"
              stroke="currentColor"
              strokeWidth="2"
              strokeLinecap="round"
              strokeLinejoin="round"
            >
              <path d="M22 11.08V12a10 10 0 1 1-5.93-9.14" />
              <polyline points="22 4 12 14.01 9 11.01" />
            </svg>
          </div>
          <h2 className="cedros-setup__title">Setup Complete</h2>
          <p className="cedros-setup__text">An admin account already exists. You can now log in.</p>
        </div>
      </div>
    );
  }

  if (success) {
    return (
      <div className={`cedros-setup ${className}`}>
        <div className="cedros-setup__complete">
          <div className="cedros-setup__icon cedros-setup__icon--success">
            <svg
              width="48"
              height="48"
              viewBox="0 0 24 24"
              fill="none"
              stroke="currentColor"
              strokeWidth="2"
              strokeLinecap="round"
              strokeLinejoin="round"
            >
              <path d="M22 11.08V12a10 10 0 1 1-5.93-9.14" />
              <polyline points="22 4 12 14.01 9 11.01" />
            </svg>
          </div>
          <h2 className="cedros-setup__title">Admin Account Created</h2>
          <p className="cedros-setup__text">
            Your admin account has been created successfully. You can now log in with your
            credentials.
          </p>
        </div>
      </div>
    );
  }

  return (
    <div className={`cedros-setup ${className}`}>
      <div className="cedros-setup__container">
        <div className="cedros-setup__header">
          <div className="cedros-setup__logo">
            <svg
              width="32"
              height="32"
              viewBox="0 0 24 24"
              fill="none"
              stroke="currentColor"
              strokeWidth="2"
              strokeLinecap="round"
              strokeLinejoin="round"
            >
              <path d="M19 7V4a1 1 0 0 0-1-1H5a2 2 0 0 0 0 4h15a1 1 0 0 1 1 1v4h-3a2 2 0 0 0 0 4h3a1 1 0 0 0 1-1v-2a1 1 0 0 0-1-1" />
              <path d="M3 5v14a2 2 0 0 0 2 2h15a1 1 0 0 0 1-1v-4" />
            </svg>
          </div>
          <h1 className="cedros-setup__title">Welcome to Cedros</h1>
          <p className="cedros-setup__subtitle">
            Let&apos;s set up your admin account to get started.
          </p>
        </div>

        <form className="cedros-setup__form" onSubmit={handleSubmit}>
          {error && <ErrorMessage error={error.message} />}

          <div className="cedros-setup__field">
            <label htmlFor="setup-email" className="cedros-setup__label">
              Email Address <span className="cedros-setup__required">*</span>
            </label>
            <input
              id="setup-email"
              type="email"
              className={`cedros-setup__input ${validationErrors.email ? 'cedros-setup__input--error' : ''}`}
              value={form.email}
              onChange={handleChange('email')}
              placeholder="admin@example.com"
              autoComplete="email"
              autoFocus
              disabled={isCreating}
            />
            {validationErrors.email && (
              <span className="cedros-setup__error">{validationErrors.email}</span>
            )}
          </div>

          <div className="cedros-setup__field">
            <label htmlFor="setup-name" className="cedros-setup__label">
              Display Name
            </label>
            <input
              id="setup-name"
              type="text"
              className="cedros-setup__input"
              value={form.name}
              onChange={handleChange('name')}
              placeholder="Admin"
              autoComplete="name"
              disabled={isCreating}
            />
          </div>

          <div className="cedros-setup__field">
            <label htmlFor="setup-org-name" className="cedros-setup__label">
              Organization Name
            </label>
            <input
              id="setup-org-name"
              type="text"
              className="cedros-setup__input"
              value={form.orgName}
              onChange={handleChange('orgName')}
              placeholder="My Organization"
              disabled={isCreating}
            />
          </div>

          <div className="cedros-setup__field">
            <label htmlFor="setup-password" className="cedros-setup__label">
              Password <span className="cedros-setup__required">*</span>
            </label>
            <input
              id="setup-password"
              type="password"
              className={`cedros-setup__input ${validationErrors.password ? 'cedros-setup__input--error' : ''}`}
              value={form.password}
              onChange={handleChange('password')}
              placeholder="At least 8 characters"
              autoComplete="new-password"
              disabled={isCreating}
            />
            {validationErrors.password && (
              <span className="cedros-setup__error">{validationErrors.password}</span>
            )}
          </div>

          <div className="cedros-setup__field">
            <label htmlFor="setup-confirm-password" className="cedros-setup__label">
              Confirm Password <span className="cedros-setup__required">*</span>
            </label>
            <input
              id="setup-confirm-password"
              type="password"
              className={`cedros-setup__input ${validationErrors.confirmPassword ? 'cedros-setup__input--error' : ''}`}
              value={form.confirmPassword}
              onChange={handleChange('confirmPassword')}
              placeholder="Confirm your password"
              autoComplete="new-password"
              disabled={isCreating}
            />
            {validationErrors.confirmPassword && (
              <span className="cedros-setup__error">{validationErrors.confirmPassword}</span>
            )}
          </div>

          <button type="submit" className="cedros-setup__button" disabled={isCreating}>
            {isCreating ? (
              <>
                <LoadingSpinner />
                <span>Creating Account...</span>
              </>
            ) : (
              'Create Admin Account'
            )}
          </button>
        </form>

        <div className="cedros-setup__footer">
          <p className="cedros-setup__note">
            This will be the first administrator account for your installation.
          </p>
          {status?.serverVersion && (
            <p className="cedros-setup__version">Server version: {status.serverVersion}</p>
          )}
        </div>
      </div>
    </div>
  );
}
