/**
 * Hook for user withdrawal operations
 *
 * Handles withdrawing SOL or SPL tokens from the user's embedded wallet
 * to an external Solana address. Also fetches wallet token balances.
 *
 * Requires an unlocked SSS wallet and feature_user_withdrawals enabled.
 *
 * Safe to call outside CedrosLoginProvider (returns no-op functions).
 */

import { useState, useCallback, useMemo } from 'react';
import { useCedrosLoginOptional } from '../context/useCedrosLogin';
import { ApiClient, handleApiError } from '../utils/apiClient';
import type {
  UseWithdrawalReturn,
  WithdrawalResponse,
  WalletBalancesResponse,
  UserWithdrawalHistoryResponse,
} from '../types/withdrawal';

export type { UseWithdrawalReturn } from '../types/withdrawal';

export function useWithdrawal(): UseWithdrawalReturn {
  const context = useCedrosLoginOptional();
  const [isSubmitting, setIsSubmitting] = useState(false);
  const [error, setError] = useState<string | null>(null);
  const [lastResult, setLastResult] = useState<WithdrawalResponse | null>(null);

  const apiClient = useMemo(() => {
    if (!context) return null;
    return new ApiClient({
      baseUrl: context.config.serverUrl,
      timeoutMs: context.config.requestTimeout,
      retryAttempts: context.config.retryAttempts,
      getAccessToken: context._internal?.getAccessToken,
    });
  }, [context]);

  const clearError = useCallback(() => {
    setError(null);
  }, []);

  const getBalances = useCallback(async (): Promise<WalletBalancesResponse> => {
    if (!apiClient) {
      throw new Error('useWithdrawal must be used within a CedrosLoginProvider');
    }

    try {
      return await apiClient.get<WalletBalancesResponse>('/wallet/withdraw/balances');
    } catch (err) {
      const apiError = handleApiError(err, 'Failed to fetch wallet balances');
      setError(apiError.message);
      throw apiError;
    }
  }, [apiClient]);

  const withdrawSol = useCallback(
    async (destination: string, amountLamports: number): Promise<WithdrawalResponse> => {
      if (!apiClient) {
        throw new Error('useWithdrawal must be used within a CedrosLoginProvider');
      }
      setIsSubmitting(true);
      setError(null);

      try {
        const data = await apiClient.post<WithdrawalResponse>('/wallet/withdraw/sol', {
          destination,
          amount_lamports: amountLamports,
        });
        setLastResult(data);
        return data;
      } catch (err) {
        const apiError = handleApiError(err, 'Failed to withdraw SOL');
        setError(apiError.message);
        throw apiError;
      } finally {
        setIsSubmitting(false);
      }
    },
    [apiClient]
  );

  const withdrawSpl = useCallback(
    async (destination: string, tokenMint: string, amount: string): Promise<WithdrawalResponse> => {
      if (!apiClient) {
        throw new Error('useWithdrawal must be used within a CedrosLoginProvider');
      }
      setIsSubmitting(true);
      setError(null);

      try {
        const data = await apiClient.post<WithdrawalResponse>('/wallet/withdraw/spl', {
          destination,
          token_mint: tokenMint,
          amount,
        });
        setLastResult(data);
        return data;
      } catch (err) {
        const apiError = handleApiError(err, 'Failed to withdraw token');
        setError(apiError.message);
        throw apiError;
      } finally {
        setIsSubmitting(false);
      }
    },
    [apiClient]
  );

  const getHistory = useCallback(
    async (limit = 10, offset = 0): Promise<UserWithdrawalHistoryResponse> => {
      if (!apiClient) {
        throw new Error('useWithdrawal must be used within a CedrosLoginProvider');
      }

      try {
        const clampedLimit = Math.max(1, Math.min(100, Math.trunc(limit)));
        const clampedOffset = Math.max(0, Math.trunc(offset));
        const params = new URLSearchParams({
          limit: String(clampedLimit),
          offset: String(clampedOffset),
        });
        return await apiClient.get<UserWithdrawalHistoryResponse>(
          `/wallet/withdraw/history?${params}`
        );
      } catch (err) {
        const apiError = handleApiError(err, 'Failed to fetch withdrawal history');
        setError(apiError.message);
        throw apiError;
      }
    },
    [apiClient]
  );

  return {
    withdrawSol,
    withdrawSpl,
    getBalances,
    getHistory,
    isSubmitting,
    error,
    clearError,
    lastResult,
  };
}
