// Minimal WebAuthn JSON helpers (no external deps).
//
// WebAuthn request/response objects contain ArrayBuffers, which can't be sent
// over JSON. Server endpoints in this repo expose options with base64url fields
// and expect credentials back with base64url fields.

type Base64UrlString = string;

function assert(condition: unknown, message: string): asserts condition {
  if (!condition) throw new Error(message);
}

function normalizeBase64Url(input: string): string {
  // Browser encoders may return base64url without padding.
  return input.replace(/-/g, '+').replace(/_/g, '/');
}

export function base64UrlToArrayBuffer(b64url: Base64UrlString): ArrayBuffer {
  assert(typeof b64url === 'string' && b64url.length > 0, 'Expected base64url string');
  const base64 = normalizeBase64Url(b64url);
  const padded = base64 + '='.repeat((4 - (base64.length % 4)) % 4);
  const binary = atob(padded);
  const bytes = new Uint8Array(binary.length);
  for (let i = 0; i < binary.length; i++) bytes[i] = binary.charCodeAt(i);
  return bytes.buffer;
}

export function arrayBufferToBase64Url(buffer: ArrayBuffer): Base64UrlString {
  const bytes = new Uint8Array(buffer);
  let binary = '';
  for (let i = 0; i < bytes.length; i++) binary += String.fromCharCode(bytes[i]);
  const base64 = btoa(binary);
  return base64.replace(/\+/g, '-').replace(/\//g, '_').replace(/=+$/g, '');
}

function mapCredentialDescriptor(desc: unknown): PublicKeyCredentialDescriptor {
  assert(typeof desc === 'object' && desc !== null, 'Invalid credential descriptor');
  const d = desc as Record<string, unknown>;
  assert(typeof d.type === 'string', 'Invalid credential descriptor type');
  assert(typeof d.id === 'string', 'Invalid credential descriptor id');

  return {
    type: d.type as PublicKeyCredentialType,
    id: base64UrlToArrayBuffer(d.id as string),
    transports: Array.isArray(d.transports)
      ? (d.transports as AuthenticatorTransport[])
      : undefined,
  };
}

export type ServerCreationOptions = {
  publicKey: Record<string, unknown>;
};

export function parseCreationOptionsFromServer(
  options: ServerCreationOptions
): PublicKeyCredentialCreationOptions {
  assert(options && typeof options === 'object', 'Missing creation options');
  const publicKey = options.publicKey as Record<string, unknown>;
  assert(publicKey && typeof publicKey === 'object', 'Missing creation options.publicKey');

  assert(typeof publicKey.challenge === 'string', 'Missing creation challenge');
  assert(typeof publicKey.rp === 'object' && publicKey.rp !== null, 'Missing rp');
  assert(typeof publicKey.user === 'object' && publicKey.user !== null, 'Missing user');

  const rp = publicKey.rp as Record<string, unknown>;
  const user = publicKey.user as Record<string, unknown>;

  assert(typeof rp.name === 'string', 'Missing rp.name');
  assert(typeof user.id === 'string', 'Missing user.id');
  assert(typeof user.name === 'string', 'Missing user.name');
  assert(typeof user.displayName === 'string', 'Missing user.displayName');

  const exclude = Array.isArray(publicKey.excludeCredentials)
    ? (publicKey.excludeCredentials as unknown[]).map(mapCredentialDescriptor)
    : undefined;

  const pubKeyCredParams = Array.isArray(publicKey.pubKeyCredParams)
    ? (publicKey.pubKeyCredParams as Array<{ type: string; alg: number }>).map((p) => ({
        type: p.type as PublicKeyCredentialType,
        alg: p.alg,
      }))
    : [];

  return {
    challenge: base64UrlToArrayBuffer(publicKey.challenge as string),
    rp: {
      name: rp.name as string,
      id: typeof rp.id === 'string' ? (rp.id as string) : undefined,
    },
    user: {
      id: base64UrlToArrayBuffer(user.id as string),
      name: user.name as string,
      displayName: user.displayName as string,
    },
    pubKeyCredParams,
    timeout: typeof publicKey.timeout === 'number' ? (publicKey.timeout as number) : undefined,
    attestation:
      typeof publicKey.attestation === 'string'
        ? (publicKey.attestation as AttestationConveyancePreference)
        : undefined,
    authenticatorSelection:
      typeof publicKey.authenticatorSelection === 'object' &&
      publicKey.authenticatorSelection !== null
        ? (publicKey.authenticatorSelection as AuthenticatorSelectionCriteria)
        : undefined,
    excludeCredentials: exclude,
    extensions:
      typeof publicKey.extensions === 'object' && publicKey.extensions !== null
        ? (publicKey.extensions as AuthenticationExtensionsClientInputs)
        : undefined,
  };
}

export type ServerRequestOptions = {
  publicKey: Record<string, unknown>;
};

export function parseRequestOptionsFromServer(
  options: ServerRequestOptions
): PublicKeyCredentialRequestOptions {
  assert(options && typeof options === 'object', 'Missing request options');
  const publicKey = options.publicKey as Record<string, unknown>;
  assert(publicKey && typeof publicKey === 'object', 'Missing request options.publicKey');

  assert(typeof publicKey.challenge === 'string', 'Missing request challenge');

  const allow = Array.isArray(publicKey.allowCredentials)
    ? (publicKey.allowCredentials as unknown[]).map(mapCredentialDescriptor)
    : undefined;

  return {
    challenge: base64UrlToArrayBuffer(publicKey.challenge as string),
    rpId: typeof publicKey.rpId === 'string' ? (publicKey.rpId as string) : undefined,
    timeout: typeof publicKey.timeout === 'number' ? (publicKey.timeout as number) : undefined,
    userVerification:
      typeof publicKey.userVerification === 'string'
        ? (publicKey.userVerification as UserVerificationRequirement)
        : undefined,
    allowCredentials: allow,
    extensions:
      typeof publicKey.extensions === 'object' && publicKey.extensions !== null
        ? (publicKey.extensions as AuthenticationExtensionsClientInputs)
        : undefined,
  };
}

export type PublicKeyCredentialJSON = {
  id: string;
  rawId: string;
  type: string;
  authenticatorAttachment?: string;
  clientExtensionResults?: AuthenticationExtensionsClientOutputs;
  response: Record<string, unknown>;
};

export function credentialToServerJson(cred: PublicKeyCredential): PublicKeyCredentialJSON {
  const rawId = arrayBufferToBase64Url(cred.rawId);
  const response = cred.response as AuthenticatorResponse;

  const common = {
    clientDataJSON: arrayBufferToBase64Url(response.clientDataJSON),
  };

  const responseJson: Record<string, unknown> = { ...common };

  // Registration
  if ('attestationObject' in response) {
    const att = response as AuthenticatorAttestationResponse;
    responseJson.attestationObject = arrayBufferToBase64Url(att.attestationObject);
    if (typeof att.getTransports === 'function') {
      try {
        responseJson.transports = att.getTransports();
      } catch {
        // ignore
      }
    }
  }

  // Authentication
  if ('authenticatorData' in response) {
    const asr = response as AuthenticatorAssertionResponse;
    responseJson.authenticatorData = arrayBufferToBase64Url(asr.authenticatorData);
    responseJson.signature = arrayBufferToBase64Url(asr.signature);
    if (asr.userHandle) {
      responseJson.userHandle = arrayBufferToBase64Url(asr.userHandle);
    }
  }

  return {
    id: cred.id,
    rawId,
    type: cred.type,
    authenticatorAttachment: cred.authenticatorAttachment ?? undefined,
    clientExtensionResults: cred.getClientExtensionResults?.() ?? {},
    response: responseJson,
  };
}
