import type { ReactNode } from 'react';
import { useWebAuthn } from '../../hooks/useWebAuthn';

export interface PasskeyLoginButtonProps {
  onSuccess?: () => void;
  className?: string;
  children?: ReactNode;
  disabled?: boolean;
}

export function PasskeyLoginButton({
  onSuccess,
  className = '',
  children,
  disabled,
}: PasskeyLoginButtonProps) {
  const { authenticatePasskey, isLoading, isSupported } = useWebAuthn();

  const isDisabled = disabled || !isSupported || isLoading;

  return (
    <button
      type="button"
      className={`cedros-button cedros-button-social ${className}`}
      onClick={async () => {
        await authenticatePasskey();
        onSuccess?.();
      }}
      disabled={isDisabled}
      aria-disabled={isDisabled}
    >
      <span className="cedros-social-icon" aria-hidden="true">
        <PasskeyIcon />
      </span>
      <span>{children ?? (isLoading ? 'Continuing...' : 'Continue with Passkey')}</span>
    </button>
  );
}

function PasskeyIcon() {
  return (
    <svg
      width="20"
      height="20"
      viewBox="0 0 24 24"
      fill="none"
      stroke="currentColor"
      strokeWidth="1.5"
      strokeLinecap="round"
      strokeLinejoin="round"
    >
      <path d="M12 10a2 2 0 0 0-2 2c0 1.02-.1 2.51-.26 4" />
      <path d="M14 13.12c0 2.38 0 6.38-1 8.88" />
      <path d="M17.29 21.02c.12-.6.43-2.3.5-3.02" />
      <path d="M2 12a10 10 0 0 1 18-6" />
      <path d="M2 16h.01" />
      <path d="M21.8 16c.2-2 .131-5.354 0-6" />
      <path d="M5 19.5C5.5 18 6 15 6 12a6 6 0 0 1 .34-2" />
      <path d="M8.65 22c.21-.66.45-1.32.57-2" />
      <path d="M9 6.8a6 6 0 0 1 9 5.2v2" />
    </svg>
  );
}
