/**
 * Component to display warnings about missing crypto capabilities
 */

import { getMissingCapabilitiesMessage, getBrowserSupportInfo } from '../../crypto';
import type { CryptoCapabilities } from '../../crypto';

export interface CapabilityWarningProps {
  /** Capability check results */
  capabilities: CryptoCapabilities;
  /** Optional class name */
  className?: string;
}

/**
 * Warning display for missing capabilities
 */
export function CapabilityWarning({ capabilities, className = '' }: CapabilityWarningProps) {
  if (capabilities.allSupported) {
    return null;
  }

  const message = getMissingCapabilitiesMessage(capabilities);
  const browserInfo = getBrowserSupportInfo();

  return (
    <div className={`cedros-capability-warning ${className}`} role="alert">
      <div className="cedros-warning-header">
        <svg
          className="cedros-warning-icon"
          width="24"
          height="24"
          viewBox="0 0 24 24"
          fill="none"
          aria-hidden="true"
        >
          <path
            d="M12 9v4m0 4h.01M21 12a9 9 0 11-18 0 9 9 0 0118 0z"
            stroke="currentColor"
            strokeWidth="2"
            strokeLinecap="round"
          />
        </svg>
        <h3 className="cedros-warning-title">Wallet Feature Unavailable</h3>
      </div>

      <p className="cedros-warning-message">{message}</p>

      <div className="cedros-capability-details">
        <h4>Browser Compatibility</h4>
        <p>
          Detected: {browserInfo.browser} {browserInfo.version}
          {browserInfo.likelySupported ? ' (likely supported)' : ' (may not be supported)'}
        </p>

        <h4>Required Features</h4>
        <ul className="cedros-capability-list">
          <li className={capabilities.webCrypto ? 'cedros-cap-ok' : 'cedros-cap-missing'}>
            Web Crypto API: {capabilities.webCrypto ? 'Available' : 'Missing'}
          </li>
          <li className={capabilities.aesGcm ? 'cedros-cap-ok' : 'cedros-cap-missing'}>
            AES-GCM Encryption: {capabilities.aesGcm ? 'Available' : 'Missing'}
          </li>
          <li className={capabilities.hkdf ? 'cedros-cap-ok' : 'cedros-cap-missing'}>
            HKDF Key Derivation: {capabilities.hkdf ? 'Available' : 'Missing'}
          </li>
          <li className={capabilities.webAuthn ? 'cedros-cap-ok' : 'cedros-cap-missing'}>
            WebAuthn/Passkeys: {capabilities.webAuthn ? 'Available' : 'Missing'}
          </li>
          <li className={capabilities.webAuthnPrf ? 'cedros-cap-ok' : 'cedros-cap-missing'}>
            WebAuthn PRF Extension: {capabilities.webAuthnPrf ? 'Available' : 'Missing'}
          </li>
          <li className={capabilities.argon2 ? 'cedros-cap-ok' : 'cedros-cap-missing'}>
            Argon2 Password Hashing: {capabilities.argon2 ? 'Available' : 'Missing'}
          </li>
        </ul>
      </div>

      <div className="cedros-capability-help">
        <h4>Recommended Browsers</h4>
        <ul>
          <li>Chrome 116+ on Windows, macOS, or Android</li>
          <li>Safari 17+ on macOS or iOS</li>
          <li>Edge 116+ on Windows</li>
        </ul>
        <p className="cedros-capability-note">
          A platform authenticator (Touch ID, Face ID, or Windows Hello) is required.
        </p>
      </div>
    </div>
  );
}
