/**
 * Credit balance display component
 *
 * Shows the user's current credit balance from Privacy Cash deposits.
 * Can display single currency or all currencies.
 */

import { useState, useEffect, useCallback } from 'react';
import { useCredits } from '../../hooks/useCredits';
import type { CreditBalanceResponse } from '../../types/deposit';

export interface CreditBalanceProps {
  /** Show only SOL balance (default) or all currencies */
  showAllCurrencies?: boolean;
  /** Auto-refresh interval in milliseconds (0 to disable) */
  refreshInterval?: number;
  /** Compact display mode */
  compact?: boolean;
  /** Additional CSS classes */
  className?: string;
  /** Callback when balance is loaded */
  onLoad?: (balance: CreditBalanceResponse[]) => void;
}

/**
 * Credit balance display
 *
 * Shows the user's credit balance. Supports auto-refresh.
 */
export function CreditBalance({
  showAllCurrencies = false,
  refreshInterval = 0,
  compact = false,
  className = '',
  onLoad,
}: CreditBalanceProps) {
  const { getBalance, getAllBalances, isLoading, error, clearError } = useCredits();

  const [balances, setBalances] = useState<CreditBalanceResponse[]>([]);
  const [loadError, setLoadError] = useState<string | null>(null);

  const fetchBalance = useCallback(async () => {
    try {
      if (showAllCurrencies) {
        const result = await getAllBalances();
        setBalances(result);
        onLoad?.(result);
      } else {
        const result = await getBalance();
        setBalances([result]);
        onLoad?.([result]);
      }
      setLoadError(null);
    } catch (err) {
      setLoadError(err instanceof Error ? err.message : 'Failed to load balance');
    }
  }, [showAllCurrencies, getBalance, getAllBalances, onLoad]);

  // Initial load
  useEffect(() => {
    fetchBalance();
  }, [fetchBalance]);

  // Auto-refresh
  useEffect(() => {
    if (refreshInterval <= 0) return;

    const interval = setInterval(fetchBalance, refreshInterval);
    return () => clearInterval(interval);
  }, [refreshInterval, fetchBalance]);

  // Error state
  if (loadError || error) {
    return (
      <div className={`cedros-credit-balance cedros-credit-balance-error ${className}`}>
        <p className="cedros-credit-error">{loadError || error}</p>
        <button
          type="button"
          className="cedros-credit-retry"
          onClick={() => {
            clearError();
            setLoadError(null);
            fetchBalance();
          }}
        >
          Retry
        </button>
      </div>
    );
  }

  // Loading state
  if (isLoading && balances.length === 0) {
    return (
      <div className={`cedros-credit-balance cedros-credit-balance-loading ${className}`}>
        <span className="cedros-credit-loading-indicator" />
        <span className="cedros-credit-loading-text">Loading balance...</span>
      </div>
    );
  }

  // Compact display
  if (compact) {
    const primaryBalance = balances[0];
    return (
      <div className={`cedros-credit-balance cedros-credit-balance-compact ${className}`}>
        {primaryBalance ? (
          <span
            className="cedros-credit-value"
            title={`${primaryBalance.balanceLamports} lamports`}
          >
            {primaryBalance.display}
          </span>
        ) : (
          <span className="cedros-credit-value cedros-credit-value-zero">0.0000 SOL</span>
        )}
        {isLoading && <span className="cedros-credit-refresh-indicator" title="Refreshing..." />}
      </div>
    );
  }

  // Full display
  return (
    <div className={`cedros-credit-balance ${className}`}>
      <div className="cedros-credit-header">
        <h4 className="cedros-credit-title">Credit Balance</h4>
        <button
          type="button"
          className="cedros-credit-refresh"
          onClick={fetchBalance}
          disabled={isLoading}
          title="Refresh balance"
        >
          {isLoading ? '...' : ''}
        </button>
      </div>

      {balances.length === 0 ? (
        <div className="cedros-credit-empty">
          <p className="cedros-credit-empty-message">No credit balance yet.</p>
          <p className="cedros-credit-empty-hint">Make a deposit to get started.</p>
        </div>
      ) : (
        <div className="cedros-credit-list">
          {balances.map((balance) => (
            <div key={balance.currency} className="cedros-credit-item">
              <span className="cedros-credit-currency">{balance.currency}</span>
              <span className="cedros-credit-amount">{balance.display}</span>
            </div>
          ))}
        </div>
      )}
    </div>
  );
}
