/**
 * Wallet recovery wizard component
 *
 * Guides users through wallet recovery using their BIP-39 recovery phrase:
 * 1. Enter 12-word recovery phrase
 * 2. Choose new auth method (password/passkey)
 * 3. Enter new credential
 * 4. Submit to re-encrypt and replace wallet material
 */

import { useState, useCallback } from 'react';
import type { FormEvent } from 'react';
import { useWalletRecovery } from '../../hooks/useWalletRecovery';
import { RecoveryPhraseInput } from './RecoveryPhraseInput';
import type { ShareAAuthMethod } from '../../types/wallet';

export interface WalletRecoveryProps {
  /** Callback when recovery completes successfully */
  onComplete?: () => void;
  /** Callback when user cancels recovery */
  onCancel?: () => void;
  /** Additional CSS classes */
  className?: string;
  /** Default auth method (optional) */
  defaultAuthMethod?: ShareAAuthMethod;
}

/**
 * Wallet recovery wizard
 *
 * Two-phase flow:
 * 1. Enter recovery phrase
 * 2. Enter new credential (password/passkey)
 */
export function WalletRecovery({
  onComplete,
  onCancel,
  className = '',
  defaultAuthMethod = 'password',
}: WalletRecoveryProps) {
  const { state, startRecovery, cancel, isRecovering } = useWalletRecovery();

  // Phase 1: Recovery phrase
  const [words, setWords] = useState<string[]>([]);
  const [phraseValidated, setPhraseValidated] = useState(false);

  // Phase 2: New credential
  const [authMethod, setAuthMethod] = useState<ShareAAuthMethod>(defaultAuthMethod);
  const [credential, setCredential] = useState('');
  const [confirmCredential, setConfirmCredential] = useState('');
  const [credentialError, setCredentialError] = useState<string | null>(null);

  // Handle phrase submission (phase 1 complete)
  const handlePhraseSubmit = useCallback((submittedWords: string[]) => {
    setWords(submittedWords);
    setPhraseValidated(true);
  }, []);

  // Handle credential submission (phase 2 complete)
  const handleCredentialSubmit = useCallback(
    async (e: FormEvent) => {
      e.preventDefault();
      setCredentialError(null);

      // Passkey doesn't need confirmation
      if (authMethod !== 'passkey') {
        if (credential !== confirmCredential) {
          setCredentialError('Passwords do not match');
          return;
        }

        if (authMethod === 'password' && credential.length < 8) {
          setCredentialError('Password must be at least 8 characters');
          return;
        }
      }

      await startRecovery(words, authMethod, credential);
    },
    [words, authMethod, credential, confirmCredential, startRecovery]
  );

  // Handle cancel
  const handleCancel = useCallback(() => {
    cancel();
    setWords([]);
    setPhraseValidated(false);
    setCredential('');
    setConfirmCredential('');
    onCancel?.();
  }, [cancel, onCancel]);

  // Handle back to phrase entry
  const handleBack = useCallback(() => {
    setPhraseValidated(false);
    setCredential('');
    setConfirmCredential('');
  }, []);

  // Handle completion
  const handleComplete = useCallback(() => {
    onComplete?.();
  }, [onComplete]);

  // Render based on state
  if (state.step === 'validating' || state.step === 'encrypting' || state.step === 'uploading') {
    return (
      <div className={`cedros-wallet-recovery ${className}`}>
        <div className="cedros-wallet-recovery-progress">
          <div className="cedros-wallet-recovery-icon">
            <LoadingSpinner />
          </div>
          <h3 className="cedros-wallet-recovery-title">
            {state.step === 'validating' && 'Validating Recovery Phrase'}
            {state.step === 'encrypting' && 'Encrypting Wallet'}
            {state.step === 'uploading' && 'Saving to Server'}
          </h3>
          <p className="cedros-wallet-recovery-description">
            {state.step === 'validating' && 'Checking your recovery phrase...'}
            {state.step === 'encrypting' && 'Securing your wallet with new encryption...'}
            {state.step === 'uploading' && 'Uploading encrypted wallet data...'}
          </p>
        </div>
      </div>
    );
  }

  if (state.step === 'complete') {
    return (
      <div className={`cedros-wallet-recovery ${className}`}>
        <div className="cedros-wallet-recovery-success">
          <div className="cedros-wallet-recovery-icon">
            <SuccessIcon />
          </div>
          <h3 className="cedros-wallet-recovery-title">Wallet Recovered</h3>
          <p className="cedros-wallet-recovery-description">
            Your wallet has been successfully recovered and secured with your new{' '}
            {authMethod === 'passkey' ? 'passkey' : 'password'}.
          </p>
          <div className="cedros-wallet-recovery-actions">
            <button
              type="button"
              className="cedros-button cedros-button-primary"
              onClick={handleComplete}
            >
              Done
            </button>
          </div>
        </div>
      </div>
    );
  }

  if (state.step === 'error') {
    return (
      <div className={`cedros-wallet-recovery ${className}`}>
        <div className="cedros-wallet-recovery-error">
          <div className="cedros-wallet-recovery-icon">
            <ErrorIcon />
          </div>
          <h3 className="cedros-wallet-recovery-title">Recovery Failed</h3>
          <p className="cedros-wallet-recovery-description">
            {state.error ?? 'An error occurred during recovery. Please try again.'}
          </p>
          <div className="cedros-wallet-recovery-actions">
            <button
              type="button"
              className="cedros-button cedros-button-primary"
              onClick={handleCancel}
            >
              Start Over
            </button>
          </div>
        </div>
      </div>
    );
  }

  // Phase 1: Enter recovery phrase
  if (!phraseValidated) {
    return (
      <div className={`cedros-wallet-recovery ${className}`}>
        <div className="cedros-wallet-recovery-phrase">
          <div className="cedros-wallet-recovery-header">
            <h3 className="cedros-wallet-recovery-title">Recover Your Wallet</h3>
            <p className="cedros-wallet-recovery-description">
              Enter your 12-word recovery phrase to restore your wallet.
            </p>
          </div>

          <RecoveryPhraseInput
            onSubmit={handlePhraseSubmit}
            onCancel={handleCancel}
            isSubmitting={false}
          />
        </div>
      </div>
    );
  }

  // Phase 2: Enter new credential
  return (
    <div className={`cedros-wallet-recovery ${className}`}>
      <form className="cedros-wallet-recovery-credential" onSubmit={handleCredentialSubmit}>
        <div className="cedros-wallet-recovery-header">
          <h3 className="cedros-wallet-recovery-title">Set New Security</h3>
          <p className="cedros-wallet-recovery-description">
            Choose how to secure your recovered wallet.
          </p>
        </div>

        {/* Auth method selector */}
        <div className="cedros-auth-method-selector">
          <label className="cedros-radio-label">
            <input
              type="radio"
              name="authMethod"
              value="password"
              checked={authMethod === 'password'}
              onChange={() => setAuthMethod('password')}
              disabled={isRecovering}
            />
            <span>Password</span>
          </label>
          <label className="cedros-radio-label">
            <input
              type="radio"
              name="authMethod"
              value="passkey"
              checked={authMethod === 'passkey'}
              onChange={() => setAuthMethod('passkey')}
              disabled={isRecovering}
            />
            <span>Passkey</span>
          </label>
        </div>

        {/* Password input */}
        {authMethod === 'password' && (
          <>
            <div className="cedros-password-input">
              <label htmlFor="recovery-password" className="cedros-label">
                New Password
              </label>
              <input
                id="recovery-password"
                type="password"
                className="cedros-input"
                value={credential}
                onChange={(e) => setCredential(e.target.value)}
                disabled={isRecovering}
                minLength={8}
                placeholder="Enter a strong password"
              />
            </div>
            <div className="cedros-password-input">
              <label htmlFor="recovery-password-confirm" className="cedros-label">
                Confirm Password
              </label>
              <input
                id="recovery-password-confirm"
                type="password"
                className="cedros-input"
                value={confirmCredential}
                onChange={(e) => setConfirmCredential(e.target.value)}
                disabled={isRecovering}
                aria-invalid={credentialError ? 'true' : undefined}
                placeholder="Confirm your password"
              />
              {credentialError && (
                <p className="cedros-input-error" role="alert">
                  {credentialError}
                </p>
              )}
            </div>
          </>
        )}

        {/* Passkey info */}
        {authMethod === 'passkey' && (
          <div className="cedros-passkey-info">
            <PasskeyIcon />
            <p>Use your passkey (Touch ID, Face ID, or Windows Hello) to secure your wallet.</p>
          </div>
        )}

        <div className="cedros-wallet-recovery-actions">
          <button
            type="button"
            className="cedros-button cedros-button-secondary"
            onClick={handleBack}
            disabled={isRecovering}
          >
            Back
          </button>
          <button
            type="submit"
            className="cedros-button cedros-button-primary"
            disabled={
              isRecovering ||
              (authMethod !== 'passkey' &&
                (credential.length === 0 || confirmCredential.length === 0))
            }
          >
            {isRecovering ? 'Recovering...' : 'Recover Wallet'}
          </button>
        </div>
      </form>
    </div>
  );
}

// Icon components
function LoadingSpinner() {
  return (
    <svg
      width="48"
      height="48"
      viewBox="0 0 48 48"
      fill="none"
      aria-hidden="true"
      className="cedros-spinner"
    >
      <circle
        cx="24"
        cy="24"
        r="20"
        stroke="var(--cedros-muted, #e5e7eb)"
        strokeWidth="3"
        fill="none"
      />
      <path
        d="M24 4a20 20 0 0 1 20 20"
        stroke="var(--cedros-primary, #6366f1)"
        strokeWidth="3"
        strokeLinecap="round"
        fill="none"
      />
    </svg>
  );
}

function SuccessIcon() {
  return (
    <svg width="48" height="48" viewBox="0 0 48 48" fill="none" aria-hidden="true">
      <circle
        cx="24"
        cy="24"
        r="20"
        stroke="var(--cedros-success, #22c55e)"
        strokeWidth="2"
        fill="var(--cedros-success-light, #dcfce7)"
      />
      <path
        d="M16 24l6 6 10-10"
        stroke="var(--cedros-success, #22c55e)"
        strokeWidth="3"
        strokeLinecap="round"
        strokeLinejoin="round"
        fill="none"
      />
    </svg>
  );
}

function ErrorIcon() {
  return (
    <svg width="48" height="48" viewBox="0 0 48 48" fill="none" aria-hidden="true">
      <circle
        cx="24"
        cy="24"
        r="20"
        stroke="var(--cedros-destructive, #ef4444)"
        strokeWidth="2"
        fill="var(--cedros-destructive-light, #fee2e2)"
      />
      <path
        d="M24 16v10M24 30v2"
        stroke="var(--cedros-destructive, #ef4444)"
        strokeWidth="3"
        strokeLinecap="round"
      />
    </svg>
  );
}

function PasskeyIcon() {
  return (
    <svg
      className="cedros-passkey-icon"
      width="48"
      height="48"
      viewBox="0 0 48 48"
      fill="none"
      aria-hidden="true"
    >
      <rect x="8" y="16" width="32" height="24" rx="4" stroke="currentColor" strokeWidth="2" />
      <circle cx="24" cy="28" r="4" stroke="currentColor" strokeWidth="2" />
      <path d="M24 32v4" stroke="currentColor" strokeWidth="2" strokeLinecap="round" />
    </svg>
  );
}
