/**
 * Hook for wallet recovery flow
 *
 * Handles recovery using the BIP-39 mnemonic (recovery phrase):
 *
 * Two recovery modes supported:
 * - full_seed: User enters 12-word full seed mnemonic (portable to other wallets)
 * - share_c_only: User enters 12-word Share C mnemonic (app-locked recovery)
 *
 * Recovery flow:
 * 1. User enters 12-word recovery phrase
 * 2. Validate and decode to seed (or Share C  fetch Share B  combine)
 * 3. Re-derive Solana pubkey (proves ownership)
 * 4. Re-split seed into new Shamir shares
 * 5. Encrypt Share A with new credential
 * 6. Send to server (pubkey match verifies ownership)
 */

import { useState, useCallback, useRef, useEffect } from 'react';
import {
  mnemonicToSeed,
  mnemonicToShare,
  isValidMnemonic,
  combineShares,
  splitSecret,
  generateArgon2Salt,
  generatePrfSalt,
  argon2DeriveInWorker,
  authenticateWithDiscoverablePrf,
  deriveKeyFromPrf,
  aesGcmEncryptToBase64,
  getPublicKeyFromSeed,
  publicKeyToBase58,
  wipeAll,
  toEncryptionKey,
  toShamirShare,
  DEFAULT_KDF_PARAMS,
  base64ToUint8Array,
  uint8ArrayToBase64,
  type Seed,
  type Argon2Salt,
} from '../crypto';
import { useWalletMaterial } from './useWalletMaterial';
import { useWalletDiscovery } from './useWalletDiscovery';
import type {
  UseWalletRecoveryReturn,
  RecoveryState,
  WalletRecoverRequest,
  ShareAAuthMethod,
} from '../types/wallet';

/**
 * Hook for wallet recovery
 *
 * Supports two recovery modes based on server config:
 * - full_seed: User enters the full 12-word seed phrase (portable)
 * - share_c_only: User enters 12-word Share C phrase, server provides Share B (app-locked)
 */
export function useWalletRecovery(): UseWalletRecoveryReturn {
  const { recover, getShareBForRecovery } = useWalletMaterial();
  const { recoveryMode } = useWalletDiscovery();

  const [state, setState] = useState<RecoveryState>({ step: 'idle' });
  const [isRecovering, setIsRecovering] = useState(false);

  // Store sensitive data for cleanup
  const sensitiveData = useRef<Uint8Array[]>([]);

  const cleanup = useCallback(() => {
    wipeAll(...sensitiveData.current);
    sensitiveData.current = [];
  }, []);

  useEffect(() => {
    return () => {
      cleanup();
    };
  }, [cleanup]);

  /**
   * Start and complete recovery in one step
   *
   * @param words - 12-word recovery phrase (full seed or Share C based on mode)
   * @param method - New auth method (password or passkey)
   * @param credential - New password (ignored for passkey)
   */
  const startRecovery = useCallback(
    async (words: string[], method: ShareAAuthMethod, credential: string): Promise<void> => {
      setIsRecovering(true);
      cleanup();

      try {
        // Step 1: Validate mnemonic
        setState({ step: 'validating' });

        if (!isValidMnemonic(words)) {
          throw new Error('Invalid recovery phrase. Please check your words.');
        }

        let seed: Seed;

        if (recoveryMode === 'share_c_only') {
          // Share C mode: decode to Share C, fetch Share B, combine
          const shareC = mnemonicToShare(words);
          sensitiveData.current.push(shareC);

          // Send Share C to server to get Share B
          const shareCBase64 = uint8ArrayToBase64(shareC);
          const response = await getShareBForRecovery({ shareC: shareCBase64 });

          // Decode Share B from response
          const shareB = base64ToUint8Array(response.shareB);
          sensitiveData.current.push(shareB);

          // Combine Share B + Share C to reconstruct seed
          seed = combineShares(toShamirShare(shareB), toShamirShare(shareC));
          sensitiveData.current.push(seed);
        } else {
          // Full seed mode: decode mnemonic directly to seed
          seed = mnemonicToSeed(words);
          sensitiveData.current.push(seed);
        }

        // Step 3: Derive Solana pubkey (proves ownership when server verifies match)
        const publicKey = getPublicKeyFromSeed(seed);
        const solanaPubkey = publicKeyToBase58(publicKey);

        // Step 4: Re-split seed into new shares
        const { shareA, shareB } = splitSecret(seed);
        sensitiveData.current.push(shareA, shareB);

        // Step 5: Encrypt Share A with new credential
        setState({ step: 'encrypting' });

        let encryptionKey: Uint8Array;
        let kdfSalt: Argon2Salt | undefined;
        let prfSaltBase64: string | undefined;

        if (method === 'passkey') {
          // Passkey method: authenticate and derive key from PRF
          const prfSalt = generatePrfSalt();
          prfSaltBase64 = uint8ArrayToBase64(prfSalt);

          const prfResult = await authenticateWithDiscoverablePrf(prfSaltBase64);
          sensitiveData.current.push(prfResult.prfOutput);

          encryptionKey = await deriveKeyFromPrf(prfResult.prfOutput, prfSalt);
          sensitiveData.current.push(encryptionKey);
        } else {
          // Password method: derive key with Argon2id
          kdfSalt = generateArgon2Salt();
          encryptionKey = await argon2DeriveInWorker(credential, kdfSalt, DEFAULT_KDF_PARAMS);
          sensitiveData.current.push(encryptionKey);
        }

        const encryptedA = await aesGcmEncryptToBase64(shareA, toEncryptionKey(encryptionKey));

        // Step 6: Build request and send to server
        setState({ step: 'uploading' });

        const request: WalletRecoverRequest = {
          solanaPubkey,
          shareAAuthMethod: method,
          shareACiphertext: encryptedA.ciphertext,
          shareANonce: encryptedA.nonce,
          shareB: uint8ArrayToBase64(shareB),
        };

        // Add method-specific fields
        if (method === 'password') {
          request.shareAKdfSalt = uint8ArrayToBase64(kdfSalt!);
          request.shareAKdfParams = DEFAULT_KDF_PARAMS;
        }

        if (method === 'passkey') {
          request.prfSalt = prfSaltBase64;
        }

        await recover(request);

        // Success
        cleanup();
        setState({ step: 'complete' });
      } catch (err) {
        cleanup();
        setState({
          step: 'error',
          error: err instanceof Error ? err.message : 'Recovery failed',
        });
      } finally {
        setIsRecovering(false);
      }
    },
    [recover, getShareBForRecovery, recoveryMode, cleanup]
  );

  const cancel = useCallback(() => {
    cleanup();
    setState({ step: 'idle' });
    setIsRecovering(false);
  }, [cleanup]);

  return {
    state,
    startRecovery,
    cancel,
    isRecovering,
  };
}
