/**
 * Wallet unlock component (v2 - server-side signing)
 *
 * Provides the UI for unlocking an enrolled wallet.
 * User enters their credential (password or passkey),
 * server validates and caches the derived key for session signing.
 *
 * No client-side share reconstruction - server handles everything.
 */

import { useState, useCallback, useEffect } from 'react';
import type { FormEvent } from 'react';
import { useWalletMaterial } from '../../hooks/useWalletMaterial';
import { usePasskeySigning } from '../../hooks/useWalletSigning';
import { useCedrosLogin } from '../../context/useCedrosLogin';
import { PasskeyPrompt } from './PasskeyPrompt';
import { LoadingSpinner } from '../shared/LoadingSpinner';
import { PasswordInput } from '../email/PasswordInput';
import type { ShareAAuthMethod, UnlockCredential } from '../../types/wallet';

export interface WalletUnlockProps {
  /** Callback when wallet is successfully unlocked */
  onUnlock?: () => void;
  /** Callback when unlock is cancelled */
  onCancel?: () => void;
  /** Whether to show the cancel button */
  showCancel?: boolean;
  /** The wallet's auth method (if known) */
  authMethod?: ShareAAuthMethod;
  /** Additional CSS classes */
  className?: string;
}

type UnlockPhase = 'idle' | 'credential' | 'unlocking' | 'unlocked' | 'error';

/**
 * Wallet unlock form
 *
 * Simple single-credential flow:
 * - Password users: Enter password
 * - Passkey users: Authenticate with passkey
 *
 * Server validates credential and caches derived key for session signing.
 */
export function WalletUnlock({
  onUnlock,
  onCancel,
  showCancel = true,
  authMethod: propAuthMethod,
  className = '',
}: WalletUnlockProps) {
  useCedrosLogin(); // Ensure user is authenticated
  const { unlock, getMaterial, isLoading } = useWalletMaterial();
  const { getPasskeyCredential, isAuthenticating } = usePasskeySigning();

  const [phase, setPhase] = useState<UnlockPhase>('idle');
  const [authMethod, setAuthMethod] = useState<ShareAAuthMethod | null>(propAuthMethod ?? null);
  const [credential, setCredential] = useState('');
  const [error, setError] = useState<string | null>(null);

  // MAINT-01: Sync authMethod state when prop changes
  useEffect(() => {
    if (propAuthMethod !== undefined) {
      setAuthMethod(propAuthMethod);
    }
  }, [propAuthMethod]);

  // Determine credential type based on auth method
  const isPasswordAuth = authMethod === 'password';
  const isPasskeyAuth = authMethod === 'passkey';

  // Start the unlock flow - fetch material to get auth method
  const startUnlock = useCallback(async () => {
    setPhase('credential');
    setError(null);

    if (!authMethod) {
      try {
        const material = await getMaterial();
        if (material) {
          setAuthMethod(material.shareAAuthMethod);
        } else {
          setError('No wallet enrolled');
          setPhase('error');
        }
      } catch (err) {
        setError(err instanceof Error ? err.message : 'Failed to get wallet info');
        setPhase('error');
      }
    }
  }, [authMethod, getMaterial]);

  // Handle credential submission
  const handleSubmit = useCallback(
    async (e: FormEvent) => {
      e.preventDefault();
      setError(null);
      setPhase('unlocking');

      try {
        let unlockCredential: UnlockCredential;

        if (isPasswordAuth) {
          unlockCredential = { type: 'password', password: credential };
        } else {
          throw new Error('Invalid auth method');
        }

        await unlock(unlockCredential);
        setPhase('unlocked');
        onUnlock?.();
      } catch (err) {
        setError(err instanceof Error ? err.message : 'Failed to unlock wallet');
        setPhase('error');
      }
    },
    [isPasswordAuth, credential, unlock, onUnlock]
  );

  // Handle passkey authentication
  const handlePasskeyUnlock = useCallback(async () => {
    setError(null);
    setPhase('unlocking');

    try {
      const passkeyCredential = await getPasskeyCredential();
      if (!passkeyCredential) {
        setPhase('credential');
        return;
      }

      await unlock(passkeyCredential);
      setPhase('unlocked');
      onUnlock?.();
    } catch (err) {
      setError(err instanceof Error ? err.message : 'Failed to unlock wallet');
      setPhase('error');
    }
  }, [getPasskeyCredential, unlock, onUnlock]);

  // Handle cancel
  const handleCancel = useCallback(() => {
    setCredential('');
    setPhase('idle');
    setError(null);
    onCancel?.();
  }, [onCancel]);

  // Retry from error state
  const handleRetry = useCallback(() => {
    setCredential('');
    setPhase('credential');
    setError(null);
  }, []);

  const isProcessing = isLoading || isAuthenticating;

  // Render based on phase
  const renderContent = () => {
    switch (phase) {
      case 'idle':
        return (
          <div className="cedros-wallet-unlock-idle">
            <div className="cedros-wallet-unlock-icon">
              <LockIcon />
            </div>
            <h3 className="cedros-wallet-unlock-title">Wallet Locked</h3>
            <p className="cedros-wallet-unlock-description">
              Unlock your wallet to sign transactions.
            </p>
            <button
              type="button"
              className="cedros-button cedros-button-primary"
              onClick={startUnlock}
            >
              Unlock Wallet
            </button>
          </div>
        );

      case 'credential':
        // Password form
        if (isPasswordAuth) {
          return (
            <form className="cedros-wallet-unlock-form" onSubmit={handleSubmit}>
              <h3 className="cedros-wallet-unlock-title">Enter Password</h3>
              <p className="cedros-wallet-unlock-description">
                Enter your wallet password to unlock.
              </p>

              <PasswordInput
                label="Password"
                value={credential}
                onChange={(e) => setCredential(e.target.value)}
                disabled={isProcessing}
                autoComplete="current-password"
                error={error ?? undefined}
              />

              <div className="cedros-wallet-unlock-actions">
                <button
                  type="submit"
                  className="cedros-button cedros-button-primary"
                  disabled={isProcessing || credential.length === 0}
                >
                  {isProcessing ? 'Unlocking...' : 'Unlock'}
                </button>
                {showCancel && (
                  <button
                    type="button"
                    className="cedros-button cedros-button-secondary"
                    onClick={handleCancel}
                    disabled={isProcessing}
                  >
                    Cancel
                  </button>
                )}
              </div>
            </form>
          );
        }

        // Passkey authentication
        if (isPasskeyAuth) {
          return (
            <div className="cedros-wallet-unlock-passkey">
              <h3 className="cedros-wallet-unlock-title">Verify with Passkey</h3>
              <p className="cedros-wallet-unlock-description">
                Use your passkey to unlock your wallet.
              </p>

              <PasskeyPrompt
                mode="authenticate"
                isLoading={isProcessing}
                error={error ?? undefined}
                onPrompt={handlePasskeyUnlock}
                onRetry={handlePasskeyUnlock}
                onCancel={showCancel ? handleCancel : undefined}
              />
            </div>
          );
        }

        // Loading auth method
        return (
          <div className="cedros-wallet-unlock-loading">
            <LoadingSpinner size="xl" />
            <p>Loading wallet info...</p>
          </div>
        );

      case 'unlocking':
        return (
          <div className="cedros-wallet-unlock-progress">
            <div className="cedros-wallet-unlock-icon">
              <LoadingSpinner size="xl" />
            </div>
            <h3 className="cedros-wallet-unlock-title">Unlocking Wallet</h3>
            <p className="cedros-wallet-unlock-description">Verifying your credentials...</p>
          </div>
        );

      case 'unlocked':
        return (
          <div className="cedros-wallet-unlock-success">
            <div className="cedros-wallet-unlock-icon">
              <SuccessIcon />
            </div>
            <h3 className="cedros-wallet-unlock-title">Wallet Unlocked</h3>
            <p className="cedros-wallet-unlock-description">
              Your wallet is ready to sign transactions.
            </p>
          </div>
        );

      case 'error':
        return (
          <div className="cedros-wallet-unlock-error">
            <div className="cedros-wallet-unlock-icon">
              <ErrorIcon />
            </div>
            <h3 className="cedros-wallet-unlock-title">Unlock Failed</h3>
            <p className="cedros-wallet-unlock-description">
              {error ?? 'Failed to unlock wallet. Please try again.'}
            </p>
            <div className="cedros-wallet-unlock-actions">
              <button
                type="button"
                className="cedros-button cedros-button-primary"
                onClick={handleRetry}
              >
                Try Again
              </button>
              {showCancel && (
                <button
                  type="button"
                  className="cedros-button cedros-button-secondary"
                  onClick={handleCancel}
                >
                  Cancel
                </button>
              )}
            </div>
          </div>
        );
    }
  };

  return <div className={`cedros-wallet-unlock ${className}`}>{renderContent()}</div>;
}

// Icon components
function LockIcon() {
  return (
    <svg width="48" height="48" viewBox="0 0 48 48" fill="none" aria-hidden="true">
      <rect
        x="10"
        y="22"
        width="28"
        height="20"
        rx="4"
        stroke="currentColor"
        strokeWidth="2"
        fill="var(--cedros-muted, #f3f4f6)"
      />
      <path d="M16 22V16a8 8 0 1 1 16 0v6" stroke="currentColor" strokeWidth="2" fill="none" />
      <circle cx="24" cy="32" r="3" fill="currentColor" />
    </svg>
  );
}

function SuccessIcon() {
  return (
    <svg width="48" height="48" viewBox="0 0 48 48" fill="none" aria-hidden="true">
      <circle
        cx="24"
        cy="24"
        r="20"
        stroke="var(--cedros-success, #22c55e)"
        strokeWidth="2"
        fill="var(--cedros-success-light, #dcfce7)"
      />
      <path
        d="M16 24l6 6 10-10"
        stroke="var(--cedros-success, #22c55e)"
        strokeWidth="3"
        strokeLinecap="round"
        strokeLinejoin="round"
        fill="none"
      />
    </svg>
  );
}

function ErrorIcon() {
  return (
    <svg width="48" height="48" viewBox="0 0 48 48" fill="none" aria-hidden="true">
      <circle
        cx="24"
        cy="24"
        r="20"
        stroke="var(--cedros-destructive, #ef4444)"
        strokeWidth="2"
        fill="var(--cedros-destructive-light, #fee2e2)"
      />
      <path
        d="M24 16v10M24 30v2"
        stroke="var(--cedros-destructive, #ef4444)"
        strokeWidth="3"
        strokeLinecap="round"
      />
    </svg>
  );
}
