import { useCallback, useEffect, useMemo, useRef, useState } from 'react';
import { useCedrosLoginOptional } from '../../context/useCedrosLogin';

export interface WalletAddressRowProps {
  address: string;
  label?: string;
  showCopy?: boolean;
  showExplorerLink?: boolean;
  /** If address is long, show a truncated preview with a reveal toggle. */
  allowReveal?: boolean;
  className?: string;
}

export function WalletAddressRow({
  address,
  label = 'Wallet Address',
  showCopy = true,
  showExplorerLink = true,
  allowReveal = true,
  className = '',
}: WalletAddressRowProps) {
  const context = useCedrosLoginOptional();
  const [copied, setCopied] = useState(false);
  const [copyError, setCopyError] = useState<string | null>(null);
  const [revealFull, setRevealFull] = useState(false);
  const copyTimerRef = useRef<number | null>(null);

  const network = context?.config.solana?.network ?? 'mainnet-beta';
  const explorerHref = useMemo(() => {
    const base = `https://explorer.solana.com/address/${address}`;
    if (network === 'mainnet-beta') return base;
    return `${base}?cluster=${encodeURIComponent(network)}`;
  }, [address, network]);

  const isTruncatable = allowReveal && address.length > 18;

  const displayAddress = useMemo(() => {
    if (!isTruncatable || revealFull) return address;
    return `${address.slice(0, 8)}...${address.slice(-8)}`;
  }, [address, isTruncatable, revealFull]);

  const handleCopy = useCallback(async () => {
    try {
      setCopyError(null);
      await navigator.clipboard.writeText(address);
      setCopied(true);

      if (copyTimerRef.current !== null) {
        window.clearTimeout(copyTimerRef.current);
      }
      copyTimerRef.current = window.setTimeout(() => {
        setCopied(false);
        copyTimerRef.current = null;
      }, 2000);
    } catch (err) {
      setCopied(false);
      setCopyError(err instanceof Error ? err.message : 'Copy failed');
    }
  }, [address]);

  useEffect(() => {
    return () => {
      if (copyTimerRef.current !== null) {
        window.clearTimeout(copyTimerRef.current);
        copyTimerRef.current = null;
      }
    };
  }, []);

  return (
    <div className={`cedros-wallet-address-row ${className}`}>
      <div className="cedros-wallet-address-row-header">
        <span className="cedros-wallet-status-pubkey-label">{label}</span>
        <div className="cedros-wallet-address-row-actions">
          {isTruncatable && (
            <button
              type="button"
              className="cedros-button cedros-button-sm cedros-button-ghost"
              onClick={() => setRevealFull((v) => !v)}
              aria-label={revealFull ? 'Hide full wallet address' : 'Show full wallet address'}
            >
              {revealFull ? (
                <svg width="16" height="16" viewBox="0 0 24 24" fill="none" aria-hidden="true">
                  <path
                    d="M3 3l18 18"
                    stroke="currentColor"
                    strokeWidth="2"
                    strokeLinecap="round"
                    strokeLinejoin="round"
                  />
                  <path
                    d="M10.7 10.7a3 3 0 0 0 4.24 4.24"
                    stroke="currentColor"
                    strokeWidth="2"
                    strokeLinecap="round"
                    strokeLinejoin="round"
                  />
                  <path
                    d="M9.88 5.09A10.94 10.94 0 0 1 12 5c5 0 9.27 3.11 11 7-0.55 1.23-1.32 2.33-2.27 3.25"
                    stroke="currentColor"
                    strokeWidth="2"
                    strokeLinecap="round"
                    strokeLinejoin="round"
                  />
                  <path
                    d="M6.61 6.61C4.4 7.9 2.74 9.8 1 12c1.73 3.89 6 7 11 7 1.14 0 2.25-0.16 3.3-0.46"
                    stroke="currentColor"
                    strokeWidth="2"
                    strokeLinecap="round"
                    strokeLinejoin="round"
                  />
                </svg>
              ) : (
                <svg width="16" height="16" viewBox="0 0 24 24" fill="none" aria-hidden="true">
                  <path
                    d="M1 12s4-7 11-7 11 7 11 7-4 7-11 7S1 12 1 12Z"
                    stroke="currentColor"
                    strokeWidth="2"
                    strokeLinecap="round"
                    strokeLinejoin="round"
                  />
                  <path
                    d="M12 15a3 3 0 1 0 0-6 3 3 0 0 0 0 6Z"
                    stroke="currentColor"
                    strokeWidth="2"
                    strokeLinecap="round"
                    strokeLinejoin="round"
                  />
                </svg>
              )}
            </button>
          )}
          {showExplorerLink && (
            <a
              className="cedros-button cedros-button-sm cedros-button-ghost"
              href={explorerHref}
              target="_blank"
              rel="noreferrer"
            >
              Explorer
            </a>
          )}
          {showCopy && (
            <button
              type="button"
              className="cedros-button cedros-button-sm cedros-button-outline"
              onClick={handleCopy}
              aria-label="Copy wallet address"
            >
              {copied ? 'Copied' : 'Copy'}
            </button>
          )}
        </div>
      </div>
      <code className="cedros-wallet-status-pubkey-value" title={address}>
        {displayAddress}
      </code>
      {copyError && (
        <p className="cedros-input-hint" role="status">
          {copyError}
        </p>
      )}
    </div>
  );
}
