/**
 * Hook for Privacy Cash deposit operations
 *
 * Handles depositing SOL to the user's Privacy Cash account.
 * Requires an unlocked SSS wallet with no-recovery mode.
 *
 * Safe to call outside CedrosLoginProvider (returns no-op functions).
 */

import { useState, useCallback, useMemo } from 'react';
import { useCedrosLoginOptional } from '../context/useCedrosLogin';
import { ApiClient, handleApiError } from '../utils/apiClient';
import type {
  UseDepositReturn,
  DepositResponse,
  DepositStatusResponse,
  DepositConfigResponse,
  DepositListResponse,
  DepositQuoteResponse,
  PublicDepositRequest,
  MicroDepositRequest,
  TieredDepositResponse,
} from '../types/deposit';

export type { UseDepositReturn } from '../types/deposit';

/**
 * Hook for Privacy Cash deposit operations
 *
 * Deposits go to the user's Privacy Cash account (user's pubkey is owner).
 * Credits are issued immediately, withdrawal to company wallet happens later.
 *
 * Requirements:
 * - User must have SSS wallet enrolled
 * - Wallet must be unlocked (call POST /wallet/unlock first)
 * - Wallet must be in "no recovery" mode
 *
 * Safe to call outside CedrosLoginProvider - returns no-op functions that throw.
 */
export function useDeposit(): UseDepositReturn {
  const context = useCedrosLoginOptional();
  const [isLoading, setIsLoading] = useState(false);
  const [error, setError] = useState<string | null>(null);

  const apiClient = useMemo(() => {
    if (!context) return null;
    return new ApiClient({
      baseUrl: context.config.serverUrl,
      timeoutMs: context.config.requestTimeout,
      retryAttempts: context.config.retryAttempts,
      getAccessToken: context._internal?.getAccessToken,
    });
  }, [context]);

  const clearError = useCallback(() => {
    setError(null);
  }, []);

  const deposit = useCallback(
    async (amountLamports: number): Promise<DepositResponse> => {
      if (!apiClient) {
        throw new Error('useDeposit must be used within a CedrosLoginProvider');
      }
      // UI-05: Validate amount client-side before sending to the API.
      if (!Number.isInteger(amountLamports) || amountLamports <= 0) {
        const err = new Error(
          `Invalid deposit amount: ${amountLamports}. Must be a positive integer (lamports).`
        );
        setError(err.message);
        throw err;
      }
      setIsLoading(true);
      setError(null);

      try {
        const data = await apiClient.post<DepositResponse>('/deposit', {
          amount_lamports: amountLamports,
        });
        return data;
      } catch (err) {
        const apiError = handleApiError(err, 'Failed to execute deposit');
        setError(apiError.message);
        throw apiError;
      } finally {
        setIsLoading(false);
      }
    },
    [apiClient]
  );

  const getStatus = useCallback(
    async (sessionId: string): Promise<DepositStatusResponse> => {
      if (!apiClient) {
        throw new Error('useDeposit must be used within a CedrosLoginProvider');
      }
      setIsLoading(true);
      setError(null);

      try {
        const data = await apiClient.get<DepositStatusResponse>(`/deposit/status/${encodeURIComponent(sessionId)}`);
        return data;
      } catch (err) {
        const apiError = handleApiError(err, 'Failed to get deposit status');
        setError(apiError.message);
        throw apiError;
      } finally {
        setIsLoading(false);
      }
    },
    [apiClient]
  );

  const getConfig = useCallback(async (): Promise<DepositConfigResponse> => {
    if (!apiClient) {
      throw new Error('useDeposit must be used within a CedrosLoginProvider');
    }
    setIsLoading(true);
    setError(null);

    try {
      const data = await apiClient.get<DepositConfigResponse>('/deposit/config');
      return data;
    } catch (err) {
      const apiError = handleApiError(err, 'Failed to get deposit config');
      setError(apiError.message);
      throw apiError;
    } finally {
      setIsLoading(false);
    }
  }, [apiClient]);

  const listDeposits = useCallback(
    async (options?: { limit?: number; offset?: number }): Promise<DepositListResponse> => {
      if (!apiClient) {
        throw new Error('useDeposit must be used within a CedrosLoginProvider');
      }
      setIsLoading(true);
      setError(null);

      try {
        const params = new URLSearchParams();
        if (options?.limit !== undefined) params.set('limit', String(options.limit));
        if (options?.offset !== undefined) params.set('offset', String(options.offset));
        const query = params.toString();
        const url = query ? `/deposits?${query}` : '/deposits';
        const data = await apiClient.get<DepositListResponse>(url);
        return data;
      } catch (err) {
        const apiError = handleApiError(err, 'Failed to list deposits');
        setError(apiError.message);
        throw apiError;
      } finally {
        setIsLoading(false);
      }
    },
    [apiClient]
  );

  const getQuote = useCallback(
    async (params: {
      inputMint: string;
      amount: number;
      taker: string;
    }): Promise<DepositQuoteResponse> => {
      if (!apiClient) {
        throw new Error('useDeposit must be used within a CedrosLoginProvider');
      }
      setIsLoading(true);
      setError(null);

      try {
        const queryParams = new URLSearchParams({
          input_mint: params.inputMint,
          amount: String(params.amount),
          taker: params.taker,
        });
        const data = await apiClient.get<DepositQuoteResponse>(`/deposit/quote?${queryParams}`);
        return data;
      } catch (err) {
        const apiError = handleApiError(err, 'Failed to get deposit quote');
        setError(apiError.message);
        throw apiError;
      } finally {
        setIsLoading(false);
      }
    },
    [apiClient]
  );

  const publicDeposit = useCallback(
    async (request: PublicDepositRequest): Promise<TieredDepositResponse> => {
      if (!apiClient) {
        throw new Error('useDeposit must be used within a CedrosLoginProvider');
      }
      setIsLoading(true);
      setError(null);

      try {
        const data = await apiClient.post<TieredDepositResponse>('/deposit/public', request);
        return data;
      } catch (err) {
        const apiError = handleApiError(err, 'Failed to execute public deposit');
        setError(apiError.message);
        throw apiError;
      } finally {
        setIsLoading(false);
      }
    },
    [apiClient]
  );

  const microDeposit = useCallback(
    async (request: MicroDepositRequest): Promise<TieredDepositResponse> => {
      if (!apiClient) {
        throw new Error('useDeposit must be used within a CedrosLoginProvider');
      }
      setIsLoading(true);
      setError(null);

      try {
        const data = await apiClient.post<TieredDepositResponse>('/deposit/micro', request);
        return data;
      } catch (err) {
        const apiError = handleApiError(err, 'Failed to execute micro deposit');
        setError(apiError.message);
        throw apiError;
      } finally {
        setIsLoading(false);
      }
    },
    [apiClient]
  );

  return {
    deposit,
    getQuote,
    publicDeposit,
    microDeposit,
    getStatus,
    getConfig,
    listDeposits,
    isLoading,
    error,
    clearError,
  };
}
