import type { DepositConfigResponse, DepositTier } from '../../types/deposit';

/**
 * Determine which tier an amount falls into
 *
 * When private deposits are disabled (recovery mode enabled), the 'private'
 * tier is not available and amounts that would qualify for it fall back to 'public'.
 */
export function getTierForAmount(usd: number, config: DepositConfigResponse): DepositTier {
  // Skip private tier if recovery mode is enabled (privateDepositsEnabled is false)
  if (config.privateDepositsEnabled && usd >= config.privateMinUsd) return 'private';
  if (usd >= config.publicMinUsd) return 'public';
  return 'sol_micro';
}
