/**
 * History component
 *
 * Shows all account activity in a tabbed interface:
 * - All: Everything in chronological order
 * - Deposits: Incoming deposits with status tracking
 * - Usage: Service charges and spending
 * - Adjustments: Refunds, bonuses, and admin adjustments
 */

import { useState, useEffect, useCallback, useMemo } from 'react';
import { useCredits } from '../../hooks/useCredits';
import type { CreditTransactionResponse, CreditHistoryResponse } from '../../types/deposit';

/** History tab category for filtering */
export type HistoryCategory = 'all' | 'deposits' | 'usage' | 'adjustments';

/** Tab configuration */
interface TabConfig {
  key: HistoryCategory;
  label: string;
  txTypes: string[] | null; // null means show all
}

const TABS: TabConfig[] = [
  { key: 'all', label: 'All', txTypes: null },
  { key: 'deposits', label: 'Deposits', txTypes: ['deposit'] },
  { key: 'usage', label: 'Usage', txTypes: ['spend', 'usage', 'charge'] },
  {
    key: 'adjustments',
    label: 'Adjustments',
    txTypes: ['refund', 'adjustment', 'bonus', 'credit'],
  },
];

export interface HistoryProps {
  /** Initially selected tab (default: 'all') */
  defaultTab?: HistoryCategory;
  /** Number of items per page (default: 10) */
  pageSize?: number;
  /** Auto-refresh interval in milliseconds (0 to disable) */
  refreshInterval?: number;
  /** Additional CSS classes */
  className?: string;
  /** Callback when history is loaded */
  onLoad?: (history: CreditHistoryResponse) => void;
  /** Callback when a transaction is clicked */
  onTransactionClick?: (transaction: CreditTransactionResponse) => void;
}

/**
 * Format amount for display
 */
function formatAmount(lamports: number, currency: string): string {
  const isNegative = lamports < 0;
  const absLamports = Math.abs(lamports);

  // For stablecoins (6 decimals), show as dollars
  // For SOL (9 decimals), show as SOL
  const isSol = currency.toUpperCase() === 'SOL';
  const decimals = isSol ? 9 : 6;
  const amount = absLamports / Math.pow(10, decimals);

  const prefix = isNegative ? '-' : '+';
  if (isSol) {
    return `${prefix}${amount.toFixed(4)} SOL`;
  }
  return `${prefix}$${amount.toFixed(2)}`;
}

/**
 * Format date for display
 */
function formatDate(dateString: string): string {
  const date = new Date(dateString);
  const now = new Date();
  const diffMs = now.getTime() - date.getTime();
  const diffDays = Math.floor(diffMs / (1000 * 60 * 60 * 24));

  // Show relative time for recent transactions
  if (diffDays === 0) {
    const diffHours = Math.floor(diffMs / (1000 * 60 * 60));
    if (diffHours === 0) {
      const diffMins = Math.floor(diffMs / (1000 * 60));
      if (diffMins < 1) return 'Just now';
      return `${diffMins}m ago`;
    }
    return `${diffHours}h ago`;
  }
  if (diffDays === 1) return 'Yesterday';
  if (diffDays < 7) return `${diffDays}d ago`;

  // Show full date for older transactions
  return date.toLocaleDateString(undefined, {
    month: 'short',
    day: 'numeric',
    year: date.getFullYear() !== now.getFullYear() ? 'numeric' : undefined,
  });
}

/**
 * Get display label for transaction type
 */
function getTypeLabel(txType: string | undefined): string {
  if (!txType) return 'Transaction';
  const labels: Record<string, string> = {
    deposit: 'Deposit',
    spend: 'Usage',
    usage: 'Usage',
    charge: 'Charge',
    refund: 'Refund',
    adjustment: 'Adjustment',
    bonus: 'Bonus',
    credit: 'Credit',
  };
  return labels[txType.toLowerCase()] || txType;
}

/**
 * Get icon for transaction type
 */
function getTypeIcon(txType: string | undefined, isPositive: boolean): string {
  const type = (txType || '').toLowerCase();
  if (type === 'deposit') return '';
  if (type === 'spend' || type === 'usage' || type === 'charge') return '';
  if (type === 'refund') return '';
  if (type === 'bonus' || type === 'credit') return '';
  if (type === 'adjustment') return isPositive ? '+' : '';
  return isPositive ? '+' : '';
}

/**
 * History component with tabs
 */
export function History({
  defaultTab = 'all',
  pageSize = 10,
  refreshInterval = 0,
  className = '',
  onLoad,
  onTransactionClick,
}: HistoryProps) {
  const { getHistory, isLoading, error, clearError } = useCredits();

  const [activeTab, setActiveTab] = useState<HistoryCategory>(defaultTab);
  const [transactions, setTransactions] = useState<CreditTransactionResponse[]>([]);
  const [total, setTotal] = useState(0);
  const [offset, setOffset] = useState(0);
  const [loadError, setLoadError] = useState<string | null>(null);

  // Get current tab config
  const currentTab = TABS.find((t) => t.key === activeTab) || TABS[0];

  // Filter transactions based on active tab (useMemo to avoid extra renders)
  const filteredTransactions = useMemo(() => {
    if (currentTab.txTypes === null) {
      return transactions;
    }
    return transactions.filter((tx) => {
      const txType = tx.txType || '';
      return currentTab.txTypes!.some((type) => txType.toLowerCase().includes(type.toLowerCase()));
    });
  }, [transactions, currentTab.txTypes]);

  const fetchHistory = useCallback(async () => {
    try {
      // Fetch more than pageSize to allow client-side filtering
      const result = await getHistory({ limit: pageSize * 3, offset });
      setTransactions(result.transactions);
      setTotal(result.total);
      onLoad?.(result);
      setLoadError(null);
    } catch (err) {
      setLoadError(err instanceof Error ? err.message : 'Failed to load history');
    }
  }, [pageSize, offset, getHistory, onLoad]);

  // Reset offset when tab changes
  useEffect(() => {
    setOffset(0);
  }, [activeTab]);

  // Fetch on mount and when dependencies change
  useEffect(() => {
    fetchHistory();
  }, [fetchHistory]);

  // Auto-refresh
  useEffect(() => {
    if (refreshInterval <= 0) return;
    const interval = setInterval(fetchHistory, refreshInterval);
    return () => clearInterval(interval);
  }, [refreshInterval, fetchHistory]);

  const totalPages = Math.ceil(total / pageSize);
  const currentPage = Math.floor(offset / pageSize) + 1;

  const goToPage = (page: number) => {
    const newOffset = (page - 1) * pageSize;
    setOffset(Math.max(0, Math.min(newOffset, Math.max(0, total - 1))));
  };

  // Handle tab change
  const handleTabChange = (tab: HistoryCategory) => {
    setActiveTab(tab);
  };

  // Error state
  if (loadError || error) {
    return (
      <div className={`cedros-tx-history cedros-tx-history-error ${className}`}>
        <p className="cedros-tx-error">{loadError || error}</p>
        <button
          type="button"
          className="cedros-tx-retry"
          onClick={() => {
            clearError();
            setLoadError(null);
            fetchHistory();
          }}
        >
          Retry
        </button>
      </div>
    );
  }

  // Loading state (initial load only)
  if (isLoading && transactions.length === 0) {
    return (
      <div className={`cedros-tx-history cedros-tx-history-loading ${className}`}>
        <span className="cedros-tx-loading-indicator" />
        <span className="cedros-tx-loading-text">Loading transactions...</span>
      </div>
    );
  }

  // Get count for each tab
  const getTabCount = (tab: TabConfig): number => {
    if (tab.txTypes === null) return transactions.length;
    return transactions.filter((tx) => {
      const txType = tx.txType || '';
      return tab.txTypes!.some((type) => txType.toLowerCase().includes(type.toLowerCase()));
    }).length;
  };

  return (
    <div className={`cedros-tx-history ${className}`}>
      {/* Header */}
      <div className="cedros-tx-header">
        <h4 className="cedros-tx-title">Transaction History</h4>
        <button
          type="button"
          className="cedros-tx-refresh"
          onClick={fetchHistory}
          disabled={isLoading}
          title="Refresh"
        >
          {isLoading ? '...' : ''}
        </button>
      </div>

      {/* Tabs */}
      <div className="cedros-tx-tabs">
        {TABS.map((tab) => {
          const count = getTabCount(tab);
          const isActive = activeTab === tab.key;
          return (
            <button
              key={tab.key}
              type="button"
              className={`cedros-tx-tab ${isActive ? 'cedros-tx-tab-active' : ''}`}
              onClick={() => handleTabChange(tab.key)}
            >
              {tab.label}
              {count > 0 && <span className="cedros-tx-tab-count">{count}</span>}
            </button>
          );
        })}
      </div>

      {/* Transaction List */}
      {filteredTransactions.length === 0 ? (
        <div className="cedros-tx-empty">
          <p className="cedros-tx-empty-message">
            {activeTab === 'all'
              ? 'No transactions yet.'
              : `No ${currentTab.label.toLowerCase()} found.`}
          </p>
          {activeTab === 'all' && (
            <p className="cedros-tx-empty-hint">Make a deposit to get started.</p>
          )}
        </div>
      ) : (
        <>
          <div className="cedros-tx-list">
            {filteredTransactions.slice(0, pageSize).map((tx) => {
              const isPositive = tx.amountLamports >= 0;
              return (
                <div
                  key={tx.id}
                  className={`cedros-tx-item ${isPositive ? 'cedros-tx-item-positive' : 'cedros-tx-item-negative'}`}
                  onClick={() => onTransactionClick?.(tx)}
                  onKeyDown={(e) => {
                    if (e.key === 'Enter' || e.key === ' ') {
                      e.preventDefault();
                      onTransactionClick?.(tx);
                    }
                  }}
                  role={onTransactionClick ? 'button' : undefined}
                  tabIndex={onTransactionClick ? 0 : undefined}
                >
                  {/* Icon */}
                  <div
                    className={`cedros-tx-icon ${isPositive ? 'cedros-tx-icon-positive' : 'cedros-tx-icon-negative'}`}
                  >
                    {getTypeIcon(tx.txType, isPositive)}
                  </div>

                  {/* Main content */}
                  <div className="cedros-tx-content">
                    <div className="cedros-tx-row-main">
                      <span className="cedros-tx-type">{getTypeLabel(tx.txType)}</span>
                      <span
                        className={`cedros-tx-amount ${isPositive ? 'cedros-tx-amount-positive' : 'cedros-tx-amount-negative'}`}
                      >
                        {formatAmount(tx.amountLamports, tx.currency)}
                      </span>
                    </div>
                    <div className="cedros-tx-row-details">
                      <span className="cedros-tx-description">{tx.description}</span>
                      <span className="cedros-tx-date">{formatDate(tx.createdAt)}</span>
                    </div>
                  </div>
                </div>
              );
            })}
          </div>

          {/* Pagination */}
          {totalPages > 1 && (
            <div className="cedros-tx-pagination">
              <button
                type="button"
                className="cedros-tx-page-btn"
                onClick={() => goToPage(currentPage - 1)}
                disabled={currentPage <= 1}
              >
                Previous
              </button>
              <span className="cedros-tx-page-info">
                Page {currentPage} of {totalPages}
              </span>
              <button
                type="button"
                className="cedros-tx-page-btn"
                onClick={() => goToPage(currentPage + 1)}
                disabled={currentPage >= totalPages}
              >
                Next
              </button>
            </div>
          )}
        </>
      )}
    </div>
  );
}
