/**
 * Organization switcher modal component
 *
 * Refactored to extract sub-components (F-02):
 * - CreateOrgForm -> ./CreateOrgForm.tsx
 * - OrgListView -> ./OrgListView.tsx
 * - OrgAvatar -> ./OrgAvatar.tsx
 * - Icons -> ./icons.tsx
 */
import { useState, useCallback, useEffect, useRef } from 'react';
import type { OrgWithMembership, CreateOrgRequest, DisplayError } from '../../types';
import { ErrorMessage } from '../shared/ErrorMessage';
import { OrgListView } from './OrgListView';
import { CreateOrgForm } from './CreateOrgForm';
import { CloseIcon } from './icons';

export interface OrgSwitcherProps {
  /** Whether the modal is open */
  isOpen: boolean;
  /** Callback to close the modal */
  onClose: () => void;
  /** List of organizations */
  orgs: OrgWithMembership[];
  /** Currently active organization */
  activeOrg: OrgWithMembership | null;
  /** Loading state */
  isLoading?: boolean;
  /** Error message */
  error?: DisplayError;
  /** Callback when organization is selected */
  onSelect: (orgId: string) => void;
  /** Callback when new organization is created */
  onCreate?: (data: CreateOrgRequest) => Promise<void>;
  /** Additional CSS class */
  className?: string;
}

type ViewMode = 'list' | 'create';

/**
 * Modal for switching organizations and creating new ones.
 *
 * @example
 * ```tsx
 * function App() {
 *   const [isOpen, setIsOpen] = useState(false);
 *   const { orgs, activeOrg, switchOrg, createOrg } = useOrgs();
 *
 *   return (
 *     <>
 *       <button onClick={() => setIsOpen(true)}>Switch org</button>
 *       <OrgSwitcher
 *         isOpen={isOpen}
 *         onClose={() => setIsOpen(false)}
 *         orgs={orgs}
 *         activeOrg={activeOrg}
 *         onSelect={switchOrg}
 *         onCreate={createOrg}
 *       />
 *     </>
 *   );
 * }
 * ```
 */
export function OrgSwitcher({
  isOpen,
  onClose,
  orgs,
  activeOrg,
  isLoading = false,
  error,
  onSelect,
  onCreate,
  className = '',
}: OrgSwitcherProps) {
  // Render null when closed - inner content unmounts, resetting state naturally
  if (!isOpen) return null;

  return (
    <OrgSwitcherModal
      onClose={onClose}
      orgs={orgs}
      activeOrg={activeOrg}
      isLoading={isLoading}
      error={error}
      onSelect={onSelect}
      onCreate={onCreate}
      className={className}
    />
  );
}

// Inner modal component - unmounts when modal closes, resetting viewMode state
function OrgSwitcherModal({
  onClose,
  orgs,
  activeOrg,
  isLoading = false,
  error,
  onSelect,
  onCreate,
  className,
}: Omit<OrgSwitcherProps, 'isOpen'>) {
  const [viewMode, setViewMode] = useState<ViewMode>('list');
  const modalRef = useRef<HTMLDivElement>(null);
  // UI-05: Store previously focused element for restoration
  const previousFocusRef = useRef<HTMLElement | null>(null);

  // UI-05: Focus trap and restoration
  useEffect(() => {
    // Save currently focused element
    previousFocusRef.current = document.activeElement as HTMLElement;

    // Focus the close button when modal opens
    const closeButton = modalRef.current?.querySelector<HTMLElement>('.cedros-modal-close');
    closeButton?.focus();

    return () => {
      // Restore focus when modal closes
      previousFocusRef.current?.focus();
    };
  }, []);

  // Close on escape key - runs unconditionally since component only mounts when modal is open
  useEffect(() => {
    const handleKeyDown = (event: KeyboardEvent) => {
      if (event.key === 'Escape') {
        onClose();
        return;
      }

      // UI-05: Focus trap - Tab/Shift+Tab within modal
      if (event.key === 'Tab' && modalRef.current) {
        const focusableElements = modalRef.current.querySelectorAll<HTMLElement>(
          'button:not([disabled]), [href], input:not([disabled]), select:not([disabled]), textarea:not([disabled]), [tabindex]:not([tabindex="-1"])'
        );
        const firstElement = focusableElements[0];
        const lastElement = focusableElements[focusableElements.length - 1];

        if (event.shiftKey) {
          // Shift+Tab: wrap from first to last
          if (document.activeElement === firstElement) {
            event.preventDefault();
            lastElement?.focus();
          }
        } else {
          // Tab: wrap from last to first
          if (document.activeElement === lastElement) {
            event.preventDefault();
            firstElement?.focus();
          }
        }
      }
    };

    document.addEventListener('keydown', handleKeyDown);
    return () => document.removeEventListener('keydown', handleKeyDown);
  }, [onClose]);

  // Handle backdrop click
  const handleBackdropClick = useCallback(
    (event: React.MouseEvent) => {
      if (event.target === event.currentTarget) {
        onClose();
      }
    },
    [onClose]
  );

  const handleSelect = useCallback(
    (orgId: string) => {
      onSelect(orgId);
      onClose();
    },
    [onSelect, onClose]
  );

  const handleCreate = useCallback(
    async (data: CreateOrgRequest) => {
      await onCreate?.(data);
      onClose();
    },
    [onCreate, onClose]
  );

  return (
    <div className="cedros-modal-backdrop" onClick={handleBackdropClick}>
      <div
        ref={modalRef}
        className={`cedros-modal cedros-org-switcher ${className}`}
        role="dialog"
        aria-modal="true"
        aria-labelledby="org-switcher-title"
      >
        <div className="cedros-modal-header">
          <h2 id="org-switcher-title" className="cedros-modal-title">
            {viewMode === 'list' ? 'Switch Organization' : 'Create Organization'}
          </h2>
          <button type="button" className="cedros-modal-close" onClick={onClose} aria-label="Close">
            <CloseIcon />
          </button>
        </div>

        <div className="cedros-modal-body">
          {error && <ErrorMessage error={error} />}

          {viewMode === 'list' ? (
            <OrgListView
              orgs={orgs}
              activeOrg={activeOrg}
              isLoading={isLoading}
              onSelect={handleSelect}
              onCreateClick={onCreate ? () => setViewMode('create') : undefined}
            />
          ) : (
            <CreateOrgForm
              isLoading={isLoading}
              onSubmit={handleCreate}
              onCancel={() => setViewMode('list')}
            />
          )}
        </div>
      </div>
    </div>
  );
}
