/**
 * WithdrawalHistory component
 *
 * Shows a paginated list of user-initiated withdrawals (SOL/SPL to external addresses).
 * Each row shows token, amount, destination, date, and links to Solana explorer.
 */

import { useState, useEffect, useCallback } from 'react';
import { useWithdrawal } from '../../hooks/useWithdrawal';
import { SUPPORTED_TOKENS } from '../deposit/tokens';
import type {
  UserWithdrawalHistoryItem,
  UserWithdrawalHistoryResponse,
} from '../../types/withdrawal';

export interface WithdrawalHistoryProps {
  /** Number of items per page (default: 10) */
  pageSize?: number;
  /** Additional CSS classes */
  className?: string;
  /** Callback when a transaction row is clicked */
  onTransactionClick?: (item: UserWithdrawalHistoryItem) => void;
  /** Solana explorer base URL (default: https://solscan.io) */
  explorerUrl?: string;
}

/** Resolve a mint address to a symbol, falling back to truncated address */
function resolveTokenSymbol(tokenType: string, tokenMint: string | null): string {
  if (tokenType === 'sol') return 'SOL';
  if (!tokenMint) return 'SPL';
  const known = SUPPORTED_TOKENS.find((t) => t.mint === tokenMint);
  if (known) return known.symbol;
  return `${tokenMint.slice(0, 4)}...${tokenMint.slice(-4)}`;
}

/** Truncate a Solana address for display */
function truncateAddress(addr: string): string {
  if (addr.length <= 12) return addr;
  return `${addr.slice(0, 4)}...${addr.slice(-4)}`;
}

/** Format date for display */
function formatDate(dateString: string): string {
  const date = new Date(dateString);
  const now = new Date();
  const diffMs = now.getTime() - date.getTime();
  const diffDays = Math.floor(diffMs / (1000 * 60 * 60 * 24));

  if (diffDays === 0) {
    const diffHours = Math.floor(diffMs / (1000 * 60 * 60));
    if (diffHours === 0) {
      const diffMins = Math.floor(diffMs / (1000 * 60));
      if (diffMins < 1) return 'Just now';
      return `${diffMins}m ago`;
    }
    return `${diffHours}h ago`;
  }
  if (diffDays === 1) return 'Yesterday';
  if (diffDays < 7) return `${diffDays}d ago`;

  return date.toLocaleDateString(undefined, {
    month: 'short',
    day: 'numeric',
    year: date.getFullYear() !== now.getFullYear() ? 'numeric' : undefined,
  });
}

export function WithdrawalHistory({
  pageSize = 10,
  className = '',
  onTransactionClick,
  explorerUrl = 'https://solscan.io',
}: WithdrawalHistoryProps) {
  const { getHistory, error, clearError } = useWithdrawal();

  const [items, setItems] = useState<UserWithdrawalHistoryItem[]>([]);
  const [total, setTotal] = useState(0);
  const [offset, setOffset] = useState(0);
  const [isLoading, setIsLoading] = useState(false);
  const [loadError, setLoadError] = useState<string | null>(null);

  const fetchHistory = useCallback(async () => {
    setIsLoading(true);
    try {
      const result: UserWithdrawalHistoryResponse = await getHistory(pageSize, offset);
      setItems(result.items);
      setTotal(result.total);
      setLoadError(null);
    } catch (err) {
      setLoadError(err instanceof Error ? err.message : 'Failed to load withdrawal history');
    } finally {
      setIsLoading(false);
    }
  }, [pageSize, offset, getHistory]);

  useEffect(() => {
    fetchHistory();
  }, [fetchHistory]);

  const totalPages = Math.ceil(total / pageSize);
  const currentPage = Math.floor(offset / pageSize) + 1;

  const goToPage = (page: number) => {
    const newOffset = (page - 1) * pageSize;
    setOffset(Math.max(0, Math.min(newOffset, Math.max(0, total - 1))));
  };

  // Error state
  if (loadError || error) {
    return (
      <div className={`cedros-withdrawal-history cedros-withdrawal-history-error ${className}`}>
        <p className="cedros-withdrawal-error">{loadError || error}</p>
        <button
          type="button"
          className="cedros-withdrawal-retry"
          onClick={() => {
            clearError();
            setLoadError(null);
            fetchHistory();
          }}
        >
          Retry
        </button>
      </div>
    );
  }

  // Loading state (initial)
  if (isLoading && items.length === 0) {
    return (
      <div className={`cedros-withdrawal-history cedros-withdrawal-history-loading ${className}`}>
        <span className="cedros-tx-loading-indicator" />
        <span className="cedros-tx-loading-text">Loading withdrawal history...</span>
      </div>
    );
  }

  return (
    <div className={`cedros-withdrawal-history ${className}`}>
      {/* Header */}
      <div className="cedros-tx-header">
        <h4 className="cedros-tx-title">Withdrawal History</h4>
        <button
          type="button"
          className="cedros-tx-refresh"
          onClick={fetchHistory}
          disabled={isLoading}
          title="Refresh"
        >
          {isLoading ? '...' : '\u21BB'}
        </button>
      </div>

      {/* List */}
      {items.length === 0 ? (
        <div className="cedros-tx-empty">
          <p className="cedros-tx-empty-message">No withdrawal history yet.</p>
        </div>
      ) : (
        <>
          <div className="cedros-tx-list">
            {items.map((item) => {
              const symbol = resolveTokenSymbol(item.tokenType, item.tokenMint);
              return (
                <div
                  key={item.id}
                  className="cedros-tx-item cedros-tx-item-negative"
                  onClick={() => onTransactionClick?.(item)}
                  onKeyDown={(e) => {
                    if (e.key === 'Enter' || e.key === ' ') {
                      e.preventDefault();
                      onTransactionClick?.(item);
                    }
                  }}
                  role={onTransactionClick ? 'button' : undefined}
                  tabIndex={onTransactionClick ? 0 : undefined}
                >
                  <div className="cedros-tx-icon cedros-tx-icon-negative">{'\u2191'}</div>
                  <div className="cedros-tx-content">
                    <div className="cedros-tx-row-main">
                      <span className="cedros-tx-type">{symbol} Withdrawal</span>
                      <span className="cedros-tx-amount cedros-tx-amount-negative">
                        {item.amount} {symbol === 'SOL' ? 'lamports' : ''}
                      </span>
                    </div>
                    <div className="cedros-tx-row-details">
                      <span className="cedros-tx-description">
                        To{' '}
                        <a
                          href={`${explorerUrl}/account/${item.destination}`}
                          target="_blank"
                          rel="noopener noreferrer"
                          onClick={(e) => e.stopPropagation()}
                        >
                          {truncateAddress(item.destination)}
                        </a>
                        {' \u00B7 '}
                        <a
                          href={`${explorerUrl}/tx/${item.txSignature}`}
                          target="_blank"
                          rel="noopener noreferrer"
                          onClick={(e) => e.stopPropagation()}
                        >
                          tx
                        </a>
                      </span>
                      <span className="cedros-tx-date">{formatDate(item.createdAt)}</span>
                    </div>
                  </div>
                </div>
              );
            })}
          </div>

          {/* Pagination */}
          {totalPages > 1 && (
            <div className="cedros-tx-pagination">
              <button
                type="button"
                className="cedros-tx-page-btn"
                onClick={() => goToPage(currentPage - 1)}
                disabled={currentPage <= 1}
              >
                Previous
              </button>
              <span className="cedros-tx-page-info">
                Page {currentPage} of {totalPages}
              </span>
              <button
                type="button"
                className="cedros-tx-page-btn"
                onClick={() => goToPage(currentPage + 1)}
                disabled={currentPage >= totalPages}
              >
                Next
              </button>
            </div>
          )}
        </>
      )}
    </div>
  );
}
