/**
 * Hook for pending wallet recovery management
 *
 * After wallet enrollment, if recovery mode is enabled, the server stores
 * recovery data temporarily. This hook fetches that data and allows the
 * user to acknowledge receipt (after which the data is deleted from server).
 */

import { useState, useCallback, useMemo, useEffect } from 'react';
import { useCedrosLoginOptional } from '../context/useCedrosLogin';
import { ApiClient, handleApiError } from '../utils/apiClient';
import type {
  PendingWalletRecoveryResponse,
  AcknowledgeRecoveryRequest,
  MessageResponse,
} from '../types/wallet';

export interface UsePendingRecoveryReturn {
  /** Whether there is pending recovery data */
  hasPendingRecovery: boolean;
  /** Recovery type: 'share_c' or 'full_seed' */
  recoveryType: string | null;
  /** Recovery phrase (base64-encoded seed) */
  recoveryPhrase: string | null;
  /** When the recovery data expires */
  expiresAt: Date | null;
  /** Fetch pending recovery data from server */
  fetchPendingRecovery: () => Promise<void>;
  /** Acknowledge that user has saved the recovery phrase (deletes from server) */
  acknowledgeRecovery: () => Promise<void>;
  /** Whether request is in progress */
  isLoading: boolean;
  /** Error from last request */
  error: string | null;
  /** Clear error */
  clearError: () => void;
}

/**
 * Hook for managing pending wallet recovery data
 *
 * Use this after wallet enrollment to show the user their recovery phrase
 * and allow them to acknowledge receipt.
 */
export function usePendingRecovery(): UsePendingRecoveryReturn {
  const context = useCedrosLoginOptional();
  const [isLoading, setIsLoading] = useState(false);
  const [error, setError] = useState<string | null>(null);
  const [pendingData, setPendingData] = useState<PendingWalletRecoveryResponse | null>(null);

  const apiClient = useMemo(() => {
    if (!context) return null;
    return new ApiClient({
      baseUrl: context.config.serverUrl,
      timeoutMs: context.config.requestTimeout,
      retryAttempts: context.config.retryAttempts,
      getAccessToken: context._internal?.getAccessToken,
    });
  }, [context]);

  const fetchPendingRecovery = useCallback(async (): Promise<void> => {
    if (!apiClient) {
      throw new Error('usePendingRecovery must be used within a CedrosLoginProvider');
    }
    setIsLoading(true);
    setError(null);

    try {
      const data = await apiClient.get<PendingWalletRecoveryResponse>('/wallet/pending-recovery');
      setPendingData(data);
    } catch (err) {
      const apiError = handleApiError(err, 'Failed to fetch pending recovery');
      setError(apiError.message);
      throw apiError;
    } finally {
      setIsLoading(false);
    }
  }, [apiClient]);

  const acknowledgeRecovery = useCallback(async (): Promise<void> => {
    if (!apiClient) {
      throw new Error('usePendingRecovery must be used within a CedrosLoginProvider');
    }
    setIsLoading(true);
    setError(null);

    try {
      const request: AcknowledgeRecoveryRequest = { confirmed: true };
      await apiClient.post<MessageResponse>('/wallet/acknowledge-recovery', request);
      // Clear pending data after successful acknowledgment
      setPendingData(null);
    } catch (err) {
      const apiError = handleApiError(err, 'Failed to acknowledge recovery');
      setError(apiError.message);
      throw apiError;
    } finally {
      setIsLoading(false);
    }
  }, [apiClient]);

  const clearError = useCallback(() => setError(null), []);

  // Auto-fetch on mount if authenticated
  useEffect(() => {
    if (apiClient && context?.authState === 'authenticated') {
      fetchPendingRecovery().catch(() => {
        // Silently ignore errors on auto-fetch
      });
    }
  }, [apiClient, context?.authState, fetchPendingRecovery]);

  return {
    hasPendingRecovery: pendingData?.hasPendingRecovery ?? false,
    recoveryType: pendingData?.recoveryType ?? null,
    recoveryPhrase: pendingData?.recoveryPhrase ?? null,
    expiresAt: pendingData?.expiresAt ? new Date(pendingData.expiresAt) : null,
    fetchPendingRecovery,
    acknowledgeRecovery,
    isLoading,
    error,
    clearError,
  };
}
