/**
 * Hook for wallet enrollment flow (server-side signing)
 *
 * Orchestrates the complete enrollment process:
 * 1. Generate 32-byte seed
 * 2. Split into 3 Shamir shares
 * 3. Encrypt Share A with password/PIN or passkey PRF
 * 4. Derive Solana public key from seed
 * 5. Upload encrypted Share A + plaintext Share B to server
 * 6. Display recovery phrase (Share C or full seed based on config)
 *
 * Recovery modes:
 * - share_c_only: User gets Share C. Recovery within app only (not portable).
 * - full_seed: User gets full seed. Can use wallet elsewhere (portable).
 *
 * Share B is sent as plaintext (SSS math protects it - 1 share reveals nothing).
 * Passkey PRF is used only for passkey auth method. Server combines shares for signing.
 *
 * H-03: Security Note - Recovery Phrase Visibility
 * The recovery phrase is stored in React state during the 'showing_recovery' step.
 * This is intentional - the user MUST see the phrase to back it up. The phrase:
 * - Is visible in React DevTools during enrollment
 * - Is cleared from state when the component unmounts or enrollment completes
 * - Byte arrays (seed, shares) are securely wiped via sensitiveData ref
 * - String mnemonics cannot be securely wiped (JS string immutability)
 */

import { useState, useCallback, useRef, useEffect } from 'react';
import { useCedrosLogin } from '../context/useCedrosLogin';
import {
  generateSeed,
  generateArgon2Salt,
  generatePrfSalt,
  splitSecret,
  argon2DeriveInWorker,
  aesGcmEncryptToBase64,
  uint8ArrayToBase64,
  authenticateWithDiscoverablePrf,
  registerPasskeyWithPrf,
  deriveKeyFromPrf,
  getPublicKeyFromSeed,
  publicKeyToBase58,
  seedToMnemonic,
  shareToMnemonic,
  wipeAll,
  toEncryptionKey,
  toShamirShare,
  DEFAULT_KDF_PARAMS,
} from '../crypto';
import { useWalletMaterial } from './useWalletMaterial';
import { useWalletDiscovery } from './useWalletDiscovery';
import type {
  UseWalletEnrollmentReturn,
  EnrollmentState,
  WalletEnrollRequest,
  ShareAAuthMethod,
} from '../types/wallet';

/**
 * Hook for wallet enrollment
 *
 * Supports auth methods:
 * - password: User sets a wallet password (Argon2id KDF)
 * - passkey: Uses passkey PRF extension
 */
export function useWalletEnrollment(): UseWalletEnrollmentReturn {
  const { user } = useCedrosLogin();
  const { enroll } = useWalletMaterial();
  const { recoveryMode } = useWalletDiscovery();

  const [state, setState] = useState<EnrollmentState>({ step: 'idle' });
  const [isEnrolling, setIsEnrolling] = useState(false);

  // Store sensitive data for cleanup
  const sensitiveData = useRef<Uint8Array[]>([]);

  const cleanup = useCallback(() => {
    wipeAll(...sensitiveData.current);
    sensitiveData.current = [];
  }, []);

  useEffect(() => {
    return () => {
      cleanup();
    };
  }, [cleanup]);

  /**
   * Core enrollment logic shared by all auth methods
   */
  const doEnrollment = useCallback(
    async (
      authMethod: ShareAAuthMethod,
      encryptionKey: Uint8Array,
      kdfSalt?: Uint8Array,
      prfSalt?: string
    ): Promise<void> => {
      // Step 1: Generate seed
      setState({ step: 'generating_seed' });
      const seed = generateSeed();
      sensitiveData.current.push(seed);

      // Step 2: Split into shares
      setState({ step: 'splitting_shares' });
      const { shareA, shareB, shareC } = splitSecret(seed);
      sensitiveData.current.push(shareA, shareB, shareC);

      // Step 3: Encrypt Share A
      setState({ step: 'encrypting_shares' });
      const encryptedA = await aesGcmEncryptToBase64(shareA, toEncryptionKey(encryptionKey));

      // Step 4: Derive Solana public key
      const publicKey = getPublicKeyFromSeed(seed);
      const solanaPubkey = publicKeyToBase58(publicKey);

      // Step 5: Upload to server
      setState({ step: 'uploading' });

      const request: WalletEnrollRequest = {
        solanaPubkey,
        shareAAuthMethod: authMethod,
        shareACiphertext: encryptedA.ciphertext,
        shareANonce: encryptedA.nonce,
        // Share B is now plaintext (SSS math protects it)
        shareB: uint8ArrayToBase64(shareB),
      };

      // Add auth-method-specific fields
      if (authMethod === 'password') {
        if (!kdfSalt) throw new Error('KDF salt required for password method');
        request.shareAKdfSalt = uint8ArrayToBase64(kdfSalt);
        request.shareAKdfParams = DEFAULT_KDF_PARAMS;
      }

      if (authMethod === 'passkey' && prfSalt) {
        request.prfSalt = prfSalt;
      }

      await enroll(request);

      // Step 6: Show recovery phrase based on recovery mode
      // - share_c_only: Show Share C (app-locked, not portable)
      // - full_seed: Show full seed (portable to other wallets)
      // - none: Skip recovery phrase entirely (Privacy Cash mode)
      if (recoveryMode === 'none') {
        // No recovery option - go straight to complete
        cleanup();
        setState({
          step: 'complete',
          solanaPubkey,
        });
      } else {
        const recoveryPhrase =
          recoveryMode === 'full_seed'
            ? seedToMnemonic(seed)
            : shareToMnemonic(toShamirShare(shareC));

        setState({
          step: 'showing_recovery',
          recoveryPhrase,
          solanaPubkey,
        });
      }
    },
    [enroll, recoveryMode, cleanup]
  );

  /**
   * Start enrollment with password (email users)
   * Uses user's login password for Share A encryption
   */
  const startEnrollmentWithPassword = useCallback(
    async (password: string): Promise<void> => {
      if (!user) {
        setState({ step: 'error', error: 'User not authenticated' });
        return;
      }

      setIsEnrolling(true);
      cleanup();

      try {
        // Derive key from password using Argon2id
        const salt = generateArgon2Salt();
        const key = await argon2DeriveInWorker(password, salt, DEFAULT_KDF_PARAMS);
        sensitiveData.current.push(key);

        await doEnrollment('password', key, salt);
      } catch (err) {
        setState({
          step: 'error',
          error: err instanceof Error ? err.message : 'Enrollment failed',
        });
      } finally {
        setIsEnrolling(false);
      }
    },
    [user, cleanup, doEnrollment]
  );

  /**
   * Start enrollment with passkey PRF
   * Uses WebAuthn PRF extension for Share A encryption
   */
  const startEnrollmentWithPasskey = useCallback(async (): Promise<void> => {
    if (!user) {
      setState({ step: 'error', error: 'User not authenticated' });
      return;
    }

    setIsEnrolling(true);
    cleanup();

    try {
      // Generate PRF salt
      const prfSalt = generatePrfSalt();
      const prfSaltBase64 = uint8ArrayToBase64(prfSalt);

      // Register passkey with PRF; fallback to authenticate if credential exists
      setState({ step: 'registering_passkey' });
      let prfOutput: Uint8Array;
      try {
        const userIdBytes = new TextEncoder().encode(user.id);
        const displayName = user.name ?? user.email ?? 'User';
        const userName = user.email ?? user.id;
        const registration = await registerPasskeyWithPrf(
          userIdBytes,
          userName,
          displayName,
          prfSalt
        );
        prfOutput = registration.prfOutput;
      } catch (err) {
        const error = err as DOMException;
        if (error?.name !== 'InvalidStateError') {
          throw err;
        }
        const prfResult = await authenticateWithDiscoverablePrf(prfSaltBase64);
        prfOutput = prfResult.prfOutput;
      }
      sensitiveData.current.push(prfOutput);

      // Derive key from PRF output
      const key = await deriveKeyFromPrf(prfOutput, prfSalt);
      sensitiveData.current.push(key);

      await doEnrollment('passkey', key, undefined, prfSaltBase64);
    } catch (err) {
      setState({
        step: 'error',
        error: err instanceof Error ? err.message : 'Enrollment failed',
      });
    } finally {
      setIsEnrolling(false);
    }
  }, [user, cleanup, doEnrollment]);

  const confirmRecoveryPhrase = useCallback(() => {
    // User confirmed they saved the recovery phrase
    const solanaPubkey = state.solanaPubkey;
    cleanup();

    setState({
      step: 'complete',
      solanaPubkey,
    });
  }, [state.solanaPubkey, cleanup]);

  const cancel = useCallback(() => {
    cleanup();
    setState({ step: 'idle' });
    setIsEnrolling(false);
  }, [cleanup]);

  return {
    state,
    startEnrollmentWithPassword,
    startEnrollmentWithPasskey,
    confirmRecoveryPhrase,
    cancel,
    isEnrolling,
  };
}
